<template>
  <div v-if="isVisible" class="seccion-periodos">

    <div class="filters-grid">
      <h2 class="modal-title">Periodos de Nómina</h2>


    </div>
    <div class="center">

      <!-- Botón para agregar nuevo -->
      <button class="btn-primary mb-4" @click="abrirModalNuevo">
        + Agregar nuevo periodo
      </button>
      <button @click="limpiarFiltros" class="btn-secondary mb-4 ">Limpiar filtros</button>
    </div>
    <!-- Filtros -->
    <div class="filters-grid">
      <select v-model="filtros.estatus" class="input">
        <option value="">Todos los estatus</option>
        <option value="pendiente">Pendiente</option>
        <option value="cerrado">Cerrado</option>
        <option value="cancelado">Cancelado</option>
      </select>

      <select v-model="filtros.tipo_nomina" class="input">
        <option value="">Todos los tipos</option>
        <option value="ordinaria">Ordinaria</option>
        <option value="extraordinaria">Extraordinaria</option>
      </select>

      <input type="date" v-model="filtros.fecha_inicio" class="input" placeholder="Desde" />
      <input type="date" v-model="filtros.fecha_fin" class="input" placeholder="Hasta" />
      <div v-if="cargando">Cargando periodos...</div>
    </div>

    <!-- Tabla de resultados -->
    <div class="table-wrapper">
      <table>
        <thead>
          <tr>
            <th>ID</th>
            <th>Fecha Inicio</th>
            <th>Fecha Fin</th>
            <th>Pago</th>
            <th>Tipo</th>
            <th>Estatus</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="periodo in periodos" :key="periodo.id"
            :class="['table-row', { 'editable-row': periodo.estatus === 'pendiente' }]"
            @click="periodo.estatus === 'pendiente' && abrirModalEditar(periodo)">
            <td>{{ periodo.id }}</td>
            <td>{{ formatearFecha(periodo.fecha_inicio) }}</td>
            <td>{{ formatearFecha(periodo.fecha_fin) }}</td>
            <td>{{ formatearFecha(periodo.fecha_pago) }}</td>
            <td class="capitalize">{{ periodo.tipo_nomina }}</td>
            <td class="capitalize">{{ periodo.estatus }}</td>
          </tr>
          <tr v-if="periodos.length === 0">
            <td colspan="6" class="no-results">No se encontraron resultados.</td>
          </tr>
        </tbody>
      </table>
    </div>

    <!-- Modal para agregar/editar -->
    <div v-if="modalVisible" class="modal-backdrop" @click.self="cerrarModal">
      <div class="modal-content">
        <h3 class="modal-subtitle">{{ modoEdicion ? 'Editar periodo' : 'Agregar nuevo periodo' }}</h3>

        <form @submit.prevent="guardarPeriodo">
          <label>
            Fecha Inicio:
            <input type="date" v-model="form.fecha_inicio" required />
          </label>

          <label>
            Fecha Fin:
            <input type="date" v-model="form.fecha_fin" required />
          </label>

          <label>
            Fecha Pago:
            <input type="date" v-model="form.fecha_pago" required />
          </label>

          <label>
            Tipo Nómina:
            <select v-model="form.tipo_nomina" required>
              <option value="ordinaria">Ordinaria</option>
              <option value="extraordinaria">Extraordinaria</option>
            </select>
          </label>

          <label>
            Estatus:
            <select v-model="form.estatus" required>
              <option value="pendiente">Pendiente</option>
              <option value="cerrado">Cerrado</option>
              <option value="cancelado">Cancelado</option>
            </select>
          </label>

          <div class="modal-actions">
            <button type="button" class="btn-secondary" @click="cerrarModal">Cancelar</button>
            <button type="submit" class="btn-primary">{{ modoEdicion ? 'Guardar cambios' : 'Agregar' }}</button>
          </div>
        </form>
      </div>
    </div>
  </div>

</template>


<script>
import axios from 'axios';
import Swal from 'sweetalert2';

export default {
  name: 'ListaPeriodosNomina',
  props: {
    idPortal: {
      type: String,
      default: ''
    },
    isVisible: {
      type: Boolean,
      default: false
    },
    idUsuario: {
      type: String,
      default: ''
    },
    idCliente: {
      type: String,
      default: ''
    },
    idRol: {
      type: String,
      default: ''
    },
  },
  data() {
    return {
      periodos: [],
      filtros: {
        estatus: '',
        tipo_nomina: '',
        fecha_inicio: '',
        fecha_fin: ''
      },
      cargando: false,
      modalVisible: false,
      modoEdicion: false, // false = agregar, true = editar
      form: {
        id: null,
        fecha_inicio: '',
        fecha_fin: '',
        fecha_pago: '',
        tipo_nomina: 'ordinaria',
        estatus: 'pendiente'
      }
    };
  },
  watch: {
    filtros: {
      deep: true,
      handler() {
        this.obtenerPeriodos();
      }
    }
  },
  mounted() {
    this.obtenerPeriodos();
  },
  methods: {
    cerrar() {
      this.$emit('update:isVisible', false); // ✅ Esta es la forma correcta
    },
    formatearFecha(fechaISO) {
      if (!fechaISO) return '';
      const fecha = new Date(fechaISO);

      // Ajustar para evitar desfase de zona horaria
      const fechaLocal = new Date(fecha.getTime() + Math.abs(fecha.getTimezoneOffset() * 60000));

      const opciones = { day: '2-digit', month: 'long', year: 'numeric' };
      const formateada = fechaLocal.toLocaleDateString('es-MX', opciones);

      return formateada.charAt(0).toUpperCase() + formateada.slice(1);
    },

    limpiarFiltros() {
      this.filtros = {
        estatus: '',
        tipo_nomina: '',
        fecha_inicio: '',
        fecha_fin: ''
      };
    },
    async obtenerPeriodos() {
      try {
        this.cargando = true; // activar indicador de carga

        const apiUrl = `${this.$apiUrl}/periodos-nomina`;
        // Crear objeto con filtros y id_cliente
        const params = {
          ...this.filtros,
          id_cliente: this.idCliente,
          id_portal: this.idPortal,
          id_usuario: this.idUsuario, // agregar este campo
        };

        // Filtrar params para eliminar claves con valores vacíos
        const filteredParams = Object.fromEntries(
          Object.entries(params).filter(([, value]) => value !== '' && value !== null && value !== undefined)
        );
        console.log('Llamando a:', apiUrl);
        console.log('Con parámetros filtrados:', filteredParams);

        const response = await axios.get(apiUrl, {
          headers: {
            'X-CSRF-TOKEN': this.$csrfToken
          },
          params: filteredParams
        });

        this.periodos = response.data;
      } catch (error) {
        console.error('Error al obtener periodos de nómina:', error);
        this.periodos = [];
      } finally {
        this.cargando = false; // desactivar indicador de carga
      }
    },


    abrirModalNuevo() {
      this.modoEdicion = false;
      this.form = {
        id: null,
        fecha_inicio: '',
        fecha_fin: '',
        fecha_pago: '',
        tipo_nomina: 'ordinaria',
        estatus: 'pendiente'
      };
      this.modalVisible = true;
    },

    abrirModalEditar(periodo) {
      this.modoEdicion = true;
      this.modalVisible = true;

      const formatDate = (fecha) => fecha ? fecha.slice(0, 10) : '';

      this.form.id = periodo.id;
      this.form.fecha_inicio = formatDate(periodo.fecha_inicio);
      this.form.fecha_fin = formatDate(periodo.fecha_fin);
      this.form.fecha_pago = formatDate(periodo.fecha_pago);
      this.form.tipo_nomina = periodo.tipo_nomina;
      this.form.estatus = periodo.estatus;
    },
    cerrarModal() {
      this.modalVisible = false;
    },

    async guardarPeriodo() {
      // Validación de fechas
      if (new Date(this.form.fecha_fin) < new Date(this.form.fecha_inicio)) {
        await Swal.fire({
          icon: 'warning',
          title: 'Fechas inválidas',
          text: 'La fecha fin no puede ser menor a la fecha inicio.',
          confirmButtonText: 'Entendido'
        });
        return;
      }

      // Añadir id_portal e id_cliente al form antes de enviar
      this.form.id_portal = this.idPortal;
      this.form.id_cliente = this.idCliente;
      this.form.id_usuario = this.idUsuario; // agregar este campo

      // Confirmación antes de guardar
      const confirmacion = await Swal.fire({
        icon: 'question',
        title: this.modoEdicion ? '¿Guardar cambios del periodo?' : '¿Agregar nuevo periodo?',
        text: this.modoEdicion
          ? 'Estás a punto de actualizar un periodo existente.'
          : 'Se creará un nuevo periodo de nómina.',
        showCancelButton: true,
        confirmButtonText: 'Sí, guardar',
        cancelButtonText: 'Cancelar'
      });

      if (!confirmacion.isConfirmed) {
        return; // Usuario canceló
      }

      try {
        if (this.modoEdicion) {
          // Actualizar periodo
          console.log('Datos enviados al endpoint:', this.form);
          console.log('Datos enviados al endpoint:', this.form.id);

          await axios.put(`${this.$apiUrl}/periodos-nomina/${this.form.id}`, this.form, {
            headers: { 'X-CSRF-TOKEN': this.$csrfToken }
          });
        } else {
          console.log('Datos enviados al endpoint:', this.form);

          await axios.post(`${this.$apiUrl}/periodos-nomina`, this.form, {
            headers: { 'X-CSRF-TOKEN': this.$csrfToken }
          });
        }

        this.cerrarModal();
        this.obtenerPeriodos();

        // Éxito
        Swal.fire({
          icon: 'success',
          title: '¡Guardado!',
          text: this.modoEdicion
            ? 'El periodo se actualizó correctamente.'
            : 'El periodo fue agregado exitosamente.'
        });
      } catch (error) {
        if (error.response?.status === 422) {
          Swal.fire({
            icon: 'error',
            title: 'Periodo duplicado',
            text: error.response.data.message || 'Este periodo ya existe.',
          });
        } else {
          console.error('Error guardando periodo:', error);
          Swal.fire({
            icon: 'error',
            title: 'Error',
            text: 'Ocurrió un error al guardar el periodo. Intente nuevamente.'
          });
        }
      }
    }

  }
};
</script>


