            <input type="text" v-model="deduccion.nombre" placeholder="Nombre de la prestación" class="input-nombre" />
            <input type="number" v-model="deduccion.valor" placeholder="Valor" class="input-valor" />
            <button @click="eliminarDeduccion(index)" class="btn-delete">
              <i class="fas fa-trash"></i> <!-- Icono de Basura -->
            </button>
          </div>
          <!-- Botón para Agregar -->
          <button @click="agregarDeduccion" class="btn btn-primary">Agregar Deducción</button>
          <div class="input-group">

            <p style="color: red !important;">
              <strong>Total deducciones:</strong> - ${{ totalDeducciones }}
