            </p>
            <p style="color: red !important;">
              <strong>Total :</strong> - ${{ totalDeduccionesT }}
            </p>
          </div>
        </section>
        <!-- Total de la Pre-Nómina -->
        <div class="prenomina-wrapper">
          <section class="section resumen-prenomina">
            <h4 class="section-title">Total de la Prenómina</h4>

