                  <span class="value ingresos">${{ totalIngresosA }}</span>
                </div>
                <div class="total-item total-final">
                  <span class="label">Total Ingresos:</span>
                  <span class="value ingresos">${{ totalIngresos + totalIngresosA }}</span>
