            <div class="total-bloque total-pago">
              <h5 class="bloque-titulo">Resultado Final</h5>
              <div class="totales-fila">
                <div class="total-item">
                  <span class="label">Total Ordinario:</span>
                  <span class="value neto">${{ totalIngresos - totalDeducciones }}</span>
                </div>
                <div class="total-item">
                  <span class="label">Total Asimilados:</span>
                  <span class="value neto">${{ totalIngresosA - totalDeduccionesA }}</span>
                </div>
                <div class="total-item total-final">
                  <span class="label">Total a Pagar:</span>
                  <span class="value neto">${{ (totalIngresos - totalDeducciones) + (totalIngresosA - totalDeduccionesA)
                    }}</span>
