<script>
import moment from 'moment-timezone';
import Swal from 'sweetalert2';
//import PrenominaMasiva from './PrenominaMasiva.vue'


export default {
  components: {
    //PrenominaMasiva,
  },
  props: {
    empleado: {
      type: Object,
      default: () => ({ laborales: {}, pre_nomina_empleados: {} })  // Default empty object if no data is passed
    },
    idRol: {
      type: String,
      default: ''
    },
    idPortal: {
      type: String,
      default: ''
    },
    idUsuario: {
      type: String,
      default: ''
    },
    idCliente: {
      type: String,
      default: ''
    },
  },
  data() {
    return {
      tooltipPositionClass: '',
      laborales: {},
      sueldoBase: 0,
      sueldoAsimilado: 0,
      horasExtras: 0,
      pago_hora_extra: 0,
      pagoDiasFestivos: 0,
      diasAusencias: 0,
      periodicidad_pago: 0,
      aguinaldo: 0,
      aguinaldoA: 0,
      vacaciones: 0,
      primaVacacional: 0,
      prestamos: 0,
      prestamo_pendiente: 0,
      total_prestamo: 0,
      prestacionesExtras: [],
      deduccionesExtras: [],
      descuentosAdicionales: 0,
      totalHorasExtras: 0,
      totalHorasExtrasA: 0,
      totalDiasFestivos: 0,
      totalDiasFestivosA: 0,
      totalDiasAusente: 0,
      totalDiasAusenteA: 0,
      totalPrimaVacacional: 0,
      totalPrimaVacacionalA: 0,

      totalDeducciones: 0,
      totalDeduccionesA: 0,
      totalDeduccionesT: 0,
      totalPrestaciones: 0,
      totalPrestacionesA: 0,
      totalPrestacionesT: 0,


      salarioNeto: 0,
      totalPagar: 0,
      totalPagarA: 0,
      totalPagarT: 0,
      totalIngresos: 0,
      totalIngresosA: 0,
      isDisabled: false,
      aguinaldoActivo: false,
      aguinaldoActivoA: false,
      periodosPendientes: [],
      periodoSeleccionado: null,
      excelModalRef: null, // Flag to disable the inputs if data is already provided
    };
  },
  methods: {
    recalcularPosicionTooltip() {
      this.$nextTick(() => {
        const wrapper = this.$refs.tooltipWrapper;
        const tooltip = wrapper.querySelector('.horas-extra-tooltip-content');

        if (!tooltip) return;

        const wrapperRect = wrapper.getBoundingClientRect();
        const tooltipRect = tooltip.getBoundingClientRect();
        const espacioDerecha = window.innerWidth - wrapperRect.right;
        const espacioIzquierda = wrapperRect.left;
        const espacioArriba = wrapperRect.top;
        const espacioAbajo = window.innerHeight - wrapperRect.bottom;

        // Posición horizontal
        if (espacioDerecha < tooltipRect.width && espacioIzquierda > tooltipRect.width) {
          this.tooltipPositionClass = 'left';
        } else {
          this.tooltipPositionClass = '';
        }

        // Posición vertical (opcional)
        if (espacioAbajo < tooltipRect.height && espacioArriba > tooltipRect.height) {
          this.tooltipPositionClass += ' top';
        }
      });
    },
    abrirModalMasivo() {
      if (this.$refs.excelModalRef) {
        this.$refs.excelModalRef.openModal();
      } else {
        console.warn('excelModalRef no está disponible');
      }
    },
    async obtenerPeriodosPendientes() {
      try {
        const response = await fetch(
          `${this.$apiUrl}/periodos-nomina?id_cliente=${this.idCliente}&id_portal=${this.idPortal}`,
          {
            method: 'GET',
            headers: {
              'Content-Type': 'application/json'
            }
          }
        );
        const result = await response.json();
        if (response.ok) {
          //console.log('Periodos recibidos:', result);
          this.periodosPendientes = result || [];

          // 👉 Asigna el ID del periodo más reciente al v-model
          if (this.periodosPendientes.length > 0) {
            this.periodoSeleccionado = this.periodosPendientes[0].id;
          } else {
            this.periodoSeleccionado = ''; // limpia si no hay resultados
          }
        } else {
          console.error('Error al obtener periodos:', result);
          Swal.fire('Error', 'No se pudieron obtener los periodos pendientes.', 'error');
        }
      } catch (error) {
        console.error('Error en la solicitud:', error);
        Swal.fire('Error', 'Hubo un problema al conectar con el servidor.', 'error');
      }
    },


    formatPeriodo(periodo) {
      const inicio = new Date(periodo.fecha_inicio);
      const fin = new Date(periodo.fecha_fin);

      const diaInicio = inicio.getDate().toString().padStart(2, '0');
      const diaFin = fin.getDate().toString().padStart(2, '0');

      const mes = fin.toLocaleString('es-ES', { month: 'long' }).toLowerCase();
      const anio = fin.getFullYear();

      let texto = `${diaInicio} al ${diaFin} de ${mes} de ${anio}`;

      if (periodo.descripcion) {
        const descripcion = periodo.descripcion.trim().replace(/\s+/g, ' ');
        texto += ` - ${descripcion}`;
      }

      return texto;
    },

    agregarPrestacion() {
      if (!Array.isArray(this.prestacionesExtras)) {
        this.prestacionesExtras = [];
      }
      this.prestacionesExtras.push({ nombre: "", valor: 0 });
    },
    eliminarPrestacion(index) {
      this.prestacionesExtras.splice(index, 1);
    },
    agregarDeduccion() {
      if (!Array.isArray(this.deduccionesExtras)) {
        this.deduccionesExtras = [];
      }
      this.$set(this.deduccionesExtras, this.deduccionesExtras.length, { nombre: "", valor: 0 });
      // Actualiza después de agregar
    },
    eliminarDeduccion(index) {
      this.deduccionesExtras.splice(index, 1);
      this.actualizarTotalDeducciones();  // Actualiza después de eliminar
    },

    actualizarTotalDeducciones() {
      const lista = Array.isArray(this.deduccionesExtras) ? this.deduccionesExtras : [];

      this.totalDeducciones = 0;
      this.totalDeduccionesA = 0;
      this.totalDeducciones = lista.reduce((acc, deduccion) => {
        return acc + (parseFloat(deduccion.valor) || 0);
      }, 0);

      this.totalDeducciones += (parseFloat(this.prestamo_pendiente) || 0);
      this.totalDeducciones += (parseFloat(this.prestamos) || 0);
      this.totalDeducciones += (parseFloat(this.totalDiasAusente) || 0);


      this.totalDeduccionesA += (parseFloat(this.totalDiasAusenteA) || 0);


      this.calculateSalarioNeto();
      this.calculateTotalPagar();
    },

    actualizarTotalPrestaciones() {
      const lista = Array.isArray(this.prestacionesExtras) ? this.prestacionesExtras : [];

      this.totalPrestaciones = 0;
      this.totalPrestacionesA = 0;
      this.totalPrestaciones = lista.reduce((acc, prestacion) => {
        return acc + (parseFloat(prestacion.valor) || 0);
      }, 0);
      this.totalPrestaciones += (parseFloat(this.totalHorasExtras) || 0);
      this.totalPrestaciones += (parseFloat(this.totalDiasFestivos) || 0);
      this.totalPrestaciones += (parseFloat(this.aguinaldo) || 0);
      this.totalPrestaciones += (parseFloat(this.totalPrimaVacacional) || 0);


      this.totalPrestacionesA += (parseFloat(this.totalHorasExtrasA) || 0);
      this.totalPrestacionesA += (parseFloat(this.totalDiasFestivosA) || 0);
      this.totalPrestacionesA += (parseFloat(this.aguinaldoA) || 0);
      this.totalPrestacionesA += (parseFloat(this.totalPrimaVacacionalA) || 0);


      this.calculateSalarioNeto();
      this.calculateTotalPagar();
    },

    // Métodos para registrar o actualizar datos
    async registrarDatos() {
      const timestamp = moment.tz('America/Mexico_City').format('YYYY-MM-DD HH:mm:ss');
      const dataToSave = {
        creacion: timestamp,
        edicion: timestamp,
        idPeriodo: this.periodoSeleccionado,
        idEmpleado: this.empleado.id,  // Asegúrate de que este campo se pase correctamente
        sueldoBase: parseFloat(this.sueldoBase),
        sueldoAsimilado: parseFloat(this.sueldoAsimilado),

        horasExtras: parseFloat(this.horasExtras),
        pagoHorasExtras: parseFloat(this.totalHorasExtras),
        pagoHorasExtrasA: parseFloat(this.totalHorasExtrasA),
        // Convierte a número decimal
        // Convierte a número decimal
        diasFestivos: this.pagoDiasFestivos,
        pagoDiasFestivos: this.totalDiasFestivos,
        pagoDiasFestivosA: this.totalDiasFestivosA, // Convierte a número decimal
        diasAusencias: this.diasAusencias,
        descuentoAusencia: parseFloat(this.totalDiasAusente),
        descuentoAusenciaA: parseFloat(this.totalDiasAusenteA),

        aguinaldo: parseFloat(this.aguinaldo),
        aguinaldoA: parseFloat(this.aguinaldoA), // Convierte a número decimal
        vacaciones: this.vacaciones,
        pagoVacaciones: parseFloat(this.totalPrimaVacacional),
        pagoVacacionesA: parseFloat(this.totalPrimaVacacionalA),  // Convierte a número decimal
        // Convierte a número decimal
        primaVacacional: parseFloat(this.primaVacacional),  // Convierte a número decimal
        prestamos: parseFloat(this.prestamos),
        prestamo_pendiente: parseFloat(this.prestamo_pendiente), // Convierte a número decimal
        deduccionesExtras: JSON.stringify(this.deduccionesExtras),  // Convierte el array a JSON
        prestacionesExtras: JSON.stringify(this.prestacionesExtras),  // Convierte el array a JSON
        totalPagar: parseFloat(this.totalIngresos - this.totalDeducciones),
        totalPagarA: parseFloat(this.totalIngresosA - this.totalDeduccionesA),
        totalPagarT: parseFloat((this.totalIngresos - this.totalDeducciones) + (this.totalIngresosA - this.totalDeduccionesA)) // Convierte a número decimal
        // Convierte a número decimal
       
      };
       console.log('Ingresos:', this.totalIngresos);
        console.log('Deducciones:', this.totalDeducciones);
        console.log('Resultado:', this.totalIngresos - this.totalDeducciones);
      try {
        const response = await fetch(`${this.$apiUrl}/empleados/registro_prenomina`, {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
          },
          body: JSON.stringify(dataToSave),
