          confirmButtonText: 'Aceptar'
        });
      }
    },
    calcularSueldo(laborales) {
      const periodicidad = laborales.periodicidad_pago;
      const sueldoDiario = parseFloat(laborales.sueldo_diario) || 0;
      const sueldoAsimilado = parseFloat(laborales.sueldo_asimilado) || 0;

      let factor = 1;

      switch (periodicidad) {
        case '01': // Diario
        case '07': // Hora
        case '08': // Jornada
        case '09': // Otro
          factor = 1;
          break;
        case '02': // Semanal
          factor = 7;
          break;
        case '03': // Quincenal
          factor = 15;
          break;
        case '04': // Mensual
          factor = 30;
          break;
        case '05': // Bimestral
          factor = 60;
          break;
        default:
          console.warn('Periodicidad no válida:', periodicidad);
      }

      const sueldoCalculado = {
        ordinario: sueldoDiario * factor,
        asimilado: sueldoAsimilado * factor,
        total: (sueldoDiario + sueldoAsimilado) * factor
      };

      return sueldoCalculado;
    },

    updateAguinaldo() {
      if (this.aguinaldoActivo) {
        // Si el switch está activado, calculamos el aguinaldo
        this.aguinaldo = this.calculateAguinaldo(this.empleado.laborales);
      } else {
        // Si el switch está apagado, el valor se pone en 0
        this.aguinaldo = 0;
      }
    },
    updateAguinaldoA() {
      if (this.aguinaldoActivoA) {
        // Si el switch está activado, calculamos el aguinaldo
        this.aguinaldoA = this.calculateAguinaldoA(this.empleado.laborales);
      } else {
        // Si el switch está apagado, el valor se pone en 0
        this.aguinaldoA = 0;
      }
    },
    calculateAguinaldo(laborales) {
      const sueldoDiario = parseFloat(laborales.sueldo_diario);

      return sueldoDiario * laborales.dias_aguinaldo ?? 0;
    },
    calculateAguinaldoA(laborales) {
      const sueldoAsimilado = parseFloat(laborales.sueldo_asimilado);

      return sueldoAsimilado * laborales.dias_aguinaldo ?? 0;
    },
    calculateSalarioNeto() {
      // Usamos los valores calculados o ingresados, asegurándonos de convertirlos a número
      const base = parseFloat(this.sueldoBase) || 0;
      const baseAsimilado = parseFloat(this.sueldoAsimilado) || 0;
      const totalHorasExtras = parseFloat(this.totalHorasExtras) || 0;
      const totalDiasFestivos = parseFloat(this.totalDiasFestivos) || 0;
      const totalDiasAusente = parseFloat(this.totalDiasAusente) || 0;
      // const totalPrestaciones = parseFloat(this.totalPrestaciones) || 0;

      // Salario Neto = Sueldo Base + Extras + Comisiones + Bonificaciones + Días Festivos - Días Ausentes - IMSS - Infonavit
      this.salarioNeto = base + baseAsimilado + totalHorasExtras + totalDiasFestivos - totalDiasAusente;
      this.calculateTotalPagar();
    },
    calculateTotalIngresos() {

      this.totalIngresos = 0;
      this.totalIngresosA = 0;

      // Usamos los valores calculados o ingresados, asegurándonos de convertirlos a número
      const base = parseFloat(this.sueldoBase) || 0;
      const totalHorasExtras = parseFloat(this.totalHorasExtras) || 0;
      const totalDiasFestivos = parseFloat(this.totalDiasFestivos) || 0;
      const totalPrestaciones = parseFloat(this.totalPrestaciones) || 0;
      this.totalIngresos = base + totalHorasExtras + totalDiasFestivos + totalPrestaciones;

      const baseA = parseFloat(this.sueldoAsimilado) || 0;
      const totalHorasExtrasA = parseFloat(this.totalHorasExtrasA) || 0;
      const totalDiasFestivosA = parseFloat(this.totalDiasFestivosA) || 0;
      const totalPrestacionesA = parseFloat(this.totalPrestacionesA) || 0;
      this.totalIngresosA = baseA + totalHorasExtrasA + totalDiasFestivosA + totalPrestacionesA;

    },
    calculateTotalPagar() {
      // Convertimos a número cada valor pagoDiasFestivos

      const totalHorasExtras = parseFloat(this.totalHorasExtras) || 0;
      const totalDiasFestivos = parseFloat(this.totalDiasFestivos) || 0;
      const vacaciones = parseFloat(this.vacaciones) || 0;
      const totalPrestaciones = parseFloat(this.totalPrestaciones) || 0;
      const prestamos = parseFloat(this.prestamos) || 0;
      //const prestamo_pendiente = parseFloat(this.prestamo_pendiente) || 0;
      const totalDeducciones = parseFloat(this.totalDeducciones) || 0;
      // Total a Pagar = Salario Neto + Aguinaldo + Vacaciones + Prima Vacacional + Vales de Despensa + Fondo de Ahorro - Préstamos - Descuentos Adicionales
      this.totalPagar = this.salarioNeto + vacaciones + totalHorasExtras + totalDiasFestivos + totalPrestaciones - prestamos - totalDeducciones;
    },
    getNombrePeriodicidad(codigo) {
      console.log("🚀 ~ getNombrePeriodicidad ~ codigo:", codigo)
      const map = {
        '01': 'Diario',
        '02': 'Semanal',
        '03': 'Quincenal',
        '04': 'Mensual',
        '05': 'Bimestral',
        '06': 'Unidad obra',
        '07': 'Comisión',
        '08': 'Precio alzado',
        '09': 'Otra Periodicidad'
      };

      return map[codigo] || 'Desconocida';
    },
  },
  watch: {
    deduccionesExtras: {
      deep: true, // Detecta cambios dentro del array
      handler() {
        this.actualizarTotalDeducciones();
      }
    },
    prestacionesExtras: {
      deep: true, // Detecta cambios dentro del array
      handler() {
        this.actualizarTotalPrestaciones();
      }
    },
    totalPrestacionesA() {
      this.totalPrestacionesT = this.totalPrestaciones + this.totalPrestacionesA;
      this.actualizarTotalPrestaciones();
      this.calculateTotalIngresos();

    },
    totalPrestaciones() {
      this.totalPrestacionesT = this.totalPrestaciones + this.totalPrestacionesA;
      this.actualizarTotalPrestaciones();
      this.calculateTotalIngresos();


    },
    totalDeducciones() {
      this.totalPrestacionesT = this.totalPrestaciones + this.totalPrestacionesA;
      this.actualizarTotalPrestaciones();

    },
    totalDeduccionesA() {
      this.totalDeduccionesT = this.totalDeducciones + this.totalDeduccionesA;
      this.actualizarTotalPrestaciones();

    },
    totalHorasExtras() {
      this.actualizarTotalPrestaciones();
    },
    totalDiasFestivos() {
      this.actualizarTotalPrestaciones();
    },
    aguinaldo() {

      this.actualizarTotalPrestaciones();
    },
    aguinaldoA() {

      this.actualizarTotalPrestaciones();
    },

    totalDiasAusente() {
      this.actualizarTotalDeducciones();

    },
    totalDiasAusenteA() {
      this.actualizarTotalDeducciones();

    },
    prestamos() {
      this.actualizarTotalDeducciones();
      this.total_prestamo = (parseFloat(this.prestamo_pendiente) || 0) - (parseFloat(this.prestamos) || 0);

    },
    prestamo_pendiente() {
      this.actualizarTotalDeducciones();

      this.total_prestamo = (parseFloat(this.prestamo_pendiente) || 0) - (parseFloat(this.prestamos) || 0);

    },
    pagoDiasFestivos(newVal) {
      const pagoDiaFestivo = this.empleado.laborales.pago_dia_festivo || 0;
      const pagoDiaFestivoA = this.empleado.laborales.pago_dia_festivo_a || 0;
      this.$set(this, "totalDiasFestivos", newVal * pagoDiaFestivo);
      this.$set(this, "totalDiasFestivosA", newVal * pagoDiaFestivoA);
      this.calculateSalarioNeto();

    },
    horasExtras(newVal) {
      const pagoPorHora = this.empleado.laborales.pago_hora_extra || 0;
      const pagoPorHoraA = this.empleado.laborales.pago_hora_extra_a || 0;

      this.$set(this, "totalHorasExtras", newVal * pagoPorHora);
      this.$set(this, "totalHorasExtrasA", newVal * pagoPorHoraA);

      this.calculateSalarioNeto();
    },

    diasAusencias(newVal) {
      const descuentoAusencia = this.empleado.laborales.descuento_ausencia || 0;
      const descuentoAusenciaA = this.empleado.laborales.descuento_ausencia_a || 0;
      this.$set(this, "totalDiasAusente", newVal * descuentoAusencia);
      this.$set(this, "totalDiasAusenteA", newVal * descuentoAusenciaA);
      this.calculateSalarioNeto();
    },
    vacaciones(newVal) {
      const primaVacacional = this.empleado.laborales.prima_vacacional || 0;
      const sueldoDia = this.empleado.laborales.sueldo_diario || 0;
      const sueldoDiaA = this.empleado.laborales.sueldo_asimilado || 0;
      this.$set(this, "totalPrimaVacacional", newVal * sueldoDia * primaVacacional);
      this.$set(this, "totalPrimaVacacionalA", newVal * sueldoDiaA * primaVacacional);

      this.actualizarTotalPrestaciones();

    },

  },
  mounted() {
    const laborales = this.empleado.laborales || {};  // Si no está definido, usa un objeto vacío
    const sueldos = this.calcularSueldo(laborales);

    this.sueldoBase = sueldos.ordinario || 0;
    this.sueldoAsimilado = sueldos.asimilado || 0;
    this.obtenerPeriodosPendientes();

    console.log("🚀 ~ mounted ~ laborales:", laborales)
    if (this.empleado?.pre_nomina_empleados && this.empleado.pre_nomina_empleados.length > 0) {
      const preNomina = this.empleado.pre_nomina_empleados[0]; // Accede al primer (y único) registro de pre_nomina_empleados
      this.horasExtras = preNomina.horasExtras || 0;
      this.pagoDiasFestivos = preNomina.pagoDiasFestivos || 0;
      this.diasAusencias = preNomina.dias_ausencias || 0;
      this.vacaciones = preNomina.dias_vacaciones || 0;
      this.prestamos = preNomina.prestamos || 0;
      this.prestamo_pendiente = preNomina.prestamo_pendiente || 0;
      this.descuentosAdicionales = preNomina.descuentos_adicionales || 0; // Verifica si "descuentos_adicionales" es el nombre correcto
      // Parseo de prestaciones_extra y deducciones_extra (que vienen como JSON escapados)
      try {
        if (preNomina.prestaciones_extra) {
          if (typeof preNomina.prestaciones_extra === 'string') {
            this.prestacionesExtras = JSON.parse(preNomina.prestaciones_extra);
            // Si fuera doble escapado, usa JSON.parse(JSON.parse(...)) pero verifica primero
          } else if (typeof preNomina.prestaciones_extra === 'object') {
            this.prestacionesExtras = preNomina.prestaciones_extra;
          } else {
            this.prestacionesExtras = [];
          }
        } else {
          this.prestacionesExtras = [];
        }

        if (preNomina.deducciones_extra) {
          if (typeof preNomina.deducciones_extra === 'string') {
            this.deduccionesExtras = JSON.parse(preNomina.deducciones_extra);
          } else if (typeof preNomina.deducciones_extra === 'object') {
            this.deduccionesExtras = preNomina.deducciones_extra;
          } else {
            this.deduccionesExtras = [];
          }
        } else {
          this.deduccionesExtras = [];
        }
      } catch (error) {
        console.error("Error al parsear prestaciones o deducciones:", error);
        this.prestacionesExtras = [];
        this.deduccionesExtras = [];
      }


      this.actualizarTotalPrestaciones();
      this.actualizarTotalDeducciones();
      this.calculateSalarioNeto();
      this.calculateTotalPagar();
    }
    window.addEventListener('wheel', () => { }, { passive: true });
    window.addEventListener('mousewheel', () => { }, { passive: true });

  }
};
</script>



