<template>
  <div class="content">
    <component :is="currentComponent" 
    :idPortal="idPortal"
    :idUsuario="idUsuario"
    :idCliente="idCliente"
    :idRol="idRol"
    @status-modificado="handleStatusModificado"
    ></component>
  </div>
</template>

<script>
export default {
  props: {
    currentComponent: {
      type: [Object, String],
      required: true
    },
    idPortal: {
      type: String,
      default: ''
    },
    idUsuario: {  // Añadido aquí
      type: String,
      default: ''
    },
    idCliente: {  // Añadido aquí
      type: String,
      default: ''
    },
    idRol: {  // Añadido aquí
      type: String,
      default: ''
    }
  },
  methods: {
    handleStatusModificado(data) {
      this.$emit('status-modificado', data);
      //console.log("🚀 ~ data:", data) // Emitir hacia App.vue
    }
    
  }
};
</script>

<style scoped>
.content {
  padding: 20px;
}
</style>