<template>
  <div id="app">
    <Header 
      :modules="modules" 
      :selectedModule="selectedModule" 
      :idPortal="idPortal" 
      @select-module="handleSelectModule"
      :idCliente="idCliente"
      :isMobile="isMobile"  
      :idRol="idRol" 
    />
    <ContentSection 
      :currentComponent="currentComponent" 
      :idPortal="idPortal" 
      :idUsuario="idUsuario" 
      :idCliente="idCliente" 
      :idRol="idRol" 
    />

    <!-- Modal global para documentos -->
    <DocumentModal 
      :visible="isDocumentModalVisible"
      :folder="selectedFolder"
      :documentName="selectedDocumentName"
      @close="closeDocumentModal"
    />
  </div>
</template>

<script>
import Header from './components/EmpleadosHeader.vue';
import ContentSection from './components/ContentSection.vue';
import Module1 from './components/EmpleadosExpediente.vue';
import Module2 from './components/EmpleadosCursos.vue';
import Module3 from './components/EmpleadosEvaluacion.vue';
import DocumentModal from './components/modales/VerDocumentos.vue'; // Asegúrate que la ruta sea correcta
import { EventBus } from './eventBus';

export default {
  components: {
    Header,
    ContentSection,
    DocumentModal  // Registro del modal global
  },
  props: {
    idPortal: {
      type: String,
      default: ''
    },
    idUsuario: {
      type: String,
      default: ''
    },
    idCliente: {
      type: String,
      default: ''
    },
    idRol: {
      type: String,
      default: ''
    }
  },
  data() {
    return {
      modules: [
        { id: 'module1', title: 'Expedientes de empleados', component: Module1, statusKey: 'documentos', icon: 'fas fa-file-alt' },
        { id: 'module2', title: 'Cursos y capacitaciones', component: Module2, statusKey: 'cursos', icon: 'fas fa-graduation-cap' },
        { id: 'module3', title: 'Evaluaciones', component: Module3, statusKey: 'evaluaciones', icon: 'fas fa-chart-line' }
      ],
      selectedModule: 'module1',
      isMobile: false, // Estado para detectar si es móvil

      // Variables para el modal de documentos
      isDocumentModalVisible: false,
      selectedFolder: '',         // Carpeta del documento, ej. '_documentEmpleado', '_cursos', etc.
      selectedDocumentName: ''    // Nombre del archivo, ej. 'documento.png'
    };
  },
  computed: {
    currentComponent() {
      const module = this.modules.find(m => m.id === this.selectedModule);
      return module ? module.component : null;
    }
  },
  methods: {
    handleSelectModule(id) {
      this.selectedModule = id;
    },
    checkIfMobile() {
      this.isMobile = window.innerWidth <= 768;
    },
    // Método para abrir el modal de documentos (se llama desde el event bus)
    openDocumentModal(doc, folder) {
      this.selectedDocumentName = doc.name|| doc.nameDocument || doc.name_document || 'default_filename.png';
      this.selectedFolder = folder; // Ej.: '_documentEmpleado', '_cursos', etc.
      this.isDocumentModalVisible = true;
    },
    closeDocumentModal() {
      this.isDocumentModalVisible = false;
      this.selectedDocumentName = '';
      this.selectedFolder = '';
    }
  },
  mounted() {
    this.checkIfMobile(); // Verificar si es móvil al cargar
    window.addEventListener('resize', this.checkIfMobile);

    // Escuchar el evento global para abrir el modal
    EventBus.$on('open-document', (doc, folder) => {
      this.openDocumentModal(doc, folder);
    });
  },
  beforeDestroy() {
    window.removeEventListener('resize', this.checkIfMobile);
    EventBus.$off('open-document');
  }
};
</script>

