import Vue from 'vue';
import 'jspreadsheet-ce/dist/jspreadsheet.css';
import App from './App.vue';
if (process.env.NODE_ENV === 'development') {
  require('@fortawesome/fontawesome-free/css/all.css');
}

// El resto de tu código de main.js

const apiUrl = window.location.hostname === 'localhost' 
  ? 'http://localhost:8000/api' 
  : 'https://apicandidatos.talentsafecontrol.com/api';

const imageUrl = window.location.hostname === 'localhost' 
  ? 'http://localhost/rodi_portal/_perfilEmpleado/'
  : 'https://portal.talentsafecontrol.com/_perfilEmpleado/';

const documentUrl = window.location.hostname === 'localhost' 
  ? 'http://localhost/rodi_portal/'
  : 'https://portal.talentsafecontrol.com/';

  const examUrl = window.location.hostname === 'localhost' 
  ? 'http://localhost/rodi_portal/_examEmpleado/'
  : 'https://portal.talentsafecontrol.com/_examEmpleado/';

  const rodiPortal = window.location.hostname === 'localhost' 
  ? 'http://localhost/rodi_portal/' 
  : 'https://portal.talentsafecontrol.com/';

const csrfMetaTag = document.querySelector('meta[name="csrf-token"]');
const csrfToken = csrfMetaTag ? csrfMetaTag.getAttribute('content') : null;

// Hacer que la URL de la API esté disponible globalmente
Vue.prototype.$apiUrl = apiUrl;
Vue.prototype.$imageUrl = imageUrl;
Vue.prototype.$documentUrl = documentUrl;
Vue.prototype.$examUrl = examUrl;
Vue.prototype.$rodiPortal = rodiPortal;


Vue.prototype.$csrfToken = csrfToken;

// Define la función para montar la aplicación Vue
let isMounted = false;

export function mountVueApp(selector) {
  if (isMounted) return; // Salir si ya está montado

  const appElement = document.querySelector(selector);
  // Verifica el parámetro de la URL
  const params = new URLSearchParams(window.location.search);
  const idPortalFromUrl = params.get('idPortal');
  const idUsuarioFromUrl = params.get('idUsuario'); // Agrega esto para idUsuario
  const idClienteFromUrl = params.get('idCliente'); 
  const idRolFromUrl = params.get('idRol'); 
  // Lee el atributo solo si el parámetro no está presente
  const idPortal = idPortalFromUrl || appElement.getAttribute('data-your-value');
  const idUsuario = idUsuarioFromUrl || appElement.getAttribute('data-your-user-value'); // Agrega esto para idUsuario
  const idCliente = idClienteFromUrl || appElement.getAttribute('data-your-client-value'); 
  const idRol = idRolFromUrl || appElement.getAttribute('data-your-rol-value'); // Agrega esto para idUsuario
  // Agrega esto para idUsuario

  

  new Vue({
    render: h => h(App, { props: { 
      idPortal: idPortal, 
      idUsuario: idUsuario,
      idCliente: idCliente,
      idRol: idRol 
    }
   }),
  }).$mount(selector);

  isMounted = true; // Marcar como montado
}

window.mountVueApp = mountVueApp;

if (document.getElementById('app')) {
  mountVueApp('#app');
}