<template>
  <header class="empleados-header">
    <div v-for="module in modules" :key="module.id" class="empleados-header-button-container">
      <!-- Mostrar botón con texto en pantallas grandes -->
      <button v-if="!isMobile" :class="{
        'empleados-header-button': true,
        'empleados-header-button-active': selectedModule === module.id
      }" @click="selectModule(module.id)">
        {{ module.title }}
      </button>

      <!-- Mostrar icono en pantallas pequeñas -->
      <i v-if="isMobile" :class="module.icon" @click="selectModule(module.id)" class="empleados-header-icon"></i>
      <span>&nbsp;</span>
      <!-- Mostrar círculo de estado -->
      <span v-if="selectedModule !== module.id" :style="getStatusStyle(moduleStatuses[module.statusKey])"
        class="status-circle"></span>
        <span>&nbsp;</span>
      <span v-if="selectedModule !== module.id" :style="getStatusStyle(moduleGeneralStatuses[module.statusKey])"
        class="status-cuadro"></span>
      <!-- Descripción solo cuando el módulo está seleccionado -->
      <p v-if="selectedModule === module.id" class="empleados-header-description">
        {{ module.description }}
      </p>
    </div>
  </header>
</template>

<script>
export default {
  props: {
    modules: {
      type: Array,
      required: true
    },
    selectedModule: {
      type: String,
      required: true
    },
    idPortal: {
      type: String,
      default: ''
    },
    idCliente: {
      type: String,
      default: ''
    },
    idRol: {
      type: String,
      default: ''
    },
    isMobile: {
      type: Boolean,
      required: true
    }
  },
  data() {
    return {
      moduleStatuses: {},
      moduleGeneralStatuses: {}
    };
  },
  mounted() {
    this.fetchModuleStatuses(); // Cargar estados al iniciar
  },
  methods: {
    fetchModuleStatuses() {
      const apiUrl = `${this.$apiUrl}/empleados/status?id_portal=${this.idPortal}&id_cliente=${this.idCliente}`;

      fetch(apiUrl)
        .then(response => {
          return response.json()
        })
        .then(data => {
          this.moduleStatuses = {
            documentos: data.statusDocuments,  // Estado específico de documentos
            cursos: data.statusCursos,         // Estado específico de cursos
            evaluaciones: data.statusEvaluaciones ?? '', // Estado específico de evaluaciones
          };

          this.moduleGeneralStatuses = {
            documentos: data.estadoDocumentos,  // Estado general de documentos
            cursos: data.estadoCursos    // Estado general de cursos
          };

        })
        .catch(error => {
          console.error('Error fetching module statuses:', error);
        });
    },
    getStatusStyle(status) {
      switch (status) {
        case 'rojo':
          return { backgroundColor: 'rgba(255, 0, 0, 0.906)' };
        case 'amarillo':
          return { backgroundColor: 'rgb(580, 210, 50)' };
        case 'verde':
          return { backgroundColor: 'rgba(128, 128, 128, 0.0)' };
        default:
          return { backgroundColor: 'rgba(128, 128, 128, 0.0)' }; // Transparente
      }
    },
    selectModule(id) {
      this.$emit('select-module', id);
      this.fetchModuleStatuses(); // Llama a la función al cambiar de módulo
    }
  }
};
</script>

<style scoped>
/* Estilos generales */
.empleados-header {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  padding: 0;
  margin: 0;
  background-color: #f0f0f0;
}

.empleados-header-button-container {
  text-align: center;
}

.empleados-header-button {
  display: inline-block;
  margin: 0;
  padding: 12px 24px;
  border: 2px solid #007bff;
  border-radius: 8px;
  background-color: #5783b1;
  color: white;
  cursor: pointer;
  font-size: 18px;
  font-weight: bold;
  text-align: center;
  transition: all 0.3s ease;
  outline: none;
}

.empleados-header-button-active {
  background-color: #4098f6;
  border-bottom: 3px solid #ffffff;
}

.empleados-header-button:hover {
  background-color: #4a6ea9;
}

.empleados-header-button:focus {
  background-color: #4a6ea9;
}

.empleados-header-icon {
  font-size: 40px;
  margin: 15px;
  cursor: pointer;
}

.status-circle {
  width: 20px;
  height: 20px;
  border-radius: 50%;
  margin-top: 8px;
  display: inline-block;
}

.status-cuadro {
  width: 20px;
  height: 20px;
  margin-top: 8px;
  display: inline-block;
}

.empleados-header-description {
  color: black;
  font-size: 12px;
  font-weight: normal;
}
</style>
