<template>
  <div v-if="isValidEvaluation" class="EmployeeEvaluation-modal-overlay" @click="closeModal">
    <div class="EmployeeEvaluation-modal-content">
      <h2>Detalles de la Evaluación</h2>
      <p><strong>ID:</strong> {{ evaluation.id }}</p>
      <p><strong>Nombre:</strong> {{ evaluation.name }}</p>
      <p><strong>Ejecutada por:</strong> {{ evaluation.executedBy }}</p>
      <h3>Conclusión</h3>
      <p>{{ evaluation.conclusiones }}</p>
      <h3>Acciones a Considerar</h3>
      <ul>
        <li>{{ evaluation.acciones }}</li>
      </ul>
      <button class="EmployeeEvaluation-button" @click="closeModal">Cerrar</button>
    </div>
  </div>
</template>


<script>
export default {
  name: 'EvaluationDetails',
  props: {
    evaluation: {
      type: Object,
      required: true
    }
  },
  computed: {
    isValidEvaluation() {
      return this.evaluation && typeof this.evaluation === 'object';
    }
  },
  methods: {
    closeModal() {
      this.$emit('close');
    }
  }
};
</script>


<style scoped>
.EmployeeEvaluation-modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5); /* Fondo oscuro semitransparente */
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
  transition: opacity 0.3s ease; /* Transición suave */
}

.EmployeeEvaluation-modal-content {
  background: white;
  padding: 30px;
  border-radius: 10px;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.3); /* Sombra suave */
  max-width: 600px; /* Máximo ancho para el modal */
  width: 90%; /* Ancho responsivo */
  animation: slideIn 0.4s ease; /* Animación de entrada */
}

@keyframes slideIn {
  from {
    opacity: 0;
    transform: translateY(-30px); /* Desplazamiento hacia arriba */
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

h2 {
  margin-bottom: 20px;
  color: #333; /* Color del título */
}

button.EmployeeEvaluation-button {
  background-color: #4CAF50; /* Verde */
  color: white;
  border: none;
  padding: 10px 15px;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s; /* Transición de color */
}

button.EmployeeEvaluation-button:hover {
  background-color: #45a049; /* Color al pasar el ratón */
}

h3 {
  margin-top: 20px;
  color: #555; /* Color de subtítulos */
}
</style>

