<template>
  <div class="profile-modal-overlay" v-if="isVisible" @click="handleCloseModal">
    <div class="profile-modal-content" @click.stop>
      <div v-if="empleadoData">
        <ProfileHeader :empleadoData="empleadoData" :idRol="idRol"  @close="handleCloseModal" />
        <ProfileTabs :currentTab="currentTab" :status="statusDocuments" :estado="estadoDocumento" :estadoExam="estadoExam" :exam="statusExam" @update:tab="currentTab = $event" @update:status="statusDocuments = $event" @update:exam="statusExam = $event" @update:estadoExam="estadoExam = $event"  @update:estado="estadoDocumento = $event" :idRol="idRol" />

        <div class="tab-content">
          <div v-if="currentTab === 'general'">
            <GeneralInfo :empleadoData="empleadoData" :idRol="idRol" :updateEmpleado="updateEmpleado"
              :idCliente="idCliente" @close="handleCloseModal" @empleadoActualizado="$emit('empleadoActualizado', $event)" />
          </div>
          <div v-if="currentTab === 'medical'">
            <MedicalInfo :empleadoData="empleadoData" :idRol="idRol" @close="handleCloseModal"
            :idCliente="idCliente" @empleadoActualizado="$emit('empleadoActualizado', $event)" />
          </div>
          <div v-if="currentTab === 'documents'">
            <Documents :empleadoData="empleadoData" :idRol="idRol" :idCliente="idCliente" :openAddDocumentModal="openAddDocumentModal"
              @close="handleCloseModal" />
          </div>
          <div v-if="currentTab === 'bgv'">
            <Bgv :empleadoData="empleadoData" :idRol="idRol" :openAddExamtModal="openAddExamModal"
              :idCliente="idCliente" @close="handleCloseModal" />
          </div>
          <div v-if="currentTab === 'laborales' && showLaboralesModal">
            <Laborales :empleadoData="empleadoData" :idRol="idRol" :openAddExamtModal="openLaboralesModal"
              :idCliente="idCliente" :idPortal="idPortal" @close="handleCloseModal" />
          </div>
        </div>

        <AddDocumentModal v-if="showAddDocumentModal" :isVisible="showAddDocumentModal" :employeeId="empleadoData.id" :idCliente="idCliente"
          :id_portal="empleadoData.id_portal" @close="closeAddDocumentModal" />
        <AddExamModal v-if="showAddExamModal" :isVisible="showAddExamModal" :employeeId="empleadoData.id"
          :id_portal="empleadoData.id_portal" @close="closeAddExamModal" :idCliente="idCliente" />
      </div>
    </div>
  </div>
</template>

<script>
import AddDocumentModal from './AddDocumentModal.vue';
import AddExamModal from './AddExamModal.vue';
import ProfileHeader from './PerfilHeader.vue';
import ProfileTabs from './PerfilTabs.vue';
import GeneralInfo from './GeneralInfo.vue';
import MedicalInfo from './MedicalInfo.vue';
import Documents from './PerfilDocuments.vue';
import Bgv from './PerfilEse.vue';
import Laborales from './LaboralesModal.vue';

export default {
  name: 'ProfileModal',
  components: {
    Laborales,
    AddExamModal,
    AddDocumentModal,
    ProfileHeader,
    ProfileTabs,
    GeneralInfo,
    MedicalInfo,
    Documents,
    Bgv
  },
  props: {
    empleado: Object,
    isVisible: Boolean,
    idCliente: {
      type: String,
      default: ''
    },
    idRol: {
      type: String,
      default: ''
    },
      idPortal: {
      type: String,
      default: ''
    }
  },
  data() {
    return {
      currentTab: 'general',
      showLaboralesModal: false,
      showAddDocumentModal: false,
      showAddExamModal: false,
      documentType: '',
      empleadoData: null,
      error: null,
      statusDocuments: this.empleado ? this.empleado.statusDocuments : null,
      estadoDocumento: this.empleado ? this.empleado.estadoDocumento : null, 
      statusExam: this.empleado ? this.empleado.statusExam : null, 
      estadoExam: this.empleado ? this.empleado.estadoExam : null,   // Cambiado aquí
    };
  },
  methods: {
    handleCloseModal() {
      this.$emit('close');
      this.currentTab = 'general';
      this.empleadoData = null; // Limpia los datos del empleado
    },
    openAddDocumentModal(type = 'general') {
      this.documentType = type;
      this.showAddDocumentModal = true;
    },
    closeAddDocumentModal() {
      this.showAddDocumentModal = false;
    },
    openAddExamModal(type = 'general') {
      this.documentType = type;
      this.showExamModal = true;
    },
    closeAddExamModal() {
      this.showAddExamModal = false;
    },
    openLaboralesModal(type = 'general') {
      this.documentType = type;
      this.showLaboralesModal = true; // Cambia el estado para mostrar el modal de laborales
    },
    closeLaboralesModal() {
      this.showLaboralesModal = false; // Cierra el modal de laborales
    },

    updateEmpleado(updatedData) {
      this.empleadoData = { ...this.empleadoData, ...updatedData };
      this.$emit('empleadoActualizado', updatedData); // Emitir el evento de actualización
    },
  },
  watch: {
    empleado(newValue) {
      this.statusDocuments = newValue ? newValue.statusDocuments : null; // Cambiado aquí
      this.$emit('update:status', this.statusDocuments); 
      this.estadoDocumento = newValue ? newValue.estadoDocumento : null; // Cambiado aquí
      this.$emit('update:estado', this.estadoDocumento);
      this.statusExam = newValue ? newValue.statusExam : null; // Cambiado aquí
      this.$emit('update:exam', this.statusExam);
      this.estadoExam = newValue ? newValue.estadoExam : null; // Cambiado aquí
      this.$emit('update:estadoExam', this.estadoExam);// Emitir el nuevo status
    },
    isVisible(newValue) {
      if (newValue) {
        this.empleadoData = { ...this.empleado }; // Crea una copia de los datos
        this.currentTab = 'general';
      } else {
        this.currentTab = 'general';
        this.empleadoData = null; // Limpia los datos
      }
    },
    currentTab(newTab) {
      // Si la pestaña es 'laborales', mostramos el modal
      if (newTab === 'laborales') {
        this.showLaboralesModal = true; // Mostrar el modal cuando se selecciona la pestaña 'laborales'
      } else {
        this.showLaboralesModal = false; // Ocultar el modal cuando se selecciona cualquier otra pestaña
      }
    },
  }
};
</script>




