<template>
  <div class="modal-overlay" @click="closeModal">
    <div class="modal-content" @click.stop>
      <h2>Cargar Archivo</h2>
      <form @submit.prevent="submitFile">
        <div class="form-group">
          <label for="fileInput">Selecciona un archivo:</label>
          <input type="file" id="fileInput" @change="handleFileUpload" required />
        </div>
        <div class="button-container">
          <button type="submit" class="submit-button">Cargar</button>
          <button type="button" class="close-button" @click="closeModal">Cerrar</button>
        </div>
      </form>
      <p v-if="fileName">Archivo seleccionado: {{ fileName }}</p>
    </div>
  </div>
</template>

<script>
import Swal from 'sweetalert2';

export default {
  props: {
    nameDocument: {
      type: String,
      required: true,
    },
  },
  data() {
    return {
      selectedFile: null,
      fileName: '',
    };
  },
  methods: {
    handleFileUpload(event) {
      this.selectedFile = event.target.files[0];
      this.fileName = this.selectedFile ? this.selectedFile.name : '';
    },
    async submitFile() {
      const formData = new FormData();
      formData.append('file', this.selectedFile);
      formData.append('file_name', this.selectedFile);
      formData.append('file', this.selectedFile);

      // Eliminar el archivo anterior
      if (this.nameDocument) {
        //console.log("🚀 ~ submitFile ~ this.nameDocument:", this.nameDocument)
        try {
          await this.$axios.post(`${this.$apiUrl}/delete`, { file_name: this.nameDocument, carpeta:'_evaluacionesPortal' });
          Swal.fire({
            icon: 'success',
            title: 'Success',
            text: 'Previous file deleted successfully.',
          });
        } catch (error) {
          console.error('Error deleting previous file:', error);
          Swal.fire({
            icon: 'error',
            title: 'Error',
            text: 'Failed to delete the previous file.',
          });
          return; // Salir si hubo un error en la eliminación
        }
      }

      try {
        await this.$axios.post('/your/upload/endpoint', formData, {
          headers: {
            'Content-Type': 'multipart/form-data',
          },
        });
        Swal.fire({
          icon: 'success',
          title: 'Success',
          text: 'File uploaded successfully.',
        });
        this.$emit('fileUploaded'); // Emitir un evento para indicar que el archivo se ha subido
        this.closeModal();
      } catch (error) {
        console.error('Error uploading file:', error);
        Swal.fire({
          icon: 'error',
          title: 'Error',
          text: 'Failed to upload the file.',
        });
      }
    },
    closeModal() {
      this.$emit('close'); // Emitir evento para cerrar el modal
    },
  },
};
</script>





<style scoped>
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.6); /* Fondo oscuro semitransparente */
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000; /* Asegúrate de que esté por encima de otros elementos */
  transition: opacity 0.3s ease; /* Transición suave */
}

.modal-content {
  background: #fefefe; /* Fondo blanco suave */
  padding: 30px;
  border-radius: 10px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.25); /* Sombra sutil */
  max-width: 400px; /* Máximo ancho para el modal */
  width: 90%; /* Ancho responsivo */
  animation: fadeIn 0.4s ease; /* Animación de entrada */
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(-20px); /* Desplazamiento hacia arriba */
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

h2 {
  margin-bottom: 20px;
  color: #333; /* Color del título */
  font-size: 24px; /* Tamaño de fuente */
}

.form-group {
  margin-bottom: 15px;
}

label {
  display: block;
  margin-bottom: 5px;
  color: #555; /* Color del texto */
}

input[type="file"] {
  border: 1px solid #ccc;
  border-radius: 5px;
  padding: 10px;
  width: 100%; /* Ancho completo */
  margin-top: 5px;
}

.button-container {
  display: flex;
  justify-content: space-between;
  margin-top: 20px; /* Espacio entre el formulario y los botones */
}

.submit-button {
  background-color: #4CAF50; /* Verde */
  color: white;
  border: none;
  padding: 10px 15px;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s; /* Transición de color */
}

.submit-button:hover {
  background-color: #45a049; /* Color al pasar el ratón */
}

.close-button {
  background-color: #f44336; /* Rojo */
  color: white;
  border: none;
  padding: 10px 15px;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s; /* Transición de color */
}

.close-button:hover {
  background-color: #e53935; /* Color al pasar el ratón */
}

p {
  margin-top: 10px;
  color: #777; /* Color para el texto del archivo seleccionado */
}
</style>
