<template>
  <div class="add-document-modal-overlay" v-if="isVisible" @click="handleCloseModal">
    <div class="add-document-modal-content" @click.stop>
      <h3>Agregar Documento</h3>
      <div class="file-drop-zone" @dragover.prevent @drop.prevent="handleFilesDrop" @click="triggerFileInput">
        <p>Arrastra archivos aquí o haz clic para seleccionarlos</p>
        <input ref="multiFileInput" type="file" multiple @change="handleMultipleFileSelect" style="display: none;" />
      </div>
      <table class="document-table">
        <thead>
          <tr>
            <th>Tipo</th>
            <th>Descripción</th>
            <th>Vencimiento</th>
            <th>Recordatorio</th>
            <th>Estatus</th>
            <th>Acciones</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(doc, index) in documents" :key="index">
            <td>
              <select v-model="doc.selectedOption" @change="updateDocumentName(doc)">
                <option value="">Selecciona...</option>
                <option value="other">Otro...</option>
                <option v-for="opt in documentOptions" :key="opt.id" :value="opt.name">{{ opt.name }}</option>
              </select>
              <input v-if="doc.selectedOption === 'other'" v-model="doc.name" type="text"
                placeholder="Otro nombre..." />
            </td>
            <td><input type="text" v-model="doc.description" /></td>
            <td><input type="date" v-model="doc.expiryDate" /></td>

            <td>
              <select v-model="doc.expiryReminder">
                <option value="0">No</option>
                <option value="1">1 día</option>
                <option value="7">1 semana</option>
                <option value="15">15 días</option>
                <option value="30">1 mes</option>
              </select>
            </td>
            <td>
              <select v-model="doc.status">
                <option value="1">Bueno</option>
                <option value="2">Regular</option>
                <option value="3">Malo</option>
              </select>
            </td>
            <td>
              <button class="delete-icon-button" @click="removeDocumentForm(index)"><i
                  class="fas fa-trash-alt"></i></button>
            </td>
          </tr>
        </tbody>
      </table>
      <button @click="addDocumentForm" class="add-row-button">+ Agregar documento</button>
      <div class="modal-buttons">
        <button @click="handleSave">Guardar</button>
        <button type="button" @click="handleCloseModal">Cancelar</button>
      </div>

    </div>
  </div>
</template>


<script>
import axios from 'axios';
import Swal from 'sweetalert2';
import moment from 'moment-timezone';
export default {
  name: 'AddDocumentModal',
  props: {
    isVisible: {
      type: Boolean,
      required: true
    },
    employeeId: {
      type: Number,
      required: true
    },
    id_portal: {
      type: Number,
      default: null
    },
     origen: {
      type: Number,
      default: null
    },
    endpointConfig: { // <-- ESTA ES LA QUE FALTABA
      type: Object,
      required: true
    }
    
  },
  data() {
    return {
      selectedOption: null,
      documentOptions: [],
      documents: [
        this.getEmptyDocument()
      ]
    };
  },
  mounted() {
      if (!this.endpointConfig.tabla || !this.endpointConfig.endpoint) {
    
      console.warn('endpointConfig incompleto:', this.endpointConfig);
    }
    this.fetchDocumentOptions();
  },
  methods: {
    triggerFileInput() {
      this.$refs.multiFileInput.click();
    },
    handleMultipleFileSelect(e) {
      this.addFiles(e.target.files);
      e.target.value = null;
    },
    handleFilesDrop(e) {
      this.addFiles(e.dataTransfer.files);
    },
    addFiles(fileList) {
      Array.from(fileList).forEach(file => {
        const newDoc = this.getEmptyDocument();
        newDoc.file = file;
        // Quitar extensión
        newDoc.name = file.name.replace(/\.[^/.]+$/, "");
        newDoc.selectedOption = 'other';
        this.documents.push(newDoc);
      });
    },
    getEmptyDocument() {
      return {
        name: '',
        description: '',
        uploadDate: this.getCurrentDate(),
        expiryDate: this.getFutureDate(1),
        expiryReminder: 0,
        file: null,
        status: 1,
        selectedOption: ''
      };
    },
    addDocumentForm() {
      this.documents.push(this.getEmptyDocument());
    },
    removeDocumentForm(index) {
      if (this.documents.length > 1) {
        this.documents.splice(index, 1);
      }
    },
    async fetchDocumentOptions() {
      try {
        const params = this.id_portal ? { id_portal: this.id_portal, tabla: this.endpointConfig.tabla } : {}; const response = await axios.get(`${this.$apiUrl}/document-options`, { params });
        this.documentOptions = response.data;
      } catch (error) {
        console.error('Error fetching document options:', error);
      }
    },
    getCurrentDate() {
      const today = new Date();
      const year = today.getFullYear();
      const month = String(today.getMonth() + 1).padStart(2, '0');
      const day = String(today.getDate()).padStart(2, '0');
      return `${year}-${month}-${day}`;
    },
    getFutureDate(years) {
      const futureDate = new Date();
      futureDate.setFullYear(futureDate.getFullYear() + years);
      const year = futureDate.getFullYear();
      const month = String(futureDate.getMonth() + 1).padStart(2, '0');
      const day = String(futureDate.getDate()).padStart(2, '0');
      return `${year}-${month}-${day}`;
    },
    handleCloseModal() {
      this.$emit('close');
    },
    async handleSave() {
      const apiUrl = `${this.$apiUrl}${this.endpointConfig.endpoint}`;
      const csrfMetaTag = document.querySelector('meta[name="csrf-token"]');
      const csrfToken = csrfMetaTag ? csrfMetaTag.getAttribute('content') : null;

      for (const doc of this.documents) {

        const formData = new FormData();
        if(this.origen != null){
          formData.append('origen', this.origen);
        } 
        formData.append('employee_id', this.employeeId);
        formData.append('name', doc.selectedOption === 'other' ? doc.name : doc.selectedOption);
        formData.append('description', doc.description);
        formData.append('expiry_date', doc.expiryDate);
        formData.append('expiry_reminder', doc.expiryReminder);
        formData.append('status', doc.status);
        formData.append('file', doc.file);
        formData.append('creacion', moment.tz('America/Mexico_City').format('YYYY-MM-DD HH:mm:ss'));
        formData.append('edicion', moment.tz('America/Mexico_City').format('YYYY-MM-DD HH:mm:ss'));
        formData.append('carpeta', this.endpointConfig.tabla);
        formData.append('id_portal', this.id_portal);

        for (let [key, value] of formData.entries()) {
  console.log(`${key}:`, value);
}
// eslint-disable-next-line no-debugger
  debugger;
       // console.log("🚀 ~ handleSave ~ formData:", formData)

        try {
          const response = await axios.post(apiUrl, formData, {
            headers: {
              'X-CSRF-TOKEN': csrfToken,
              'Content-Type': 'multipart/form-data'
            }
          });
          this.$emit('documentoSubido', response.data);
        } catch (error) {
          console.error('Error subiendo documento:', error);
        }
      }


      Swal.fire({
        icon: 'success',
        title: 'Documentos subidos',
        text: 'Todos los documentos se han subido correctamente.',
        confirmButtonText: 'Aceptar'
      }).then(() => {
        this.handleCloseModal();
      });
    },

    



    handleFileUpload(event, doc) {
      doc.file = event.target.files[0];
    },
    updateDocumentName(doc) {
      if (doc.selectedOption !== 'other') {
        doc.name = doc.selectedOption;
      }
    }
  }
};
</script>

