<template>
  <div class="profile-tabs">
    <button @click="setTab('general')" :class="{ active: currentTab === 'general' }">
      Información General
    </button>

    <button @click="setTab('laborales')" :class="{ active: currentTab === 'laborales' }">
      Datos Laborales
    </button>

    <button @click="setTab('medical')" :class="{ active: currentTab === 'medical' }">
      Información Médica
    </button>

    <button @click="handleDocumentsClick($event)" :class="{ active: currentTab === 'documents' }">
      Documentos del Empleado
      <span v-if="status === 'amarillo' || status === 'rojo'" class="alert-symbol" :class="status"></span>
      <span v-if="estado === 'amarillo' || estado === 'rojo'" class="alert-symbol2" :class="estado"></span>
    </button>

    <button @click="handleExamClick($event)" :class="{ active: currentTab === 'bgv' }">
      Registros de BGV y Exámenes
      <span v-if="exam === 'amarillo' || exam === 'rojo'" class="alert-symbol3" :class="exam"></span>
      <span v-if="estadoExam === 'amarillo' || estadoExam === 'rojo'" class="alert-symbol4" :class="estadoExam"></span>
    </button>
  </div>
</template>

<script>
export default {
  props: {
    currentTab: String,
    status: String,
    estado: String,
    exam: String,
    estadoExam: String,
  },
  methods: {
    setTab(tab) {
      this.$emit('update:tab', tab);
      this.$emit('openLaboralesModal', tab === 'laborales');
    },

    handleDocumentsClick(event) {
      this.setTab('documents');
      this.ocultarAlertas(event, ['.alert-symbol', '.alert-symbol2']);
    },

    handleExamClick(event) {
      this.setTab('bgv');
      this.ocultarAlertas(event, ['.alert-symbol3', '.alert-symbol4']);
    },

    ocultarAlertas(event, clases) {
      this.$nextTick(() => {
        clases.forEach(cls => {
          const span = event.currentTarget.querySelector(cls);
          if (span) span.style.display = 'none';
        });
      });
    },
  },
};
</script>

<style scoped>
.profile-tabs {
  display: flex;
  justify-content: center;
  border-bottom: 1px solid #ddd;
  margin-bottom: 15px;
  padding: 10px 5px;
}

.profile-tabs button {
  background: none;
  border: none;
  padding: 10px 15px;
  cursor: pointer;
  font-size: 16px;
  color: #007bff;
  transition: border-bottom 0.3s ease, font-size 0.3s ease;
  text-align: center;
  flex: 1 0 auto;
}

.profile-tabs button.active {
  border-bottom: 3px solid #007bff;
  font-weight: bold;
}

@media (max-width: 600px) {
  .profile-tabs {
    flex-direction: column;
    align-items: stretch;
    padding: 10px 0;
  }

  .profile-tabs button {
    font-size: 14px;
    padding: 8px 10px;
    text-align: left;
  }

  .profile-tabs button.active {
    border-bottom: none;
    border-left: 3px solid #007bff;
  }
}

.alert-symbol,
.alert-symbol2,
.alert-symbol3,
.alert-symbol4 {
  display: inline-block;
  width: 10px;
  height: 10px;
  border-radius: 50%;
  margin-left: 5px;
}

@media (min-width: 600px) {
  .alert-symbol {
    width: 10px;
    height: 10px;
  }
}

@media (min-width: 900px) {
  .alert-symbol {
    width: 12px;
    height: 12px;
  }
}

.amarillo {
  background-color: rgba(205, 202, 0, 0.915);
}

.rojo {
  background-color: rgba(255, 0, 0, 0.906);
}
</style>
