<template>
  <div>
    <h2>Información General </h2>
    <template v-if="idRol == 6 || idRol == 9 || idRol == 1">

      <!-- Formulario para subir archivo -->
      <form @submit.prevent="uploadFile" id="uploadForm" method="POST" enctype="multipart/form-data">
        <fieldset class="form-section">
          <legend>Actualización Masiva </legend>
          <p class="upload-instructions">
            La descarga de la plantilla en Excel está diseñada para realizar una <strong>carga masiva de
              Información General</strong> de múltiples empleados.
            Esto facilita subir los datos de todos los empleados en un solo archivo, agilizando el proceso de
            actualización.
          </p>
          <input type="file" @change="cargarExcel" accept=".xlsx, .xls" class="excel-upload" />
          <button type="submit">Subir Archivo</button>
          <!-- Botón para descargar plantilla CSV -->
          <button type="button" @click="descargarPlantillaExcel" class="btn-download">Descargar Plantilla</button>
        </fieldset>
      </form>

    </template>
    <form @submit.prevent="updateEmpleado" class="employee-form">

      <fieldset class="form-section">

        <legend>Información Personal</legend>
        <p class="module-description">
          En este módulo, podrás ingresar y actualizar la información personal del empleado. Asegúrate de que todos los
          datos sean correctos y completos, ya que se utilizarán para registros oficiales y comunicaciones dentro de la
          empresa.
        </p>
        <div class="form-grid">
          <div class="form-group">
            <input type="hidden" id="id" v-model="localEmpleadoData.id" />
            <label for="nombre">Nombre:<span class="required">*</span></label>
            <input type="text" id="nombre" v-model="localEmpleadoData.nombre" required />
            <span class="tooltipEmpleados">Ingresa tu primer nombre tal como aparece en tus documentos oficiales. Esto
              puede variar según la nacionalidad (por ejemplo, nombre de pila en culturas occidentales).</span>
          </div>
          <div class="form-group">
            <label for="paterno">Apellido Paterno:<span class="required">*</span></label>
            <input type="text" id="paterno" v-model="localEmpleadoData.paterno" required />
            <span class="tooltipEmpleados">Ingresa tu apellido familiar. En algunas culturas, este es el apellido que
              identifica tu linaje familiar.</span>
          </div>
          <div class="form-group">
            <label for="materno">Apellido Materno:</label>
            <input type="text" id="materno" v-model="localEmpleadoData.materno" />
            <span class="tooltipEmpleados">Ingresa cualquier apellido materno o nombres adicionales que puedas tener,
              los cuales pueden ser comunes en ciertas culturas.</span>
          </div>
          <div class="form-group">
            <label for="telefono">Teléfono:</label>
            <input type="text" id="telefono" v-model="localEmpleadoData.telefono" />
            <span class="tooltipEmpleados">Ingresa tu número de contacto principal. Asegúrate de incluir el código de
              país si aplica.</span>
          </div>
          <div class="form-group">
            <label for="correo">Correo Electrónico:</label>
            <input type="email" id="correo" v-model="localEmpleadoData.correo" />
            <span class="tooltipEmpleados">Ingresa una dirección de correo electrónico válida donde te puedan contactar.
              Esto es importante para la comunicación.</span>
          </div>
          <div class="form-group">
            <label for="departamento">Area/Departamento:</label>
            <input type="text" id="departamento" v-model="localEmpleadoData.departamento" />
            <span class="tooltipEmpleados">Introduce el título o puesto que ocupa el empleado dentro de la
              organización.</span>
          </div>
          <div class="form-group">
            <label for="puesto">Puesto:</label>
            <input type="text" id="puesto" v-model="localEmpleadoData.puesto" />
            <span class="tooltipEmpleados">Ingresa tu título o puesto dentro de la organización.</span>
          </div>
          <div class="form-group">
            <label for="edad">Fecha de Nacimiento:</label>
            <input type="date" id="edad" v-model="localEmpleadoData.fecha_nacimiento" />
            <span class="tooltipEmpleados">Selecciona tu fecha de nacimiento en el formato día, mes, año. Esto es
              esencial para la identificación.</span>
          </div>
          <div class="form-group">
            <label for="curp">CURP:</label>
            <input type="text" id="curp" v-model="localEmpleadoData.curp" />
            <span class="tooltipEmpleados">Ingresa tu CURP (Clave Única de Registro de Población) si eres de México.
              Esto te identifica de manera única en el sistema.</span>
          </div>
          <div class="form-group">
            <label for="nss">NSS:</label>
            <input type="text" id="nss" v-model="localEmpleadoData.nss" maxlength="20" />
            <span class="tooltipEmpleados">Ingresa tu NSS (Número de Seguro Social) si aplica. Esto es crucial para los
              servicios de seguridad social.</span>
          </div>
          <div class="form-group">
            <label for="rfc">RFC:</label>
            <input type="text" id="rfc" v-model="localEmpleadoData.rfc" maxlength="20" />
            <span class="tooltipEmpleados">Ingresa tu RFC (Registro Federal de Contribuyentes) si eres de México. Esto
              es importante para la identificación fiscal.</span>
          </div>
          <div class="form-group">
            <label for="id">ID de Empleado:</label>
            <input type="text" id="id" v-model="localEmpleadoData.id_empleado" />
            <span class="tooltipEmpleados">Ingresa tu número único de identificación de empleado proporcionado por tu
              organización.</span>
          </div>
          <div class="form-group">
            <label for="creacion">Fecha de Ingreso:</label>
            <input type="date" id="creacion" v-model="formattedDate" />
            <span class="tooltipEmpleados">Selecciona la fecha de ingreso del empleado.</span>
          </div>
        </div>
      </fieldset>


      <fieldset class="form-section">
        <legend>Dirección</legend>
        <div class="form-grid">
          <div class="form-group">
            <label for="calle">Calle:</label>
            <input type="hidden" id="id_domicilio" v-model="localEmpleadoData.domicilio_empleado.id" />
            <input type="text" id="calle" v-model="localEmpleadoData.domicilio_empleado.calle" placeholder="Calle" />
            <span class="tooltipEmpleados">Ingresa el nombre de tu calle. Esta es la línea principal de tu dirección de
              residencia.</span>
          </div>
          <div class="form-group">
            <label for="num_ext">Número Exterior:</label>
            <input type="text" id="num_ext" v-model="localEmpleadoData.domicilio_empleado.num_ext"
              placeholder="Número Exterior" />
            <span class="tooltipEmpleados">Ingresa el número exterior de tu residencia, si aplica. Puede ser el número
              de casa o número de apartamento.</span>
          </div>
          <div class="form-group">
            <label for="num_int">Número Interior:</label>
            <input type="text" id="num_int" v-model="localEmpleadoData.domicilio_empleado.num_int"
              placeholder="Número Interior" />
            <span class="tooltipEmpleados">Ingresa el número interior si vives en un departamento o complejo.</span>
          </div>
          <div class="form-group">
            <label for="colonia">Colonia:</label>
            <input type="text" id="colonia" v-model="localEmpleadoData.domicilio_empleado.colonia"
              placeholder="Colonia" />
            <span class="tooltipEmpleados">Ingresa el nombre de tu colonia o localidad, que puede ser importante para
              los servicios de entrega.</span>
          </div>
          <div class="form-group">
            <label for="ciudad">Ciudad:</label>
            <input type="text" id="ciudad" v-model="localEmpleadoData.domicilio_empleado.ciudad" placeholder="Ciudad" />
            <span class="tooltipEmpleados">Ingresa el nombre de tu ciudad. Esto es esencial para determinar tu
              ubicación.</span>
          </div>
          <div class="form-group">
            <label for="estado">Estado:</label>
            <input type="text" id="estado" v-model="localEmpleadoData.domicilio_empleado.estado" placeholder="Estado" />
            <span class="tooltipEmpleados">Ingresa tu estado o provincia. Esto puede variar dependiendo de tu
              país.</span>
          </div>
          <div class="form-group">
            <label for="pais">País:</label>
            <input type="text" id="pais" v-model="localEmpleadoData.domicilio_empleado.pais" placeholder="País" />
            <span class="tooltipEmpleados">Ingresa tu país de residencia. Esto es importante para la identificación
              internacional.</span>
          </div>
          <div class="form-group">
            <label for="cp">Código Postal:</label>
            <input type="text" id="cp" v-model="localEmpleadoData.domicilio_empleado.cp" placeholder="Código Postal" />
            <span class="tooltipEmpleados">Ingresa el código postal de tu dirección. Esto ayuda a localizar con
              precisión tu residencia.</span>
          </div>
        </div>
      </fieldset>

      <fieldset class="form-section">
        <legend>Información Extra</legend>
        <div class="form-grid">
          <div class="form-group" v-for="(campo, index) in localEmpleadoData.campoExtra" :key="index">

            <!-- Nombre (etiqueta editable) -->
            <label v-if="!campo.editandoNombre" @dblclick="editarNombre(index)">
              {{ campo.nombre || 'Campo desconocido' }}:
            </label>
            <input v-else type="text" class="form-control" v-model="campo.nombre" @blur="guardarNombre(index)"
              @keyup.enter="guardarNombre(index)" placeholder="Ejemplo: RFC, CURP, Número de empleado" />

            <!-- Contenedor para valor y botón eliminar -->
            <input v-if="!campo.editandoValor" type="text" class="form-control" :id="'campo-' + index"
              :value="campo.valor" @dblclick="editarValor(index)" readonly />
            <input v-else type="text" class="form-control" v-model="campo.valor" @blur="guardarValor(index)"
              @keyup.enter="guardarValor(index)" :placeholder="'Ingrese la informacion  para este  campo'" />
            <br>
            <template v-if="idRol == 6 || idRol == 9 || idRol == 1">

              <button type="button" class="delete-button" @click="eliminarCampoExtra(index)" title="Eliminar campo">
                <i class="fas fa-trash-alt"></i>
              </button>
            </template>
            <span class="tooltipEmpleados">
              Aquí puedes agregar un campo adicional personalizado. Ingresa el título del campo (por ejemplo: CURP, RFC,
              Número de empleado) y su valor correspondiente.
            </span>
          </div>
        </div>

        <!-- Botón agregar -->
        <div class="form-group">
              <template v-if="idRol == 6 || idRol == 9 || idRol == 1">

          <button type="button" class="add-button" @click="agregarCampoExtra">
            + Agregar campo extra
          </button>
          </template>
        </div>
      </fieldset>

      <div class="button-container">
        <template v-if="idRol == 6 || idRol == 9 || idRol == 1 || idRol == 10">

          <button type="submit" class="update-button">Actualizar</button>
        </template>
        <button class="close-button " type="button" @click="handleCloseModal">Cerrar</button>
      </div>
    </form>
  </div>
</template>

<script>
import axios from 'axios';
import Swal from 'sweetalert2';
import moment from 'moment-timezone'; // Importa moment y moment-timezone

export default {
  name: 'GeneralInfoForm',
  props: {
    empleadoData: {
      type: Object,
      required: true
    },
    idRol: {  // Añadido aquí
      type: String,
      default: ''
    },
    idCliente: {
      type: String,
      default: ''
    },
  },
  data() {
    return {
      localEmpleadoData: JSON.parse(JSON.stringify(this.empleadoData)), // Crea una copia profunda
      excelFile: null,
    };
  },
  computed: {
    formattedDate: {
      get() {
        // Si la fecha existe, transforma al formato YYYY-MM-DD
        return this.localEmpleadoData.creacion
          ? this.localEmpleadoData.creacion.split(" ")[0]
          : "";
      },
      set(value) {
        // Al actualizar el input, actualiza `localEmpleadoData.edicion`
        this.localEmpleadoData.creacion = value + " 00:00:00";
      },
    },
    camposExtra() {
      return Array.isArray(this.localEmpleadoData.campoExtra)
        ? this.localEmpleadoData.campoExtra
        : [];
    },
    hasCampoExtra() {
      return this.camposExtra.length > 0;
    },
  },
  methods: {
    cargarExcel(event) {
      this.excelFile = event.target.files[0];
      console.log('Archivo seleccionado:', this.excelFile);
    },

    async uploadFile() {
      if (!this.excelFile) {
        Swal.fire({
          icon: 'warning',
          title: 'Archivo no seleccionado',
          text: 'Por favor selecciona un archivo antes de subir.'
        });
        return;
      }

      const formData = new FormData();
      formData.append('file', this.excelFile);
      formData.append('id_rol', this.idRol);
      formData.append('id_cliente', this.idCliente);

      try {
        const response = await axios.post(`${this.$apiUrl}/upload-general-info`, formData, {
          headers: {
            'Content-Type': 'multipart/form-data'
          }
        });

        Swal.fire({
          icon: 'success',
          title: 'Éxito',
          text: response.data.message || "Archivo subido y datos actualizados con éxito."
        }).then(() => {
          // Emitir evento solo después de que el usuario acepte el mensaje
          this.handleCloseModal(); // Cerrar modal
        });


      } catch (error) {
        console.error(error);
        Swal.fire({
          icon: 'error',
          title: 'Error',
          text: error.response?.data?.error || "Hubo un error al subir el archivo."
        });
      }
    },
    descargarPlantillaExcel() {
      const empleadoId = this.empleadoData?.id;

      if (!empleadoId) {
        Swal.fire({
          icon: 'error',
          title: 'Error',
          text: 'ID de empleado no disponible.'
        });
        return;
      }

      axios.get(`${this.$apiUrl}/download-template-general`, {
        params: { empleado_id: empleadoId },
        responseType: 'blob',
      })
        .then((response) => {
          const url = window.URL.createObjectURL(new Blob([response.data]));
          const link = document.createElement('a');
          link.href = url;
          link.setAttribute('download', 'Informacion_General.xlsx');
          document.body.appendChild(link);
          link.click();
          document.body.removeChild(link);
        })
        .catch((error) => {
          console.error('Error al descargar el archivo CSV:', error);
          Swal.fire({
            icon: 'error',
            title: 'Descarga fallida',
            text: 'No se pudo descargar la plantilla.'
          });
        });
    },
    async updateEmpleado() {
      const filteredData = Object.fromEntries(
        Object.entries(this.localEmpleadoData).filter(([key, value]) =>
          key !== 'status' && value !== '' && value !== null
        )
      );
      console.log('Datos filtrados:', filteredData);


      // Establecer la fecha de edición con la fecha y hora actual en la zona horaria de México
      filteredData.edicion = moment.tz('America/Mexico_City').format('YYYY-MM-DD HH:mm:ss');

      try {
        const apiUrl = `${this.$apiUrl}/empleados/update`;


        if (!this.$csrfToken) {
          console.error('CSRF token no encontrado');
          return; // Evita enviar la solicitud si el token no está presente
        }

        const response = await axios.put(apiUrl, filteredData, {
          headers: {
            'X-CSRF-TOKEN': this.$csrfToken
          }
        });

        console.log('Respuesta de la API:', response); // Ver respuesta completa

        // Mostrar SweetAlert de éxito
        Swal.fire({
          icon: 'success',
          title: 'Actualización exitosa',
          text: 'Los datos del empleado se han actualizado correctamente.',
          confirmButtonText: 'Aceptar'
        }).then(() => {
          // Emitir evento solo después de que el usuario acepte el mensaje
          this.$emit('empleadoActualizado', filteredData);
          this.handleCloseModal(); // Cerrar modal
        });
      } catch (error) {
        let errorMessage = 'Ocurrió un error al actualizar el empleado.';

        if (error.response) {
          errorMessage = error.response.data.error || 'Error desconocido';
          console.error('Error al actualizar el empleado:', error.response.data);
        } else if (error.request) {
          console.error('No se recibió respuesta del servidor:', error.request);
          errorMessage = 'No se recibió respuesta del servidor.';
        } else {
          console.error('Error en la configuración de la solicitud:', error.message);
          errorMessage = error.message;
        }

        // Mostrar SweetAlert de error
        Swal.fire({
          icon: 'error',
          title: 'Error',
          text: errorMessage,
          confirmButtonText: 'Aceptar'
        });
      }
    },

    handleCloseModal() {
      this.$emit('close'); // Emite el evento para cerrar el modal
    },
    formatLabel(key) {
      if (typeof key !== 'string') return 'Campo desconocido';

      return key
        .replace(/_/g, ' ')
        .replace(/([A-Z])/g, ' $1')
        .replace(/^./, str => str.toUpperCase());
    },
    editarNombre(index) {
      this.localEmpleadoData.campoExtra[index].editandoNombre = true;
    },

    // Guardar el nombre cuando se pierde el foco o se presiona Enter
    guardarNombre(index) {
      this.localEmpleadoData.campoExtra[index].editandoNombre = false;
    },

    // Hacer editable el valor del campo
    editarValor(index) {
      this.localEmpleadoData.campoExtra[index].editandoValor = true;
    },

    // Guardar el valor cuando se pierde el foco o se presiona Enter
    guardarValor(index) {
      this.localEmpleadoData.campoExtra[index].editandoValor = false;
    },

    // Agregar un nuevo campo extra
    agregarCampoExtra() {
      if (!Array.isArray(this.localEmpleadoData.campoExtra)) {
        this.localEmpleadoData.campoExtra = [];
      }

      this.localEmpleadoData.campoExtra.push({
        nombre: '',
        valor: '',
        editandoNombre: true, // Inicia en modo edición
        editandoValor: true
      });
    },
    async eliminarCampoExtra(index) {
      const campo = this.camposExtra[index];

      // Confirmar con SweetAlert
      const resultado = await Swal.fire({
        title: '¿Estás seguro?',
        text: 'Este campo extra será eliminado permanentemente.',
        icon: 'warning',
        showCancelButton: true,
        confirmButtonText: 'Sí, eliminar',
        cancelButtonText: 'Cancelar'
      });

      if (resultado.isConfirmed) {
        try {
          // Verifica si el campo tiene ID (campo persistente en BD)
          if (campo.id) {
            const apiUrl = `${this.$apiUrl}/empleados/campo-extra/${campo.id}`;
            await axios.delete(apiUrl, {
              headers: {
                'X-CSRF-TOKEN': this.$csrfToken
              }
            });
          }

          // Eliminar del arreglo local
          this.localEmpleadoData.campoExtra.splice(index, 1);

          Swal.fire({
            icon: 'success',
            title: 'Eliminado',
            text: 'El campo extra ha sido eliminado correctamente.',
            timer: 1000, // tiempo en milisegundos (2 segundos)
            showConfirmButton: false
          });

        } catch (error) {
          console.error('Error al eliminar campo extra:', error);
          Swal.fire({
            icon: 'error',
            title: 'Error',
            text: 'Ocurrió un error al eliminar el campo extra intentalo de nuevo mas tarde.'
          });
        }
      }
    }
  },
  watch: {
    empleadoData: {
      handler(newValue) {
        const processedCampoExtra = Array.isArray(newValue.campoExtra)
          ? newValue.campoExtra.map(campo => ({
            ...campo,
            editandoNombre: campo.editandoNombre ?? false,
            editandoValor: campo.editandoValor ?? false
          }))
          : [];

        this.localEmpleadoData = {
          ...JSON.parse(JSON.stringify(newValue)),
          campoExtra: processedCampoExtra
        };

       // console.log('📦 Datos recibidos en empleadoData:', newValue);
      },
      deep: true,
      immediate: true // ← Esto ayuda si el componente ya se monta con datos
    }
  }
};
</script>


