<template>
  <div v-if="isVisible" class="profile-modal-overlay">
    <div class="profile-modal-content">
      <span class="close" @click="closeModal">&times;</span>
      <h2>Registro de Empleados</h2>
      <!-- Mensaje adicional para indicar limitaciones de carga -->

      <!-- Formulario para subir archivo -->
      <form @submit.prevent="uploadFile" id="uploadForm" method="POST" enctype="multipart/form-data">
        <legend>Registro Masivo de Empleados</legend>
        <p class="upload-instructions">
          Por favor, sube un archivo con datos válidos de los empleados. Los correos electrónicos duplicados impedirán
          que el archivo se procese correctamente.
        </p>
        <input type="file" ref="fileInput" accept=".xlsx, .xls" />
        <button type="submit">Subir Archivo</button>

        <!-- Botón para descargar plantilla CSV -->
        <button type="button" @click="downloadCsv" class="btn-download">Descargar Plantilla CSV</button>
      </form>

      <form @submit.prevent="registerEmpleado" class="employee-form">

        <fieldset class="form-section">
          <legend>Registrar un Empleado</legend>
          <legend>Información Personal</legend>
          <p class="upload-limitations">
            Los únicos campos obligatorios son el Nombre, Apellido y Número de Teléfono. Los demás campos son opcionales
            y pueden completarse más adelante.
          </p>
          <div class="form-grid">
            <div class="form-group">
              <label for="nombre">Nombre:<span class="required">*</span></label>
              <input type="text" id="nombre" v-model="localEmpleadoData.nombre" required @input="updateNombre" />
              <span class="tooltipRegistroE">Introduce tu primer nombre tal como aparece en los documentos
                oficiales.</span>
            </div>
            <div class="form-group">
              <label for="paterno">Apellido Paterno:<span class="required">*</span></label>
              <input type="text" id="paterno" v-model="localEmpleadoData.paterno" required @input="updatePaterno" />
              <span class="tooltipRegistroE">Introduce tu apellido paterno (primer apellido).</span>
            </div>
            <div class="form-group">
              <label for="materno">Apellido Materno (si aplica):</label>
              <input type="text" id="materno" v-model="localEmpleadoData.materno" @input="updateMaterno" />
              <span class="tooltipRegistroE">Introduce tu apellido materno si es necesario (solo en países que lo
                utilicen como parte del nombre completo).</span>
            </div>
            <div class="form-group">
              <label for="telefono">Teléfono:</label>
              <input type="text" id="telefono" v-model="localEmpleadoData.telefono" />
              <span class="tooltipRegistroE">Introduce tu número de teléfono principal (incluyendo código de área).
                Puede ser un teléfono móvil o fijo.</span>
            </div>
            <div class="form-group">
              <label for="correo">Correo Electrónico:</label>
              <input type="email" id="correo" v-model="localEmpleadoData.correo" />
              <span class="tooltipRegistroE">Introduce una dirección de correo electrónico válida. Este correo se
                utilizará para notificaciones importantes.</span>
            </div>
            <div class="form-group">
              <label for="departamento">Area/Departamento:</label>
              <input type="text" id="departamento" v-model="localEmpleadoData.departamento" />
              <span class="tooltipRegistroE">Introduce el título o puesto que ocupa el empleado dentro de la
                organización.</span>
            </div>

            <div class="form-group">
              <label for="fecha_nacimiento">Fecha de Nacimiento:</label>
              <input type="date" id="fecha_nacimiento" v-model="localEmpleadoData.fecha_nacimiento" />
              <span class="tooltipRegistroE">Selecciona la fecha en que naciste. Esto es necesario para verificar la
                edad del empleado.</span>
            </div>
            <div class="form-group">
              <label for="curp">CURP (si aplica):</label>
              <input type="text" id="curp" v-model="localEmpleadoData.curp" />
              <span class="tooltipRegistroE">Introduce tu CURP si eres ciudadano mexicano. El CURP es una clave única de
                registro de población en México.</span>
            </div>
            <div class="form-group">
              <label for="nss">NSS (si aplica):</label>
              <input type="text" id="nss" v-model="localEmpleadoData.nss" maxlength="11" />
              <span class="tooltipRegistroE">Introduce tu número de seguridad social (NSS) si aplica, especialmente en
                México.</span>
            </div>
            <div class="form-group">
              <label for="rfc">RFC (si aplica):</label>
              <input type="text" id="rfc" v-model="localEmpleadoData.rfc" maxlength="13" />
              <span class="tooltipRegistroE">Introduce tu RFC si eres de México. El RFC es una clave fiscal utilizada
                por el gobierno mexicano.</span>
            </div>
            <div class="form-group">
              <label for="id_empleado">ID del Empleado:</label>
              <input type="number" id="id_empleado" v-model="localEmpleadoData.id_empleado" />
              <span class="tooltipRegistroE">Introduce el ID único del empleado. Esto es necesario para identificar de
                manera precisa al empleado en el sistema.</span>
            </div>
            <div class="form-group">
              <label for="puesto">Puesto:</label>
              <input type="text" id="puesto" v-model="localEmpleadoData.puesto" />
              <span class="tooltipRegistroE">Introduce el título o puesto que ocupa el empleado dentro de la
                organización.</span>
            </div>
          </div>
        </fieldset>

        <fieldset class="form-section">
          <legend>Dirección</legend>
          <div class="form-grid">
            <div class="form-group">
              <label for="calle">Calle:</label>
              <input type="text" id="calle" v-model="localEmpleadoData.domicilio_empleado.calle" />
              <span class="tooltipRegistroE">Por favor, ingrese el nombre de su calle o carretera.</span>
            </div>
            <div class="form-group">
              <label for="num_ext">Número Exterior:</label>
              <input type="text" id="num_ext" v-model="localEmpleadoData.domicilio_empleado.num_ext" />
              <span class="tooltipRegistroE">Ingrese el número exterior de su dirección, generalmente el número
                principal de la casa o edificio.</span>
            </div>
            <div class="form-group">
              <label for="num_int">Número Interior:</label>
              <input type="text" id="num_int" v-model="localEmpleadoData.domicilio_empleado.num_int" />
              <span class="tooltipRegistroE">Si aplica, ingrese el número interior, como el número de departamento o
                unidad.</span>
            </div>
            <div class="form-group">
              <label for="colonia">Colonia:</label>
              <input type="text" id="colonia" v-model="localEmpleadoData.domicilio_empleado.colonia" />
              <span class="tooltipRegistroE">Ingrese el nombre de la colonia o barrio en el que vive.</span>
            </div>
            <div class="form-group">
              <label for="ciudad">Ciudad:</label>
              <input type="text" id="ciudad" v-model="localEmpleadoData.domicilio_empleado.ciudad" />
              <span class="tooltipRegistroE">Ingrese el nombre de la ciudad en la que reside.</span>
            </div>
            <div class="form-group">
              <label for="estado">Estado:</label>
              <input type="text" id="estado" v-model="localEmpleadoData.domicilio_empleado.estado" />
              <span class="tooltipRegistroE">Ingrese el nombre del estado o provincia donde vive.</span>
            </div>
            <div class="form-group">
              <label for="pais">País:</label>
              <input type="text" id="pais" v-model="localEmpleadoData.domicilio_empleado.pais" />
              <span class="tooltipRegistroE">Ingrese el país en el que reside.</span>
            </div>
            <div class="form-group">
              <label for="cp">Código Postal:</label>
              <input type="text" id="cp" v-model="localEmpleadoData.domicilio_empleado.cp" />
              <span class="tooltipRegistroE">Ingrese el código postal de su área de residencia.</span>
            </div>
          </div>
        </fieldset>
        <fieldset class="form-section">
          <legend>Información Extra</legend>
          <div class="form-grid">
            <div v-for="(field, index) in localEmpleadoData.extraFields" :key="index" >
              <!-- Etiqueta editable -->
              <div class="form-group" >
                <label v-if="!field.editando" @dblclick="activarEdicion(index)"
                :title="'Haz doble clic para editar el nombre del campo (por ejemplo: CURP, RFC, etc.)'">
                  {{ field.label || 'Haz doble clic para editar' }}
                </label>
                <input v-else type="text" v-model="field.label" @blur="guardar(index)" @keyup.enter="guardar(index)"
                  placeholder="Ejemplo: RFC, CURP, Número de empleado" :ref="'inputCampo-' + index" />
                   <span class="tooltipRegistroE">Escribe el Titulo del campo. Presiona Enter o haz clic fuera para guardar (por ejemplo: CURP, RFC, etc.).</span>
              </div>
              <div class="form-group">
              <!-- Valor -->
              <input type="text" v-model="field.value"
                :placeholder="'Ingrese el valor correspondiente para: ' + (field.label || 'este campo')"
                :title="'Escribe el dato correspondiente al campo ' + (field.label || 'extra')" />
                 <span class="tooltipRegistroE">Escribe el dato correspondiente al campo  {{ (field.label || 'campo extra') }} </span>
                </div>
              <!-- Eliminar -->
              <br />
              <button type="button" class="delete-button" @click="removeExtraField(index)"
                title="Haz clic para eliminar este campo adicional">
                <i class="fas fa-trash-alt"></i>
              </button>
            </div>
          </div>

          <!-- Botón para agregar campo -->
          <div class="form-group" style="margin-top: 10px;">
            <button type="button" class="add-button" @click="addExtraField">
              + Agregar campo
            </button>
          </div>
        </fieldset>



        <div class="button-container">
          <button type="submit" class="update-button">Registrar</button>
          <button class="close-button" type="button" @click="closeModal">
            Cerrar
          </button>
        </div>
      </form>
    </div>
  </div>
</template>

<script>
import axios from 'axios';
import Swal from 'sweetalert2';
import moment from 'moment-timezone';

export default {
  props: {
    idPortal: {
      type: String,
      default: ''
    },
    idUsuario: {
      type: String,
      default: ''
    },
    idCliente: {
      type: String,
      default: ''
    },
  },
  data() {
    return {
      isUploading: false,
      isVisible: false,
      uploadStatus: '',
      uploadStatusClass: '',
      localEmpleadoData: {
        nombre: '',
        paterno: '',
        materno: '',
        telefono: '',
        correo: '',
        departamento: '',
        puesto: '',
        fecha_nacimiento: '',
        curp: '',
        nss: '',
        rfc: '',
        id_empleado: '',
        extraFields: [],
        domicilio_empleado: {
          calle: '',
          num_ext: '',
          num_int: '',
          colonia: '',
          ciudad: '',
          estado: '',
          pais: '',
          cp: ''
        }
      }
    };
  },

  methods: {
    activarEdicion(index) {
      this.localEmpleadoData.extraFields[index].editando = true;
      this.$nextTick(() => {
        this.$refs['inputCampo-' + index]?.focus();
      });
    },
    guardar(index) {
      const campo = this.localEmpleadoData.extraFields[index];
      if (campo.label.trim() !== '') {
        campo.editando = false;
      } else {
        this.$nextTick(() => {
          this.$refs['inputCampo-' + index]?.focus();
        });
      }
    },
    addExtraField() {
      this.localEmpleadoData.extraFields.push({
        label: '',
        value: '',
        editando: true // cada campo tiene su propio estado de edición
      });
    },
    removeExtraField(index) {
      this.localEmpleadoData.extraFields.splice(index, 1);
    },
    updateNombre() {
      this.localEmpleadoData.nombre = this.localEmpleadoData.nombre.toUpperCase();
    },
    updatePaterno() {
      this.localEmpleadoData.paterno = this.localEmpleadoData.paterno.toUpperCase();
    },
    updateMaterno() {
      this.localEmpleadoData.materno = this.localEmpleadoData.materno.toUpperCase();
    },
    toUpperCase(value) {
      return value ? value.toUpperCase() : '';
    },
    openModal() {
      this.isVisible = true;
    },
    closeModal() {
      this.isVisible = false;
      this.resetForm();
      this.$emit('modalClosed');  // Reset form on close
    },
    resetForm() {
      this.localEmpleadoData = {
        nombre: '',
        paterno: '',
        materno: '',
        telefono: '',
        correo: '',
        puesto: '',
        departamento: '',
        fecha_nacimiento: '',
        curp: '',
        nss: '',
        rfc: '',
        id_empleado: '',
        extraFields: [],
        domicilio_empleado: {
          calle: '',
          num_ext: '',
          num_int: '',
          colonia: '',
          ciudad: '',
          estado: '',
          pais: '',
          cp: ''
        }
      };
    },
    async checkEmailExists(email) {
      try {
        const response = await axios.get(`${this.$apiUrl}/empleados/check-email`, {
          params: { correo: email }
        });
        return response.data.exists; // Asumiendo que el backend devuelve { exists: true/false }
      } catch (error) {
        console.error('Error checking email:', error);
        return false; // Si hay un error, asumimos que el correo no existe
      }
    },

    async registerEmpleado() {
      try {

        if (this.localEmpleadoData.correo != null) {
          const emailExists = await this.checkEmailExists(this.localEmpleadoData.correo);
          if (emailExists) {
            Swal.fire({
              icon: 'error',
              title: 'Error',
              text: 'El correo ya esta  registrado en otro empleado.',
              confirmButtonText: 'Aceptar'
            });
            return; // Detener el proceso de registro
          }
        }
        // Filtra los datos para obtener solo los campos llenos
        const filteredData = Object.fromEntries(
          Object.entries(this.localEmpleadoData).filter(([key, value]) => {
            // Incluir 'domicilio_empleado' si tiene al menos un campo con valor
            if (key === 'domicilio_empleado') {
              return Object.values(value).some(val => val.trim() !== '');
            }
            if (key === 'extraFields') {
              return Array.isArray(value) && value.some(field =>
                field &&
                typeof field.label === 'string' &&
                typeof field.value === 'string' &&
                field.label.trim() !== '' &&
                field.value.trim() !== ''
              );
            }
            // Incluir cualquier otro campo que tenga un valor no vacío
            return value && value.trim() !== '';
          })
        );

        // Asegúrate de incluir 'domicilio_empleado' incluso si todos sus campos están vacíos
        if (this.localEmpleadoData.domicilio_empleado && Object.keys(this.localEmpleadoData.domicilio_empleado).length > 0) {
          filteredData.domicilio_empleado = this.localEmpleadoData.domicilio_empleado;
        }
        // Incluir extraFields tal como están
        if (Array.isArray(this.localEmpleadoData.extraFields)) {
          filteredData.extraFields = this.localEmpleadoData.extraFields.filter(field =>
            field &&
            typeof field === 'object' &&
            typeof field.label === 'string' &&
            typeof field.value === 'string' &&
            field.label.trim() !== '' &&
            field.value.trim() !== ''
          );
        }

        if (Array.isArray(this.localEmpleadoData.extraFields)) {
          filteredData.extraFields = this.localEmpleadoData.extraFields.map(field => {
            return {
              nombre: field.label.trim(), // Renombrado
              valor: field.value.trim()    // Renombrado
            };
          });
        }
        // Agregar información adicional
        const timestamp = moment.tz('America/Mexico_City').format('YYYY-MM-DD HH:mm:ss');
        filteredData.creacion = timestamp;
        filteredData.edicion = timestamp;
        filteredData.id_portal = this.idPortal;
        filteredData.id_usuario = this.idUsuario;
        filteredData.id_cliente = this.idCliente;

        const enModoPruebas = false; // cambia a false para modo real

        if (enModoPruebas) {
          console.log("🚀 ~ filteredData:", filteredData);
          return;
        }

        const apiUrl = `${this.$apiUrl}/empleados/register`;
        if (!this.$csrfToken) {
          console.error('CSRF token no encontrado');
          return; // Evita enviar la solicitud si el token no está presente
        }

        const response = await axios.post(apiUrl, filteredData, {
          headers: {
            'X-CSRF-TOKEN': this.$csrfToken
          }
        });

        console.log('Respuesta de la API:', response); // Ver respuesta completa

        // Mostrar SweetAlert de éxito
        Swal.fire({
          icon: 'success',
          title: 'Actualizacion exitosa',
          text: 'Los  datos  del empleado se actualizaron correctamente.',
          confirmButtonText: 'Aceptar'
        }).then(() => {
          this.$emit('empleadoActualizado', filteredData);
          this.closeModal(); // Cerrar modal
        });
      } catch (error) {
        let errorMessage = 'Ocurrió un error al registrar al empleado.';

        if (error.response) {
          const response = error.response;

          if (response.status === 422 && response.data.errors) {
            // Laravel devuelve errores de validación aquí
            const errores = response.data.errors;
            errorMessage = Object.values(errores).flat().join('\n');
          } else if (response.status === 409) {
            // Error personalizado: empleado duplicado
            errorMessage = response.data.message || 'El empleado ya existe.';
          } else if (response.status === 500) {
            // Error interno del servidor
            errorMessage = response.data.message || 'Error interno del servidor.';
          } else if (response.data.message) {
            errorMessage = response.data.message;
          }

          console.error('Error al registrar empleado:', response.data);
        } else if (error.request) {
          console.error('No se recibió respuesta del servidor:', error.request);
          errorMessage = 'No se recibió respuesta del servidor.';
        } else {
          console.error('Error en la configuración de la solicitud:', error.message);
          errorMessage = error.message;
        }

        Swal.fire({
          icon: 'error',
          title: 'Error',
          text: errorMessage,
          confirmButtonText: 'Aceptar'
        });
      }
    },
    downloadCsv() {
      // Realizar la solicitud GET al backend de Laravel usando axios
      axios.get(`${this.$apiUrl}/download-template`, {
        responseType: 'blob',  // Especificar que la respuesta es un archivo binario (CSV)
      })
        .then((response) => {
          // Crear un enlace para descargar el archivo
          const url = window.URL.createObjectURL(new Blob([response.data]));
          const link = document.createElement('a');
          link.href = url;
          link.setAttribute('download', 'plantilla_carga_masiva.xlsx'); // Nombre del archivo CSV a descargar
          document.body.appendChild(link);
          link.click(); // Simular clic para iniciar la descarga
          document.body.removeChild(link); // Eliminar el enlace después de la descarga
        })
        .catch((error) => {
          console.error('Error al descargar el archivo CSV:', error);
        });
    },
    async uploadFile() {
      const formData = new FormData();
      const fileInput = this.$refs.fileInput;
      if (this.isUploading) {
        return;  // Si ya se está subiendo, no hacer nada
      }
      this.isUploading = true;
      if (!fileInput.files[0]) {
        // Muestra un error si no se seleccionó archivo
        Swal.fire({
          icon: 'error',
          title: 'Sin archivo seleccionado',
          text: 'No se ha seleccionado un archivo. Por favor, elige uno válido.',
        });
        this.isUploading = false;
        return;
      }
      const timestamp = moment.tz('America/Mexico_City').format('YYYY-MM-DD HH:mm:ss');


      formData.append('file', fileInput.files[0]);

      // Agregar datos generales al FormData

      formData.append('creacion', timestamp);
      formData.append('edicion', timestamp);
      formData.append('id_portal', this.idPortal);
      formData.append('id_usuario', this.idUsuario);
      formData.append('id_cliente', this.idCliente);
      for (let pair of formData.entries()) {
        console.log(`${pair[0]}: ${pair[1]}`);
      }
      try {
        // Muestra un mensaje de "cargando"
        Swal.fire({
          title: 'Cargando empleados...',
          text: 'Estamos obteniendo los datos. Esto puede tardar un momento, gracias por tu paciencia.',
          showConfirmButton: false,
          allowOutsideClick: false,
          didOpen: () => {
            Swal.showLoading();
          }
        });
        const apiUrlImport = `${this.$apiUrl}/empleados/importar`;
        // Enviar el archivo y los datos generales al servidor
        const response = await axios.post(apiUrlImport, formData, {
          headers: {
            'Content-Type': 'multipart/form-data',
            'X-CSRF-TOKEN': this.$csrfToken
          },

        });

        // Cierra el Swal y muestra el mensaje de éxito
        const duplicados = response.data.duplicados || [];
        const totalDuplicados = response.data.total_duplicados || 0;
        const mensaje = response.data.message || 'Empleados importados correctamente';

        Swal.fire({
          icon: 'success',
          title: 'Importación exitosa',
          html: `
    <p>${mensaje}</p>
    ${totalDuplicados > 0
              ? `<p><strong>${totalDuplicados}</strong> empleados ya existían y no fueron agregados.</p>`
              : ''}`,
          confirmButtonText: 'Aceptar'
        }).then(() => {
          // Mostrar lista detallada si hay duplicados
          if (duplicados.length > 0) {
            const lista = duplicados
              .map(emp => `<li>${emp.nombre} ${emp.paterno} ${emp.correo ? `- ${emp.correo}` : ''}</li>`)
              .join('');

            Swal.fire({
              icon: 'info',
              title: 'Empleados duplicados',
              html: `<ul style="text-align: left;">${lista}</ul>`,
              confirmButtonText: 'Aceptar',
              width: 600
            });
          }
        });
      } catch (error) {
        // En caso de error, muestra el mensaje correspondiente
        let errorMessage = 'An error occurred while uploading the file.';

        if (error.response) {
          console.error('Response Data:', error.response.data);
          console.error('Response Status:', error.response.status);
          console.error('Response Headers:', error.response.headers);
        } else if (error.request) {
          console.error('Request Data:', error.request);
        } else {
          console.error('Error Message:', error.message);
        }

        Swal.fire({
          icon: 'error',
          title: 'Upload Failed',
          text: errorMessage
        });
      } finally {
        this.isUploading = false;  // Restablecer el estado cuando la carga termine
      }
    },
  }
};
</script>


<style scoped>
.extra-info {
  margin-top: 20px;
  padding: 10px;
  border: 1px solid #ccc;
}

.extra-field {
  display: flex;
  gap: 10px;
  margin-bottom: 10px;
}

.add-button {
  background-color: #d9edf7;
  border: 1px solid #31708f;
  color: #31708f;
  padding: 6px 12px;
  border-radius: 4px;
  cursor: pointer;
  font-weight: bold;
  margin-top: 20px;
}

.delete-button {
  background: transparent;
  border: none;
  cursor: pointer;
  font-size: 18px;
  color: #c00 !important;
  padding: 0;
  line-height: 1;
  opacity: 1 !important;
  filter: none !important;
  transition: color 0.2s;
}

.delete-button:hover {
  color: #a00 !important;
}

.tooltipRegistroE {
  display: none;
  position: absolute;
  background-color: #4c6fa6a7;
  color: #fff;
  padding: 5px;
  border-radius: 10px;
  z-index: 10;
  bottom: 100%;
  /* Position above the label */
  left: 0;
  /* Align with the left edge */
  margin-bottom: 5px;
