<template>
  <div class="modal" v-if="show" @click="closeModal">
    <div class="modal-content" @click.stop>
      <h2 class="employee-name">{{ empleados.nombre }} {{ empleados.paterno }} {{ empleados.materno }}</h2>

      <!-- Leyenda -->
      <p class="info-text">Aquí puedes ver los detalles de los cursos tomados por el empleado.</p>

      <div class="header">
        <input type="text" class="search" placeholder="Search by course name or date" v-model="searchQuery" />
      </div>

      <div class="courses-table-wrapper">
        <table class="courses-table">
          <thead>
            <tr>
              <th>ID</th>
              <th>Nombre</th>
              <th>Tipo</th>
              <th>Fecha de Finalización</th>
              <th>Fecha de Expiración</th>
              <th>Detalles</th>
            </tr>
          </thead>
          <tbody>
            <tr v-if="filteredCourses.length === 0">
              <td colspan="6" class="no-courses">Aún no se han cargado los cursos para el empleado.</td>
            </tr>
            <tr v-for="(course, index) in filteredCourses" :key="index">
              <td>{{ course.id }}</td>
              <td style="position: relative;">
                {{ course.optionName }} {{ course.nameAlterno }}
                <span class="tooltipDocuments">{{ course.description }}</span>
              </td>
              <td>{{ course.origen === 1 ? 'Interno' : 'Externo' }}</td>
              <td>{{ course.upload_date }}</td>
              <td>{{ course.expiry_date }}</td>
              <td>
                <div class="modal-buttons">
                  <template v-if="course.name && course.name.includes('_sin_')">
                    <span class="warning-text">Sin evidencia</span>
                  </template>
                  <template v-else>
                    <button @click="openEvidence(course)" class="view-button">Ver</button>
                  </template>
                </div>
              </td>
            </tr>
          </tbody>
        </table>
      </div>

      <div class="modal-footer">
        <button class="close-button" @click="closeModal">Cerrar</button>
      </div>
    </div>
  </div>
</template>

<script>
import api from '@/services/apiEmpleados'; // Asegúrate de que la ruta sea correcta
import { EventBus } from '@/eventBus';

export default {
  props: {
    empleados: {
      type: Object,
      required: true
    },
    origen: { type: Number, required: true },

    show: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      courses: [],
      searchQuery: ''
    };
  },
  computed: {
    filteredCourses() {
      const query = this.searchQuery.toLowerCase();
      return this.courses.filter(course => {
        const courseType = course.origen === 1 ? 'Interno' : 'Externo'; // Convertir a cadena
        return (
          course.name.toLowerCase().includes(query) || // Filtrar por nombre
          course.creacion.includes(query) || // Filtrar por fecha de completación
          course.expiry_date.includes(query) || // Filtrar por fecha de expiración
          courseType.toLowerCase().includes(query) // Filtrar por tipo (Interno/Externo)
        );
      });
    }
  },
  methods: {

    fetchCursos() {
      const apiUrl = `${this.$apiUrl}/cursos/empleado`; // Ajusta la URL a la ruta correcta
      const employeeId = this.empleados.id; // Suponiendo que tienes el ID del empleado en `this.empleados.id`
      const origen = this.origen; // Suponiendo que tienes el origen en `this.empleados.origen`
      api.get(apiUrl, { params: { employee_id: employeeId, origen: origen } })
        .then(response => {
          const cursos = response.data.documentos; // 👈 accede correctamente al array

          if (Array.isArray(cursos)) {
            this.courses = cursos.map(course => ({
              ...course,
              estado: course.status == 2 ? 'amarillo' : (course.status == 3 ? 'rojo' : '')
            }));

            console.log("🚀 ~ fetchDocuments ~ this.courses:", this.courses);

          } else {
            console.log("🚀 ~no hay cursos ");

          }
        })
        .catch(error => {
          console.error('Error fetching cursos:', error);
        });
    },
    closeModal() {
      this.$emit('close');
    },
    openEvidence(course) {
      EventBus.$emit('open-document', course, '_cursos');
    },

    getRandomDate(daysOffset = 0) {
      const today = new Date();
      today.setDate(today.getDate() - Math.floor(Math.random() * 30)); // Genera una fecha aleatoria en el pasado
      const randomDate = new Date(today);
      if (daysOffset) {
        randomDate.setDate(randomDate.getDate() + daysOffset);
      }
      return randomDate.toISOString().split('T')[0]; // Formato YYYY-MM-DD
    }
  },
  mounted() {
    this.fetchCursos(); // Genera cursos al montar el componente
  }
};
</script>

