<template>
  <div>
    <!-- Etiqueta -->
    <label for="lada">Lada:</label>
    <div class="custom-select" @click="toggleDropdown">
      <!-- Opción seleccionada -->
      <div class="selected-option">
        <img :src="selectedOption.flag" width="20" height="15" style="margin-right: 8px;" />
        {{ selectedOption.name }} ({{ selectedOption.code }})
      </div>

      <!-- Menú desplegable -->
      <div v-if="dropdownVisible" class="dropdown">
        <!-- Barra de búsqueda -->
        <input
          type="text"
          placeholder="Buscar país..."
          v-model="searchQuery"
          class="search-input"
          @click.stop
        />

        <!-- Lista de opciones -->
        <ul class="options">
          <li
            v-for="(country, index) in filteredCountries"
            :key="index"
            @click.stop="selectCountry(country)"
          >
            <img :src="country.flag" width="20" height="15" style="margin-right: 8px;" />
            {{ country.name }} ({{ country.code }})
          </li>
          <!-- Mensaje cuando no hay resultados -->
          <li v-if="filteredCountries.length === 0" class="no-results">
            No se encontraron resultados
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    value: String, // v-model para sincronizar datos externos
    countries: {
      type: Array,
      default: () => [
        { name: 'Argentina', code: '+54', flag: 'https://flagcdn.com/w320/ar.png' },
        { name: 'México', code: '+52', flag: 'https://flagcdn.com/w320/mx.png' },
        { name: 'España', code: '+34', flag: 'https://flagcdn.com/w320/es.png' },
        { name: 'Estados Unidos', code: '+1', flag: 'https://flagcdn.com/w320/us.png' },
        { name: 'Brasil', code: '+55', flag: 'https://flagcdn.com/w320/br.png' },
        { name: 'Chile', code: '+56', flag: 'https://flagcdn.com/w320/cl.png' },
        { name: 'Colombia', code: '+57', flag: 'https://flagcdn.com/w320/co.png' },
        { name: 'Perú', code: '+51', flag: 'https://flagcdn.com/w320/pe.png' },
        { name: 'Francia', code: '+33', flag: 'https://flagcdn.com/w320/fr.png' },
        { name: 'Reino Unido', code: '+44', flag: 'https://flagcdn.com/w320/gb.png' },
        { name: 'Italia', code: '+39', flag: 'https://flagcdn.com/w320/it.png' },
        { name: 'Alemania', code: '+49', flag: 'https://flagcdn.com/w320/de.png' },
        { name: 'Canadá', code: '+1', flag: 'https://flagcdn.com/w320/ca.png' },
        { name: 'Australia', code: '+61', flag: 'https://flagcdn.com/w320/au.png' },
        { name: 'Japón', code: '+81', flag: 'https://flagcdn.com/w320/jp.png' },
        { name: 'India', code: '+91', flag: 'https://flagcdn.com/w320/in.png' },
        { name: 'China', code: '+86', flag: 'https://flagcdn.com/w320/cn.png' },
        { name: 'Panamá', code: '+507', flag: 'https://flagcdn.com/w320/pa.png' },
        { name: 'Venezuela', code: '+58', flag: 'https://flagcdn.com/w320/ve.png' },
      ],
    },
  },
  data() {
    return {
      dropdownVisible: false, // Estado del menú desplegable
      searchQuery: '', // Búsqueda de país
      selectedOption: this.countries.find(country => country.code === this.value) || this.countries[1],
    };
  },
  computed: {
    // Filtra países en base a la búsqueda
    filteredCountries() {
      return this.countries.filter((country) =>
        country.name.toLowerCase().includes(this.searchQuery.toLowerCase())
      );
    },
  },
  watch: {
    selectedOption(newValue) {
      // Emitir el valor seleccionado cuando cambia
      this.$emit('input', newValue.code);
    },
  },
  methods: {
    // Muestra u oculta el menú
    toggleDropdown() {
      this.dropdownVisible = !this.dropdownVisible;
    },
    // Selecciona un país y emite el valor
    selectCountry(country) {
      this.selectedOption = country;
      this.$emit('input', country.code); // Emitir valor seleccionado con v-model
      this.dropdownVisible = false;
    },
  },
  mounted() {
    // Emitir el valor predeterminado de México cuando se monta el componente
    this.$emit('input', this.selectedOption.code);
  },
};
</script>

<style scoped>
.custom-select {
  position: relative;
  width: 250px;
  border: 1px solid #ddd;
  border-radius: 6px;
  cursor: pointer;
  padding: 10px;
  background: #fff;
}

.selected-option {
  display: flex;
  align-items: center;
}

.dropdown {
  position: absolute;
  top: 100%;
  left: 0;
  width: 100%;
  border: 1px solid #ddd;
  border-radius: 6px;
  background: #fff;
  z-index: 10;
}

.search-input {
  width: calc(100% - 20px);
  margin: 10px;
  padding: 5px 8px;
  border: 1px solid #ccc;
  border-radius: 4px;
  font-size: 14px;
}

.options {
  list-style: none;
  margin: 0;
  padding: 0;
  max-height: 200px;
  overflow-y: auto;
}

.options li {
  display: flex;
  align-items: center;
  padding: 8px;
  cursor: pointer;
}

.options li:hover {
  background-color: #f0f0f0;
}

.no-results {
  padding: 8px;
  text-align: center;
  color: #999;
}
</style>
