<template>
  <div>
    <p>Esta sección está diseñada para cargar y gestionar antecedentes laborales (BGV), pruebas de dopaje y exámenes
      médicos del empleado. Además, ofrece la posibilidad de configurar fechas de vencimiento y establecer
      notificaciones automáticas, lo que permite un mayor control y seguimiento de los documentos para garantizar su
      actualización y cumplimiento oportuno.</p>

    <div class="header">
      <input type="text" class="search" placeholder="Buscar" v-model="searchQuery" />
      <template v-if="idRol == 6 || idRol == 9 || idRol == 1 || idRol == 10">
        <div class="tooltip-wrapper">
          <button  @click="openAddOpcionModal">Lista Opciones</button>
          <span class="tooltip-text">
            En este formulario puedes agregar, editar o eliminar tu listado de opciones para  nombrar los exámenes.
          </span>
        </div>
        <button @click="openAddDocumentModal">Agregar documento</button>
        <button @click="openSendToRodiModal"
          title="Selecciona un examen médico, prueba de dopaje, prueba psicométrica, BGV o ESE para solicitar a RODI.">Solicitar
          a RODI</button>

      </template>
    </div>
    <div v-if="isLoading" class="loading-spinner">
      No hay examenes registrados aun.
    </div>

    <!-- Mostrar la tabla solo si isLoading es falso -->
    <div v-else>
      <div class="exams-table-wrapper">
        <table class="document-table">
          <thead>
            <tr>
              <th>Nombre</th>
              <th>Tipo</th>
              <th>Fecha de carga</th>
              <th>Fecha de vencimiento</th>
              <th>Días restantes</th>
              <th>Acciones</th>
            </tr>
          </thead>
          <tbody>
            <tr v-if="filteredDocuments.length === 0">
              <td colspan="6" class="text-center">
                No hay documentos cargados para este empleado.
              </td>
            </tr>
            <tr v-for="(exm, index) in filteredDocuments" :key="index">

              <template v-if="exm.isSentByRodi">
                <td style="width: 150px; position: relative;">
                  {{ exm.nameDocument }}
                  <span class="tooltipExam">{{ exm.description }}</span>
                </td>
                <td>Enviar a RODI</td>
              </template>

              <template v-else>
                <td style="position: relative;">
                  {{ exm.optionName }} {{ exm.nameAlterno }}
                  <span class="tooltipExam">{{ exm.description }}</span>
                </td>
                <td>{{ exm.optionType }}</td>
              </template>

              <td>{{ exm.upload_date }}</td>
              <td>
                {{ exm.expiry_date }}
                <div style="position: relative; display: inline-block;">
                  <template v-if="idRol == 6 || idRol == 9 || idRol == 1 || idRol == 10">

                    <i class="fas fa-sync" @click="exm.isSentByRodi ? openUpdateModal2(exm) : openUpdateModal(exm)"
                      style="cursor: pointer; margin-left: 30px; color: #59ae38; font-size: 1.5em;"></i>
                    <span class="tooltipExam">Actualizar</span>
                    <span v-if="exm.estado == 'amarillo' || exm.estado == 'rojo'" class="estadoDocumento"
                      :class="exm.estado"></span>
                  </template>
                </div>
              </td>
              <td>
                <div
                  :style="getStyle(calculateDaysRemaining(exm.expiry_date, exm.expiry_reminder), exm.expiry_reminder)"
                  class="tooltipExam-container" ref="miElemento" style="position: relative;">

                  Días <strong>{{ calculateDaysRemaining(exm.expiry_date, exm.expiry_reminder) }}</strong>
                  <span class="tooltipExam">Recordarme {{ exm.expiry_reminder }} días antes</span>
                </div>
              </td>
              <td style="width: 230px; position: relative;">
                <template v-if="exm.isSentByRodi">
                  <template v-if="exm.socioeconomico > 0">
                    <a v-if="exm.liberado > 0">
                      <i :class="'fas fa-file-invoice download-icon ' + (exm.icono_resultado ? exm.icono_resultado : '')"
                        @click="getBgv(exm.id_candidato)" title="BGV"></i>
                    </a>
                    <a v-else>
                      <i :class="'fas fa-file-invoice download-icon ' + (exm.icono_resultado ? exm.icono_resultado : '')"
                        @click="downloadPrevio(exm.id_candidato)" title="BGV"></i>
                    </a>
                  </template>
                  <a v-if="exm.medico > 0" :href="exm.url" target="_blank">
                    <i :class="{
                      'fas fa-stethoscope download-icon': true,
                      'icono_medico': exm.medicoDetalle && exm.medicoDetalle.id,
                      'icono_resultado_espera': !exm.medicoDetalle || !exm.medicoDetalle.id
                    }" @click="getMedico(exm)" title="Medical Test"></i>
                  </a>
                  <a v-if="exm.tipo_antidoping > 0 && exm.doping && exm.doping.doping_hecho === 1"
                    @click="getDoping(exm)" title="Drug Test">
                    <i :class="{
                      'fas fa-syringe download-icon': true,
                      'icono_doping_reprobado': exm.doping.fecha_resultado && exm.doping.resultado_doping === 1,
                      'icono_doping_aprobado': exm.doping.fecha_resultado && exm.doping.resultado_doping !== 1
                    }"></i>
                  </a>
                  <!-- Si no cumple la primera condición, chequea la segunda -->
                  <a v-else-if="exm.tipo_antidoping == 0 && exm.doping && exm.doping.doping_hecho === 0">

                  </a>
                  <a v-else>
                    <i class="fas fa-syringe download-icon icono_resultado_espera" @click="getDoping(exm)"
                      title="Drug Test"></i>
                  </a>
                  <a v-if="exm.psicometrico > 0" target="_blank">
                    <i :class="{
                      'fas fa-brain download-icon ': true,
                      'icono_psicometria': exm.psicometricoDet && exm.psicometrico.id,
                      'icono_resultado_espera': !exm.psicometricoDet || !exm.psicometricoDet.id,
                    }" @click="getPsicometrico(exm)" title="Practicometric Test"></i>
                  </a>

                </template>

                <template v-else>
                  <template v-if="exm.nameDocument && exm.nameDocument.includes('_sin_')">
                    <!-- contenido cuando incluye '_sin_' -->
                  </template>
                  <template v-else>
                    <a :href="exm.url" target="_blank">
                      <i class="fas fa-file-download download-icon" @click="openDocument(exm)"
                        title="Ver Documento"></i>
                    </a>
                  </template>
                  <template v-if="idRol == 6 || idRol == 9 || idRol == 1 || idRol == 10">

                    <i class="fas fa-trash delete-icon" @click="deleteDocument(exm)" title="Eliminar Documento"></i>
                  </template>
                </template>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
    <AddDocumentsModal v-if="isAddModalVisible" :isVisible="isAddModalVisible" :employeeId="empleadoData.id"
      :id_portal="empleadoData.id_portal" @close="closeAddDocumentModal" @save="saveDocument"
      :endpointConfig="{ tabla: '_examEmpleado', endpoint: '/exams' }" :documentToEdit="selectedDocument"
      @documentoSubido="fetchDocuments" />

    <!-- Modal para ACTUALIZAR -->
    <AddDocumentModal v-if="isUpdateModalVisible" :isVisible="isUpdateModalVisible" :employeeId="empleadoData.id"
      :id_portal="empleadoData.id_portal" @close="closeUpdateModal" @save="saveDocument"
      @documentoSubido="fetchDocuments" :endpointConfig="{ tabla: '_examEmpleado', endpoint: '/documents' }"
      :documentToEdit="selectedDocument" />

    <AddOpcionModal v-if="isAddOpcionModalVisible" :isVisible="isAddOpcionModalVisible" @close="closeOptionModal"
      :idPortal="empleadoData.id_portal" :idCliente="this.idCliente" tabla="_examEmpleado" @cerrar="cerrarModal('opciones')" />
    <!--AddExamModal v-if="isModalVisible" :isVisible="isModalVisible" :employeeId="empleadoData.id"
      :id_portal="empleadoData.id_portal" @close="closeAddExamModal" @save="saveDocument"
      @documentoSubido="fetchDocuments" /-->

    <SendToRodiModal v-if="isSendToRodiModalVisible" @close="closeSendToRodiModal" :employeeId="empleadoData.id"
      :id_portal="empleadoData.id_portal" :idCliente="idCliente" @documentoSubido="fetchDocuments" />

    <div class="modal" v-if="isUpdateModalVisible2">
      <div class="modal-content">
        <span class="close" @click="closeUpdateModal">&times;</span>
        <h2>Actualizar Fecha de Expiración</h2>
        <form @submit.prevent="submitUpdate">
          <label for="expiry-date">Fecha de Expiración:</label>
          <input id="expiry-date" v-model="newExpiryDate" type="date" required />

          <label for="expiryReminder">Recordatorio de Expiración:</label>
          <select v-model="selectedReminder" @change="updateReminder">
            <option value="0">No notificar</option>
            <option value="1">1 día antes</option>
            <option value="7">1 semana antes</option>
            <option value="15">15 días antes</option>
            <option value="30">1 mes antes</option>
            <option value="custom">Personalizado...</option>
          </select>

          <!-- Nuevo campo para el estatus del documento con valores numéricos -->

          <label for="status"
            title="Aqui se  define  si el  estado del documento por  default sera marcado como bueno ">Estatus del
            Documento:</label>
          <br>
          <div class>
            <input type="radio" id="good" value="1" v-model="status" />
            <label for="good" class="good-label">Bueno</label>

            <input type="radio" id="average" value="2" v-model="status" />
            <label for="average" class="average-label">Regular</label>

            <input type="radio" id="bad" value="3" v-model="status" />
            <label for="bad" class="bad-label">Malo</label>
          </div>
          <div v-if="isCustomReminder">
            <label for="customReminder">Días antes:</label>
            <input type="number" id="customReminder" v-model="customReminder" min="0" placeholder="Ingrese días"
              @input="updateReminder" />
          </div>

          <div class="modal-buttons">
            <button type="submit">Guardar</button>
            <button type="button" @click="closeUpdateModal2">Cancelar</button>
          </div>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
import AddDocumentsModal from './AddDocumentsModal.vue';
import AddDocumentModal from './AddDocumentModal.vue';
//import AddExamModal from './AddExamModal.vue';
import SendToRodiModal from './SendToRodiModal.vue';
import AddOpcionModal from '../modales/AddOpcionModal.vue';

import Swal from 'sweetalert2';
import { EventBus } from '@/eventBus';


export default {
  components: {
    AddDocumentsModal,
    AddDocumentModal,
    AddOpcionModal,
    SendToRodiModal,
  },
  props: {
    empleadoData: Object,
    idCliente: {
      type: String,
      default: ''
    },
    idPortal: { type: String, default: '' },
    idUsuario: {
      type: String,
      default: ''
    },

    idRol: {
      type: String,
      default: ''
    },
  },
  data() {
    return {
      isLoading: true,
      isAddModalVisible: false,
      isUpdateModalVisible: false,
      isUpdateModalVisible2: false,
      isAddOpcionModalVisible: false,
      isSendToRodiModalVisible: false,
      newExpiryDate: '',
      exams: [],
      selectedDocument: null,
      selectedReminder: '0',
      customReminder: '',
      isCustomReminder: false,
      isDocumentAvailable: false,
      searchQuery: '',
      status: 1,
    };
  },
  computed: {
    filteredDocuments() {
      const query = this.searchQuery.toLowerCase();
      return this.exams.filter(exm => {
        return (exm.optionName && exm.optionName.toLowerCase().includes(query)) ||
          (exm.upload_date && exm.upload_date.toLowerCase().includes(query)) ||
          (exm.expiry_date && exm.expiry_date.toLowerCase().includes(query));
      });
    }
  },
  mounted() {
    // Primero, obtenemos los documentos
    this.fetchDocuments();

    // Luego, nos aseguramos de que el DOM haya sido actualizado
  },
  watch: {
    exams: {
      handler(newDocs) {
        newDocs.forEach(exm => {
          exm.daysRemaining = this.calculateDaysRemaining(exm.expiry_date, exm.expiry_reminder);
        });
      },
      deep: true,
    },
    newExpiryDate() {
      if (this.selectedDocument) {
        this.selectedDocument.daysRemaining = this.calculateDaysRemaining(this.newExpiryDate, this.isCustomReminder ? this.customReminder : this.selectedReminder);
      }
    },
    customReminder() {
      if (this.selectedDocument) {
        this.selectedDocument.daysRemaining = this.calculateDaysRemaining(this.selectedDocument.expiry_date, this.isCustomReminder ? this.customReminder : this.selectedReminder);
      }
    },
    selectedReminder() {
      if (this.selectedDocument) {
        this.selectedDocument.daysRemaining = this.calculateDaysRemaining(this.selectedDocument.expiry_date, this.isCustomReminder ? this.customReminder : this.selectedReminder);
      }
    },
  },


  methods: {
    openAddOpcionModal() {
      this.isAddOpcionModalVisible = true;
    },
    closeOptionModal() {
      this.isAddOpcionModalVisible = false;
      this.fetchDocuments();
    },
    async getMedico(exm) {
      if (exm && exm.medicoDetalle) {
        const idMedico = exm.medicoDetalle.id; // Obtenemos el ID del médico

        if (idMedico) {
          const urlApi = `${this.$rodiPortal}Medico/crearPDF?id=${idMedico}`;

          try {
            const response = await fetch(urlApi, {
              method: 'GET',
              headers: {
                'Accept': 'application/pdf' // Indica que esperas un PDF como respuesta
              }
            });

            if (!response.ok) {
              throw new Error('Error downloading the PDF');
            }

            // Crea un blob del PDF recibido
            const blob = await response.blob();
            const url = window.URL.createObjectURL(blob);

            // Crea un enlace y simula un clic para descargar el archivo
            const a = document.createElement('a');
            a.href = url;
            a.download = 'medical_result.pdf'; // Nombre del archivo a descargar
            document.body.appendChild(a);
            a.click();

            // Limpia el objeto URL
            window.URL.revokeObjectURL(url);
            a.remove();

          } catch (error) {
            console.error('Error al descargar el archivo:', error);
            Swal.fire({
              title: 'Error',
              text: 'No se pudo descargar el archivo.',
              icon: 'error',
              confirmButtonText: 'Aceptar'
            });
          }
        } else {
          Swal.fire({
            title: 'No disponible',
            text: 'No se encontró el ID médico.',
            icon: 'warning',
            confirmButtonText: 'Aceptar'
          });
        }
      } else {
        Swal.fire({
          title: 'No disponible',
          text: 'Los detalles del doctor no están disponibles.',
          icon: 'warning',
          confirmButtonText: 'Aceptar'
        });
      }
    },


    async getDoping(exm) {
      if (exm && exm.doping) {
        const idDop = exm.doping.id;
        const url = `${this.$rodiPortal}Doping/createPDF?id=${idDop}`;
        console.log("URL de solicitud:", url);
        // Obtenemos el ID del doping
        // console.log("🚀 ~ getDoping ~ idDop:", idDop)

        if (exm.doping.fecha_resultado && exm.doping.doping_hecho === 1) {
          if (idDop) {
            try {
              // Construimos la URL con el idDop como parámetro de consulta
              const response = await fetch(`${this.$rodiPortal}Doping/createPDF?id=${idDop}`, {
                method: 'GET',
                headers: {
                  'Content-Type': 'application/json'
                }
              });

              if (!response.ok) {
                throw new Error('Network response was not ok');
              }

              const blob = await response.blob(); // Obtener el blob de la respuesta

              // Crear un enlace para la descarga
              const url = window.URL.createObjectURL(blob);
              const link = document.createElement('a');
              link.href = url;
              link.setAttribute('download', 'doping_result.pdf'); // Nombre del archivo a descargar
              document.body.appendChild(link);
              link.click();
              link.remove();

              // Liberar el objeto URL
              window.URL.revokeObjectURL(url);
            } catch (error) {
              console.error('Error al descargar el archivo:', error);
              Swal.fire({
                title: 'Error',
                text: 'No se pudo descargar el archivo.',
                icon: 'error',
                confirmButtonText: 'Aceptar'
              });
            }
          } else {
            Swal.fire({
              title: 'No disponible',
              text: 'No se encontró el ID de la prueba antidoping.',
              icon: 'warning',
              confirmButtonText: 'Aceptar'
            });
          }
        } else {
          Swal.fire({
            title: 'No disponible',
            text: 'El resultado de la prueba antidoping aún no está disponible.',
            icon: 'warning',
            confirmButtonText: 'Aceptar'
          });
        }
      } else {
        console.error('Invalid drug exam object', exm);
      }
    },


    async getPsicometrico(exm) {
      console.log('exm:', exm); // Para fines de depuración
      if (exm && exm.psicometrico && exm.psicometricoDet) {
        if (exm.psicometricoDet.archivo_psicometrico) {
          const url = `${this.$rodiPortal}_psicometria/${exm.psicometricoDet.archivo_psicometrico}`;
          window.open(url, '_blank'); // Abre el archivo en una nueva pestaña
        } else {
          Swal.fire({
            title: 'No disponible',
            text: 'El archivo aún no está disponible.',
            icon: 'warning',
            confirmButtonText: 'Aceptar'
          });
        }
      } else {
        // Manejar el caso en que exm sea indefinido o le falten propiedades necesarias
        console.error('Objeto de examen no válido', exm);
      }
    },


    async downloadPrevio(id_candidato) {
      const urlApi = `${this.$rodiPortal}Candidato_Conclusion/createPrevioPDF?idPDF=${id_candidato}`;

      try {
        const response = await fetch(urlApi, {
          method: 'GET',
          headers: {
            'Accept': 'application/pdf' // Indica que esperas un PDF como respuesta
          }
        });

        if (!response.ok) {
          if (response.status === 404) {
            Swal.fire({
              title: 'No disponible',
              text: 'No se encontró el ID del candidato.',
              icon: 'warning',
              confirmButtonText: 'Aceptar'
            });
            return;
          } else {
            throw new Error('No se pudo descargar el PDF');
          }
        }

        // Crea un blob del PDF recibido
        const blob = await response.blob();
        const url = window.URL.createObjectURL(blob);

        // Crea un enlace y simula un clic para descargar el archivo
        const a = document.createElement('a');
        a.href = url;
        a.download = 'reporte.pdf'; // Nombre del archivo a descargar
        document.body.appendChild(a);
        a.click();

        // Limpia el objeto URL
        window.URL.revokeObjectURL(url);
        a.remove();

      } catch (error) {
        console.error('El previo aun no esta  listo :', error);
        Swal.fire({
          title: 'Error',
          text: 'No se pudo descargar el archivo.',
          icon: 'error',
          confirmButtonText: 'Aceptar'
        });
      }
    },

    async getBgv(id_candidato) {
      const urlApi = `${this.$rodiPortal}Candidato_Conclusion/createPDF?idCandidatoPDF=${id_candidato}`;

      try {
        const response = await fetch(urlApi, {
          method: 'GET',
          headers: {
            'Accept': 'application/pdf' // Indica que esperas un PDF como respuesta
          }
        });

        if (!response.ok) {
          throw new Error('Error downloading the PDF');
        }

        // Crea un blob del PDF recibido
        const blob = await response.blob();
        const url = window.URL.createObjectURL(blob);

        // Crea un enlace y simula un clic para descargar el archivo
        const a = document.createElement('a');
        a.href = url;
        a.download = 'reporte.pdf'; // Nombre del archivo a descargar
        document.body.appendChild(a);
        a.click();

        // Limpia el objeto URL
        window.URL.revokeObjectURL(url);
        a.remove();

      } catch (error) {
        console.error('Error al obtener la respuesta:', error);
        Swal.fire({
          title: 'Error',
          text: `Error: ${error.message}`,
          icon: 'error',
          confirmButtonText: 'Aceptar'
        });
      }
    },

    openUpdateModal2(exm) {
      console.log("🚀 ~ openUpdateModal ~ exm:", exm)
      this.selectedDocument = exm;
      this.status = exm.status;
      this.newExpiryDate = exm.expiry_date;
      this.selectedReminder = exm.expiry_reminder || '';

      const validReminders = [0, 1, 7, 15, 30];

      // Verificamos si selectedReminder es uno de los valores válidos numéricos
      if (!validReminders.includes(Number(this.selectedReminder))) {
        // Si no es un valor válido, asignamos 'custom' y el valor original a customReminder
        this.customReminder = this.selectedReminder;  // Guardamos el valor original como 'custom'
        this.selectedReminder = 'custom'; // Marcamos como 'custom'
      } else {
        // Si es un valor válido, usamos el valor numérico
        this.customReminder = '';  // No hay necesidad de customReminder si el valor es válido
      }
      this.isCustomReminder = this.selectedReminder === 'custom';
      this.isUpdateModalVisible2 = true;
    },
    openSendToRodiModal() {
      Swal.fire({
        title: 'Información importante',
        text: 'Las pruebas y exámenes solicitados en RODI generan un costo adicional según el estudio o tipo de examen a aplicar. Para más información sobre los costos, haz clic en el siguiente enlace.',
        icon: 'info',
        showCancelButton: true,
        confirmButtonText: 'Aceptar',
        cancelButtonText: 'Cancelar',
        footer: '<a href="https://www.rodi.com.mx/" target="_blank">Haz clic aquí para ver la información de costos</a>'
      }).then((result) => {
        if (result.isConfirmed) {
          this.isSendToRodiModalVisible = true; // Abre el modal solo si el usuario acepta
        }
      });
    },

    closeSendToRodiModal() {
      this.isSendToRodiModalVisible = false;
    },
    // abre  los  documentos 
    openDocument(exm) {

      EventBus.$emit('open-document', exm, '_examEmpleado');
    },
    // abre  el modal  para  actualizar  las  fechas  de  expiracion 


    openUpdateModal(doc) {
      this.selectedDocument = doc;
      this.isUpdateModalVisible = true;
      this.isUpdateModalVisible2 = false;
    },

    closeUpdateModal() {
      this.isUpdateModalVisible = false;
      this.selectedDocument = null;
      this.fetchDocuments();
    },
    closeUpdateModal2() {
      this.isUpdateModalVisible2 = false;

      this.fetchDocuments();
    },
    // actualiza  la fecha  de expiracion 
    async submitUpdate() {

      if (this.newExpiryDate) {
        try {
          const response = await fetch(`${this.$apiUrl}/documents/${this.selectedDocument.id}`, {
            method: 'PUT',
            headers: {
              'Content-Type': 'application/json',
            },
            body: JSON.stringify({
              table: 'exams_empleados',
              expiry_date: this.newExpiryDate,
              expiry_reminder: this.isCustomReminder ? this.customReminder : this.selectedReminder,
              status: this.status,

            }),
          });

          if (!response.ok) {
            throw new Error('Error updating the expiration date');
          }

          this.selectedDocument.expiry_date = this.newExpiryDate;
          this.selectedDocument.expiry_reminder = this.isCustomReminder ? this.customReminder : this.selectedReminder;

          await Swal.fire({
            icon: 'success',
            title: '¡Éxito!',
            text: 'La fecha de vencimiento se actualizó correctamente.',
            confirmButtonText: 'Aceptar'
          });
          this.closeUpdateModal();
          await this.fetchDocuments();
        } catch (error) {
          console.error(error);
          await Swal.fire({
            icon: 'error',
            title: '¡Error!',
            text: 'No se pudo actualizar la fecha de vencimiento.',
            confirmButtonText: 'Aceptar'
          });
        }
      }
    },
    //  calcula  la fecha  de   expiracionde los documentos 
    calculateDaysRemaining(expiryDate, daysBefore) {

      const today = new Date();
      // Ajusta la fecha de hoy a medianoche
      today.setHours(0, 0, 0, 0);

      const expiry = new Date(expiryDate);
      // Ajusta la fecha de expiración a medianoche
      expiry.setHours(0, 0, 0, 0);

      // Verifica si daysBefore es null
      if (daysBefore === null || daysBefore == 0) {
        return 'No Notificar'; // Devuelve un valor en blanco
      }

      // Calcula la fecha de advertencia
      const warningDate = new Date(expiry);
      warningDate.setDate(warningDate.getDate() - daysBefore); // Usa daysBefore aquí

      // Calcula los días restantes
      const timeDiff = expiry - today;
      const retornar = Math.ceil(timeDiff / (1000 * 3600 * 24)) + 1;
      //console.log("🚀 ~ calculateDaysRemaining ~ retornar:", retornar)
      return retornar; // Días restantes
    },
    //  alerta  para  vencimiento de documentos 
    getStyle(timeDiff, daysBefore) {

      if (daysBefore === null || daysBefore == 0) {
        return {
          backgroundColor: '#ccffcc',
          color: 'black',
          padding: '10px',
          borderRadius: '5px',
        };
      }

      // Verificar si la fecha de expiración ha pasado o es hoy
      if (timeDiff <= 0) {
        return {
          backgroundColor: 'rgba(255, 0, 0, 0.906)', // rojo tenue
          color: 'white',
          padding: '10px',
          borderRadius: '5px',
        };
      }

      // Verificar si la diferencia en días es igual a daysBefore
      if (timeDiff <= daysBefore) {
        return {
          backgroundColor: 'rgba(255, 0, 0, 0.906)', // rojo tenue (advertencia)
          color: 'white',
          padding: '10px',
          borderRadius: '5px',
        };
      }

      // Verificar si está dentro de un rango de 5 días antes de daysBefore
      if (timeDiff > daysBefore && timeDiff <= (daysBefore + 5)) {
        return {
          backgroundColor: 'rgba(205, 202, 0, 0.915)', // amarillo tenue
          padding: '10px',
          color: 'white',
          borderRadius: '5px',
        };
      }

      // Si no cumple ninguna de las condiciones anteriores, es verde
      return {
        backgroundColor: 'rgba(12, 232, 34, 0.922)', // verde tenue
        color: 'white',
        padding: '10px',
        borderRadius: '5px',
      };
    },

    async fetchDocuments() {
      try {
        const response = await fetch(`${this.$apiUrl}/exam/${this.empleadoData.id}`);
        if (!response.ok) {
          throw new Error('No se pudo obtener los examenes');
        }
        const data = await response.json();

        // Mapeo de documentos para incluir propiedades adicionales
        this.exams = data.documentos.map(exm => ({
          ...exm,
          daysRemaining: this.calculateDaysRemaining(exm.expiry_date, exm.expiry_reminder),
          isSentByRodi: exm.id_candidato !== null,
          status: exm.statusexm,
          estado: exm.statusexm == 2 ? 'amarillo' : (exm.statusexm == 3 ? 'rojo' : '')// Verifica si el documento fue enviado por RODI
        }));
        // Comprobar si hay al menos un documento enviado por RODI
        this.isDocumentAvailable = this.exams.some(exm => exm.isSentByRodi);
        this.isLoading = false;

      } catch (error) {
        console.error('Error al obtener examenes:', error);
        // Genera documentos de ejemplo en caso de error
      }
    },



    closeAddExamModal() {
      this.isModalVisible = false;
    },

    generateSampleDocuments() {
      return [
        {
          optionName: 'Bgv',
          upload_date: new Date().toISOString().split('T')[0],
          expiry_date: new Date(new Date().setFullYear(new Date().getFullYear() + 1)).toISOString().split('T')[0],
          url: '#',
        },
        {
          optionName: 'Drug test ',
          upload_date: new Date().toISOString().split('T')[0],
          expiry_date: new Date(new Date().setFullYear(new Date().getFullYear() + 1)).toISOString().split('T')[0],
          url: '#',
        },
      ];
    },
    openAddDocumentModal() {
      this.isAddModalVisible = true;
    },
    closeAddDocumentModal() {
      this.isAddModalVisible = false;
      this.fetchDocuments();
    },

    openAddExamModal() {
      this.isModalVisible = true;
    },

    saveDocument(formData) {
      this.$emit('add-document', formData);
      this.closeAddExamModal();
    },
    deleteDocument(exm) {
      Swal.fire({
        title: '¿Estás seguro?',
        text: 'Este documento se eliminará permanentemente.',
        icon: 'warning',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        confirmButtonText: 'Sí, ¡eliminar!',
        cancelButtonText: 'Cancelar'
      }).then(async (result) => {
        if (result.isConfirmed) {
          try {
            const response = await fetch(`${this.$apiUrl}/documents`, {
              method: 'DELETE',
              headers: {
                'Content-Type': 'application/json',
              },
              body: JSON.stringify({
                id: exm.id,
                nameDocument: exm.nameDocument,
                tabla: 'examenes',
              }),
            });

            if (!response.ok) {
              throw new Error('Error al eliminar el documento');
            }

            // Verifica que exams esté definido antes de filtrar
            if (this.exams) {
              this.exams = this.exams.filter(e => e.id !== exm.id);
              Swal.fire('¡Eliminado!', 'El documento ha sido eliminado.', 'success');
            } else {
              console.error('Exams está indefinido al intentar filtrar');
            }
          } catch (error) {
            Swal.fire('¡Error!', error.message, 'error');
          }
        }
      });
    },



    updateReminder() {
      this.isCustomReminder = this.selectedReminder === 'custom';
      if (!this.isCustomReminder) {
        this.customReminder = this.selectedReminder;
      }
    },
  },
};
</script>

