      <p class="info-text">Aquí puedes cargar y gestionar cursos internos y programas de capacitación diseñados para
        mejorar las habilidades y el rendimiento de los empleados dentro de la organización, promoviendo su desarrollo
        profesional continuo.</p>

      <div class="header">
        <input type="text" class="search" placeholder="Search" v-model="searchQuery" />
        <div class="tooltip-wrapper">
          <button class="tooltip-exams register-course-button" @click="openAddOpcionModal">Lista Opciones</button>
          <span class="tooltip-text">
            En este formulario puedes agregar, editar o eliminar tu listado de opciones precargados para nombrar los cursos  y capacitaciones.
          </span>
        </div>
        <button class="register-course-button" @click="openAddDocumentModal">Registrar Curso</button>

      </div>

      <div class="courses-table-wrapper">
        <table class="courses-table">
          <thead>
            <tr>
              <th>ID</th>
              <th>Nombre</th>
              <th>Fecha de Registro</th>
              <th>Fecha de Expiración</th>
              <th>Días Restantes</th>
              <th>Acciones</th>
            </tr>
          </thead>
          <tbody>
            <tr v-if="filteredCourses.length === 0">
              <td colspan="6" class="no-courses">Aún no se han cargado los cursos para este empleado.</td>
            </tr>
            <tr v-for="(course, index) in filteredCourses" :key="index">
              <td>{{ course.id }}</td>
              <td style="position: relative;">
                {{ course.optionName }} {{ course.nameAlterno }}
                <span class="tooltipCursos">{{ course.description }}</span>
              </td>
              <td>{{ course.upload_date }}</td>
              <td>{{ course.expiry_date }}
                <div style="position: relative; display: inline-block;">
                  <i class="fas fa-sync" @click="openUpdateModal(course)"
                    style="cursor: pointer; margin-left: 30px; color: #59ae38; font-size: 1.5em;"></i>
                  <span class="tooltipCursos">Actualizar</span>
                  <span v-if="course.estado === 'amarillo' || course.estado === 'rojo'" class="estadoDocumento"
                    :class="course.estado"> </span>
                </div>
              </td>
              <td>
                <div
                  :style="getStyle(calculateDaysRemaining(course.expiry_date, course.expiry_reminder), course.expiry_reminder)"
                  class="tooltipCursos-container" style="position: relative;">
                  Days <strong>{{ calculateDaysRemaining(course.expiry_date, course.expiry_reminder) }}</strong>
                  <span class="tooltipCursos">Recordarme {{ course.expiry_reminder }} dias antes</span>
                </div>
              </td>
              <td>
                <div class="modal_cursos-buttons">
                  <template v-if="course.name && course.name.includes('_sin_')">
                    <!-- contenido cuando incluye '_sin_' -->
                  </template>
                  <template v-else>
                    <button v-if="course.name !== null" @click="openEvidence(course)" class="icon-button"
                      aria-label="View">
                      <i class="fas fa-eye"></i> <!-- Icono para Ver -->
                    </button>
                  </template>


                  <!-- Icono para Eliminar -->
                  <button type="button" @click="deleteCourse(course)" class="icon-button" aria-label="Delete">
                    <i class="fas fa-trash-alt"></i> <!-- Icono para Eliminar -->
                  </button>
                </div>
              </td>
            </tr>
          </tbody>
        </table>
      </div>

      <div class="modal_cursos-footer">
        <button class="close-button" @click="closeModal">Close</button>
      </div>
    </div>
</div>

    <!-- Modal para AGREGAR -->
    <AddDocumentsModal v-if="isAddModalVisible" :isVisible="isAddModalVisible" :employeeId="empleados.id"
      :id_portal="empleados.id_portal" @close="closeAddDocumentModal" @save="saveDocument"
      :endpointConfig="{ tabla: '_cursos', endpoint: '/cursos/registrar' }" :documentToEdit="selectedDocument"
      @documentoSubido="fetchDocuments" :origen="origen" />

    <!-- Modal para ACTUALIZAR -->
    <AddDocumentModal v-if="isUpdateModalVisible" :isVisible="isUpdateModalVisible" :employeeId="empleados.id"
      :id_portal="empleados.id_portal" @close="closeUpdateModal" @save="saveDocument" @documentoSubido="fetchDocuments"
      :endpointConfig="{ tabla: '_cursos', endpoint: '/documents' }" :documentToEdit="selectedDocument"
      :origen="origen" />
    <AddOpcionModal v-if="isAddOpcionModalVisible" :isVisible="isAddOpcionModalVisible" @close="closeOptionModal"
      :idPortal="empleados.id_portal" :idCliente="this.idCliente" tabla="_cursos"
      @cerrar="cerrarModal('opciones')" />

  </div>
  
</template>



<script>
import AddDocumentsModal from '../componentsPerfil/AddDocumentsModal.vue';
import AddDocumentModal from '../componentsPerfil/AddDocumentModal.vue';
import api from '@/services/apiEmpleados'; // Asegúrate de que la ruta sea correcta
import AddOpcionModal from '../modales/AddOpcionModal.vue';
import Swal from 'sweetalert2';
import { EventBus } from '@/eventBus';

export default {
  props: {
    empleados: { type: Object, required: true },
    show: { type: Boolean, default: false },
    origen: { type: Number, required: true },
    idPortal: { type: String, default: '' },
    idUsuario: {
      type: String,
      default: ''
    },
    idCliente: {
      type: String,
      default: ''
    },
    idRol: {
      type: String,
      default: ''
    },
  },
  components: {
    AddDocumentsModal,
    AddDocumentModal,
    AddOpcionModal
  },
  data() {
    return {
      courses: [],
      searchQuery: '',
      isAddModalVisible: false,
      isUpdateModalVisible: false,
      isAddOpcionModalVisible: false,
      customReminder: '',
      isCustomReminder: false,
      selectedDocument: null,
      newExpiryDate: '',
      status: 0,
      selectedReminder: '0',
    };
  },
  computed: {
    filteredCourses() {
      if (!this.courses) return [];
      const query = this.searchQuery.toLowerCase();
      return this.courses.filter(course => {
        const optionName = course.optionName ? course.optionName.toLowerCase() : '';
        const uploadDate = course.upload_date ? course.upload_date.toLowerCase() : '';
        const expiryDate = course.expiry_date ? course.expiry_date.toString() : '';

        return optionName.includes(query) ||
          uploadDate.includes(query) ||
          expiryDate.includes(query);
      });
    }
  },
  watch: {
    courses: {
      handler(newDocs) {
        newDocs.forEach(course => {
          course.daysRemaining = this.calculateDaysRemaining(course.expiry_date, course.expiry_reminder);
        });
      },
      deep: true,
    },
    newExpiryDate() {
      if (this.selectedDocument) {
        this.selectedDocument.daysRemaining = this.calculateDaysRemaining(this.newExpiryDate, this.isCustomReminder ? this.customReminder : this.selectedReminder);
      }
    },
    customReminder() {
      if (this.selectedDocument) {
        this.selectedDocument.daysRemaining = this.calculateDaysRemaining(this.selectedDocument.expiry_date, this.isCustomReminder ? this.customReminder : this.selectedReminder);
      }
    },
    selectedReminder() {
      if (this.selectedDocument) {
        this.selectedDocument.daysRemaining = this.calculateDaysRemaining(this.selectedDocument.expiry_date, this.isCustomReminder ? this.customReminder : this.selectedReminder);
      }
    },
  },
  methods: {
    openAddOpcionModal() {
      this.isAddOpcionModalVisible = true;
    },
    closeOptionModal() {
      this.isAddOpcionModalVisible = false;
      this.fetchDocuments();
    },
    openAddDocumentModal() {
      this.isAddModalVisible = true;
    },

    openUpdateModal(doc) {
      this.selectedDocument = doc;
      this.isUpdateModalVisible = true;
    },
    closeUpdateModal() {
      this.isUpdateModalVisible = false;
      this.selectedDocument = null;
      this.fetchDocuments();
    },

    saveDocument(formData) {
      this.$emit('add-document', formData);
      this.closeAddDocumentModal();
    },
    closeAddDocumentModal() {
      this.isAddModalVisible = false;
      this.fetchDocuments();
    },




    closeModal() {
      // Solo cierra si no hay otros modales abiertos
      if (!this.isUpdateModalVisible && !this.isAddModalVisible) {
        this.$emit('close');
        this.$emit('update');
      }
    },
    openEvidence(course) {
      // console.log("🚀 ~ openEvidence ~ course:", course)

      EventBus.$emit('open-document', course, '_cursos');
    },
    deleteCourse(course) {
      Swal.fire({
        title: 'Estas  Seguro?',
        text: "Este  documento se borrara  permanentemente.",
        icon: 'warning',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        confirmButtonText: 'Si, Borrarlo!',
        width: '90%', // Establece un ancho responsivo
        padding: '20px', // Añadir un poco de relleno para que se vea bien
        customClass: {
          popup: 'responsive-popup', // Agrega una clase personalizada para estilos adicionales
        }
      }).then(async (result) => {
        if (result.isConfirmed) {
          try {
            // Log the document name

            const response = await fetch(`${this.$apiUrl}/documents`, {
              method: 'DELETE',
              headers: {
                'Content-Type': 'application/json',
              },
              body: JSON.stringify({
                id: course.id,
                nameDocument: course.name,
                tabla: 'cursos',
              }),
            });

            if (!response.ok) {
              throw new Error('Error deleting the document');
            }

            // Update the document list
            this.courses = this.courses.filter(c => c.id !== course.id);
            Swal.fire('Deleted!', 'El Curso se Elimino correctamente.', 'success');
          } catch (error) {
            Swal.fire('Error!', error.message, 'error');
          }
        }
      });
    },

    fetchDocuments() {
      const apiUrl = `${this.$apiUrl}/cursos/empleado`; // Ajusta la URL a la ruta correcta
      const employeeId = this.empleados.id; // Suponiendo que tienes el ID del empleado en `this.empleados.id`
      const origen = this.origen; // Suponiendo que tienes el origen en `this.empleados.origen`
      api.get(apiUrl, { params: { employee_id: employeeId, origen: origen } })
        .then(response => {
          const cursos = response.data.documentos; // 👈 accede correctamente al array

          if (Array.isArray(cursos)) {
            this.courses = cursos.map(course => ({
              ...course,
              estado: course.status == 2 ? 'amarillo' : (course.status == 3 ? 'rojo' : '')
            }));

            console.log("🚀 ~ fetchDocuments ~ this.courses:", this.courses);
          } else {
            console.log("🚀 ~ No se devolvieron cursos");
          }
        })
        .catch(error => {
          console.error('Error fetching cursos:', error);
        });
    },
    calculateDaysRemaining(expiryDate, daysBefore) {

      const today = new Date();
      // Ajusta la fecha de hoy a medianoche
      today.setHours(0, 0, 0, 0);

      const expiry = new Date(expiryDate);
      // Ajusta la fecha de expiración a medianoche
      expiry.setHours(0, 0, 0, 0);

      // Verifica si daysBefore es null
      if (daysBefore == null || daysBefore == 0) {
        return 'Do not notify'; // Devuelve un valor en blanco
      }

      // Calcula la fecha de advertencia
      const warningDate = new Date(expiry);
      warningDate.setDate(warningDate.getDate() - daysBefore); // Usa daysBefore aquí

      // Calcula los días restantes
      const timeDiff = expiry - today;
      const retornar = Math.ceil(timeDiff / (1000 * 3600 * 24)) + 1;
      // console.log("🚀 ~ calculateDaysRemaining ~ retornar:", retornar)
      return retornar; // Días restantes
    },



    getStyle(timeDiff, daysBefore) {

      if (daysBefore === null) {
        return {
          backgroundColor: '#ccffcc',
          color: 'black',
          padding: '10px',
          borderRadius: '5px',
        };
      }

      // Verificar si la fecha de expiración ha pasado o es hoy
      if (timeDiff <= 0) {
        return {
          backgroundColor: 'rgba(255, 0, 0, 0.906)', // rojo tenue
          color: 'white',
          padding: '10px',
          borderRadius: '5px',
        };
      }

      // Verificar si la diferencia en días es igual a daysBefore
      if (timeDiff <= daysBefore) {
        return {
          backgroundColor: 'rgba(255, 0, 0, 0.906)', // rojo tenue (advertencia)
          color: 'white',
          padding: '10px',
          borderRadius: '5px',
        };
      }

      // Verificar si está dentro de un rango de 5 días antes de daysBefore
      if (timeDiff > daysBefore && timeDiff <= (daysBefore + 5)) {
        return {
          backgroundColor: 'rgba(205, 202, 0, 0.915)', // amarillo tenue
          padding: '10px',
          color: 'white',
          borderRadius: '5px',
        };
      }

      // Si no cumple ninguna de las condiciones anteriores, es verde
      return {
        backgroundColor: 'rgba(12, 232, 34, 0.922)', // verde tenue
        color: 'white',
        padding: '10px',
        borderRadius: '5px',
      };
    },
    getRandomDate() {
      const start = new Date();
      const end = new Date(start);
      end.setFullYear(end.getFullYear() + 1); // Expira en un año
      const randomDate = new Date(start.getTime() + Math.random() * (end.getTime() - start.getTime()));
      return randomDate.toISOString().split('T')[0]; // Formato YYYY-MM-DD
    },
    getRandomNearDate() {
      const today = new Date();
      const daysToAdd = Math.floor(Math.random() * 10); // Genera un número entre 0 y 9
      const nearexpiry_date = new Date(today);
      nearexpiry_date.setDate(today.getDate() + daysToAdd);
      return nearexpiry_date.toISOString().split('T')[0]; // Formato YYYY-MM-DD
    },

    updateReminder() {
      if (this.selectedReminder === 'custom') {
        this.isCustomReminder = true;
      } else {
        this.isCustomReminder = false;
        this.customReminder = this.selectedReminder;
      }
    },

  },
  mounted() {
    this.fetchDocuments(); // Llama a la función al montar el componente
  }
};
</script>




