<template>
  <div v-if="visible" class="document-modal">
    <div class="modal-overlay" @click="close"></div>
    <div class="modal-content">
      <button class="close-button" @click="close">&times;</button>

      <div class="iframe-container">
        <iframe :src="documentUrl"></iframe>
      </div>

      <p class="save-instruction">
       <strong> Si deseas descargar el documento, haz clic derecho sobre el documento y selecciona "Guardar como"</strong>
      </p>

      <div class="modal-actions">
        <button class="close-modal-button" @click="close">Cerrar</button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'DocumentModal',
  props: {
    visible: {
      type: Boolean,
      default: false,
    },
    folder: {
      type: String,
      default: '',
    },
    documentName: {
      type: String,
      default: '',
    },
  },
  computed: {
    documentUrl() {
      if (this.folder && this.documentName) {
        return `${this.$documentUrl}/${this.folder}/${this.documentName}`;
      }
      return '';
    },
  },
  methods: {
    close() {
      this.$emit('close');
    }
  },
};
</script>

<style scoped>
.document-modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1000;
}

.modal-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
}

.modal-content {
  position: absolute;
  top: 5%;
  left: 5%;
  width: 90%;
  height: 90%;
  background: #fff;
  padding: 20px;
  z-index: 1001;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.iframe-container {
  flex: 1;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.iframe-container iframe {
  width: 100%;
  height: 100%;
  border: none;
  object-fit: contain;
}

.save-instruction {
  font-size: 1.3rem;
  text-align: center;
  margin-top: 10px;
  color: #333;
}

.close-button {
  position: absolute;
  top: 10px;
  right: 10px;
  font-size: 32px;
  background-color: red;
  color: white;
  border: none;
  border-radius: 6px;
  width: 40px;
  height: 40px;
  cursor: pointer;
}

.modal-actions {
  margin-top: 10px;
  text-align: center;
}

.close-modal-button {
  background-color: red;
  color: white;
  font-size: 1.1rem;
  padding: 10px 20px;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.close-modal-button:hover {
  background-color: darkred;
}
</style>
