<template>
  <div>
    <h2>Información Laboral</h2>


   
    <!-- Contenedor de pestañas para alternar entre secciones -->
    <div class="section-tabs">
      <!--button @click="setSection('periodos')" :class="{ active: currentSection === 'periodos' }">
        Periodos Nomina
      </button-->
      <button @click="setSection('datosLaborales')" :class="{ active: currentSection === 'datosLaborales' }">
        Datos Laborales
      </button>
      <button @click="setSection('preNomina')" :class="{ active: currentSection === 'preNomina' }">
        Prenómina
      </button>
      <button @click="setSection('periodos')" :class="{ active: currentSection === 'periodos' }">
        Historial
      </button>

    </div>

    <!-- Sección de Pre-Nómina -->
    <div v-if="currentSection === 'preNomina'">

      <!-- Aquí cargamos el modal automáticamente sin botón -->
      <PreNominaModal :isVisible="isPreNominaVisible" :idRol="idRol" @update:isVisible="isPreNominaVisible = $event"
        :empleado="empleado" :idPortal="idPortal"  :idCliente="idCliente" />
    </div>

    <!-- Sección de Datos Laborales -->
    <div v-if="currentSection === 'datosLaborales'">


      <!-- Aquí cargamos el modal dentro de esta sección -->
      <DatosLaboralesModal :isVisible="isDatosLaboralesVisible" :idRol="idRol" :idPortal="idPortal"
        :idCliente="idCliente" :idUsuario="idUsuario" @update:isVisible="isDatosLaboralesVisible = $event"
        :empleado="empleado" @datosActualizados="getEmpleadoData" />

    </div>
    <div v-if="currentSection === 'periodos'">

      <PeriodosNominaModal :isVisible="isPeriodosNominaVisible" :idRol="idRol" :idPortal="idPortal"
        :idCliente="idCliente" :idUsuario="idUsuario" :empleado="empleado"
        @update:isVisible="isPeriodosNominaVisible = $event" @datosActualizados="getEmpleadoData" />
    </div>
  </div>
</template>

<script>
// Importar los modales
import PreNominaModal from '@/components/modales/PreNominaModal.vue';
import DatosLaboralesModal from '@/components/modales/DatosLaboralesModal.vue';
import PeriodosNominaModal from '@/components/modales/PeriodosNominaModal.vue';
import axios from 'axios';

import Swal from 'sweetalert2';
import moment from 'moment-timezone';

export default {
  props: {
    empleadoData: { type: Object, required: true },
    idRol: {
      type: String,
      default: ''
    },
    idCliente: {
      type: String,
      default: ''

    },
    idPortal: {
      type: String,
      default: ''
    },
    idUsuario: {
      type: String,
      default: ''
    },
  },
  components: {
    PreNominaModal,
    DatosLaboralesModal,
    PeriodosNominaModal, // Registrar el componente de Datos Laborales
  },
  data() {
    return {
      currentSection: 'datosLaborales', // Sección activa por defecto
      isPreNominaVisible: false,  // Controla la visibilidad del modal de Pre-Nómina
      isDatosLaboralesVisible: false,
      isPeriodosNominaVisible: false,
      isHistorialNominaVisible: false,    // Controla la visibilidad del moda
      empleado: null,
    };
  },
  mounted() {
    // Llamamos a la función cuando el componente se monta
    this.getEmpleadoData();
  },
  methods: {
     recalcularPosicionTooltip() {
      this.$nextTick(() => {
        const wrapper = this.$refs.tooltipWrapper;
        const tooltip = wrapper.querySelector('.horas-extra-tooltip-content');

        if (!tooltip) return;

        const wrapperRect = wrapper.getBoundingClientRect();
        const tooltipRect = tooltip.getBoundingClientRect();
        const espacioDerecha = window.innerWidth - wrapperRect.right;
        const espacioIzquierda = wrapperRect.left;
        const espacioArriba = wrapperRect.top;
        const espacioAbajo = window.innerHeight - wrapperRect.bottom;

        // Posición horizontal
        if (espacioDerecha < tooltipRect.width && espacioIzquierda > tooltipRect.width) {
          this.tooltipPositionClass = 'left';
        } else {
          this.tooltipPositionClass = '';
        }

        // Posición vertical (opcional)
        if (espacioAbajo < tooltipRect.height && espacioArriba > tooltipRect.height) {
          this.tooltipPositionClass += ' top';
        }
      });
    },
    handleCloseModal() {
      // Aquí pones la lógica para cerrar el modal
      // Por ejemplo, cambiar una variable que controla la visibilidad
      this.isModalOpen = false;
    },
   


    async updateEmpleado() {
      const filteredData = Object.fromEntries(
        Object.entries(this.localEmpleadoData).filter(([key, value]) =>
          key !== 'status' && value !== '' && value !== null
        )
      );
     // console.log('Datos filtrados:', filteredData);


      // Establecer la fecha de edición con la fecha y hora actual en la zona horaria de México
      filteredData.edicion = moment.tz('America/Mexico_City').format('YYYY-MM-DD HH:mm:ss');

      try {
        const apiUrl = `${this.$apiUrl}/empleados/update`;


        if (!this.$csrfToken) {
          console.error('CSRF token no encontrado');
          return; // Evita enviar la solicitud si el token no está presente
        }

        await axios.put(apiUrl, filteredData, {
          headers: {
            'X-CSRF-TOKEN': this.$csrfToken
          }
        });

       // console.log('Respuesta de la API:', response); // Ver respuesta completa

        // Mostrar SweetAlert de éxito
        Swal.fire({
          icon: 'success',
          title: 'Actualización exitosa',
          text: 'Los datos del empleado se han actualizado correctamente.',
          confirmButtonText: 'Aceptar'
        }).then(() => {
          // Emitir evento solo después de que el usuario acepte el mensaje
          this.$emit('empleadoActualizado', filteredData);
          this.handleCloseModal(); // Cerrar modal
        });
      } catch (error) {
        let errorMessage = 'Ocurrió un error al actualizar el empleado.';

        if (error.response) {
          errorMessage = error.response.data.error || 'Error desconocido';
          console.error('Error al actualizar el empleado:', error.response.data);
        } else if (error.request) {
          console.error('No se recibió respuesta del servidor:', error.request);
          errorMessage = 'No se recibió respuesta del servidor.';
        } else {
          console.error('Error en la configuración de la solicitud:', error.message);
          errorMessage = error.message;
        }

        // Mostrar SweetAlert de error
        Swal.fire({
          icon: 'error',
          title: 'Error',
          text: errorMessage,
          confirmButtonText: 'Aceptar'
        });
      }
    },
    // Cambiar la sección activa
    setSection(section) {
      this.currentSection = section;
      this.getEmpleadoData();

      // Resetear visibilidad
      this.isDatosLaboralesVisible = false;
      this.isPreNominaVisible = false;
      this.isPeriodosNominaVisible = false;

      // Activar solo el correspondiente
      if (section === 'datosLaborales') {
        this.isDatosLaboralesVisible = true;
      } else if (section === 'preNomina') {
        this.isPreNominaVisible = true;
      } else if (section === 'periodos') {
        this.isPeriodosNominaVisible = true;
      }
    },


    // Función para hacer la solicitud al backend y obtener los datos del empleado
    // Función para hacer la solicitud al backend y obtener los datos laborales del empleado
    getEmpleadoData() {
      const empleadoId = this.empleadoData.id;
      const apiUrl = `${this.$apiUrl}/empleado/${empleadoId}/laborales`;
      // Obtienes el id del empleado desde el prop recibido
      axios.get(apiUrl)
        .then((response) => {
          this.empleado = response.data;  // Guardamos la respuesta en la propiedad "empleado"
          //console.log("🚀 ~ .then ~ response.data:", response.data)

        })
        .catch((error) => {
          console.error('Error al obtener los datos laborales del empleado:', error);
        });
    },
  },
};
</script>

<style scoped>
.section-tabs {
  display: flex;
  margin-bottom: 15px;
}

.section-tabs button {
  background: none;
  border: 1px solid #ddd;
  padding: 10px 15px;
  cursor: pointer;
  font-size: 16px;
  color: #007bff;
  transition: background 0.3s ease;
  flex: 1;
}

.section-tabs button.active {
  background-color: #007bff;
  color: white;
  font-weight: bold;
}

button {
  margin-top: 10px;
}


</style>
