                    <br />
                  </div>
                </span>
                <!-- Switch que activa/desactiva el input -->

              </div>
              <input v-model="descuentoAusencia" type="number" id="descuentoAusencia" />

            </div>

            <br>
            <label for="descuentoAusenciaA">Descuento por Ausencia Asim.</label>

            <div class="switch-container">
              <!-- Input de pagoHoraExtra -->
              <div class="horas-extra-tooltip-wrapper" ref="tooltipWrapper" :class="tooltipPositionClass"
                @mouseenter="recalcularPosicionTooltip">
                <span class="horas-extra-tooltip-icon">ℹ️
                  <div class="horas-extra-tooltip-content">
                    <strong>Descuento por Ausencia Asimilada:</strong> Establece el valor que se aplicará como descuento al empleado en caso de ausencias injustificadas durante el periodo de nómina.
                    .<br />
                  </div>
                </span>
                <!-- Switch que activa/desactiva el input -->

              </div>
              <input v-model="descuentoAusenciaA" type="number" id="descuentoAusenciaA" />

            </div>
          </div>

          <div class="input-item" title="Ingrese  la  cantidad  de  dias  de vacaciones disponibles">
            <div style="margin-bottom: 1rem;">

              <label for="vacacionesDisponibles">Vacaciones disponibles</label>
