            </div>
            <div style="margin-bottom: 1rem;">

              <label for="infonavit" title="Ingrese el porcentaje  que  aplicara  a la prima vacacional(Si aplica)">%
                Prima Vacacional</label><br>
              <div class="input-with-icon">
                <input v-model="primaVacacional" type="number" id="primaVacacional" placeholder="Ingrese el porcentaje"
                  step="0.01" />
                <i class="fas fa-percent"> </i> <!-- Icono de porcentaje -->
              </div>
            </div>
          </div>

          <div class="input-item" title="Ingrese la cantidad de días de vacaciones disponibles">
            <div style="margin-bottom: 1rem;">
              <label for="diasAguinaldo">Días Aguinaldo</label>
