            </div>
            <div style="margin-bottom: 1rem;">
              <label for="prestamoPendiente">Préstamo Pendiente</label> <br>
              <input v-model="prestamoPendiente" type="number" id="prestamoPendiente" />
            </div>
          </div>

        </div>
      </section>
      <!-- Porcentajes  -->

      <!-- Régimen y Jornada -->
      <section class="section">
        <h4 class="section-title">Regimen y Jornada</h4>
        <div class="input-group">
          <div class="input-item">
            <label for="tipoRegimen">Tipo de Régimen</label>
            <select v-model="tipoRegimen" id="tipoRegimen">
              <option value="0">Ninguno</option>
