              placeholder="Ej. Administrativo, Operativo, etc." />
          </div>

          <!-- Periodicidad de Pago (catálogo SAT) -->
          <div class="input-item">
            <label for="periodicidadPago">Periodicidad de Pago</label>
            <select v-model="periodicidadPago" id="periodicidadPago">
              <option value="01">Diario</option>
              <option value="02">Semanal</option>
              <option value="03">Quincenal</option>
              <option value="04">Mensual</option>
