              <option value="06">Unidad obra</option>
              <option value="07">Comisión</option>
              <option value="08">Precio alzado</option>
              <option value="09">Otra Periodicidad</option>
            </select>
          </div>

          <!-- Tipo de Nómina (visible o invisible según lógica de negocio) -->
          <div class="input-item">
            <label for="sindicato">Pertenece a algun sindicato?</label>
            <select v-model="sindicato" id="sindicato">
              <option value="SI">Si</option>
              <option value="NO">No</option>
            </select>
          </div>
        </div>
      </section>

      <!-- Días de Descanso -->
      <section class="section">
        <h4 class="section-title">Días de Descanso</h4>
        <div class="dias-descanso">
          <label v-for="dia in diasSemana" :key="dia" :class="{ 'selected': diasDescanso.includes(dia) }"
            @click="toggleDiaDescanso(dia)" class="dia-button">
            {{ dia }}
          </label>
        </div>
      </section>

      <!-- Botones de acción -->
      <!-- Botones de acción -->
      <template v-if="idRol == 6 || idRol == 9 || idRol == 1 || idRol == 10">
        <div class="form-actions">
          <!-- Si hay datos en laborales, muestra "Actualizar" -->
          <button v-if="tieneDatosLaborales" @click="actualizarLaborales()" class="btn btn-primary">
            Actualizar
          </button>

          <!-- Si laborales está vacío, muestra "Registrar" -->
          <button v-else @click="guardarLaborales()" class="btn btn-success">
