<script>
import Swal from 'sweetalert2';
import axios from 'axios';

export default {
  props: {
    empleado: {
      type: Object,
      default: () => ({ laborales: {} })
    },
    idRol: {
      type: String,
      default: ''
    },
    idCliente: {
      type: String,
      default: ''
    }
  },
  data() {

    return {
      tooltipPositionClass: '', // '', 'left', 'top', etc.
      showModal: true,
      pagoHoraExtra: 0.00,
      pagoHoraExtraA: 0.00,
      sueldoDiario: 0.00,
      sueldoDiarioAsimilado: 0.00,
      horasExtraActivadas: false,
      horasExtraActivadasA: false,
      pagoDiaFestivoActivado: false,
      pagoDiaFestivoActivadoA: false,
      pagoDiaFestivo: 0.00,
      pagoDiaFestivoA: 0.00,
      prestamoPendiente: 0.00,
      vacacionesDisponibles: 0,//this.calcularVacaciones(fechaIngreso, diasVacacionesGuardados),
      puesto: '',
      creacion: '',
      tipoContrato: 'indefinido',
      otroTipoContrato: '',
      infonavit: 0,
      diasAguinaldo: 15,
      descuentoAusencia: 0,
      descuentoAusenciaA: 0,
      primaVacacional: 0,
      imss: 0,
      tipoRegimen: '0',
      tipoJornada: 'ninguno',
      horasDia: 0,
      grupoNomina: 'quincenal',
      periodicidadPago: 'quincenal',
      sindicato: 'NO',
      diasDescanso: []
    };
  },
  computed: {

    diasSemana() {
      return ['Lunes', 'Martes', 'Miércoles', 'Jueves', 'Viernes', 'Sábado', 'Domingo'];
    },
    tieneDatosLaborales() {
      return this.empleado?.laborales ? Object.keys(this.empleado.laborales).length > 0 : false;
    },
    sueldoMes() {
      return (this.toNumber(this.sueldoDiario) + this.toNumber(this.sueldoDiarioAsimilado)) * 30;
    },

    pagoHoraExtraComputed: {
      // Getter: calcula el pago por hora extra
      get() {
        if (this.horasExtraActivadas && this.pagoHoraExtra !== null) {
          return this.pagoHoraExtra;
        }
        return this.sueldoDiario / this.horasDia;
      },
      // Setter: actualiza el pago por hora extra
      set(nuevoPagoHoraExtra) {
        if (this.horasExtraActivadas) {
          this.pagoHoraExtra = nuevoPagoHoraExtra;
        } else {
          this.pagoHoraExtra = null;
        }
      },
    },
    pagoHoraExtraComputedA: {
      // Getter: calcula el pago por hora extra
      get() {
        if (this.horasExtraActivadasA && this.pagoHoraExtraA !== null) {
          return this.pagoHoraExtraA;
        }
        return this.sueldoDiarioAsimilado / this.horasDia;
      },
      // Setter: actualiza el pago por hora extra
      set(nuevoPagoHoraExtraA) {
        if (this.horasExtraActivadasA) {
          this.pagoHoraExtraA = nuevoPagoHoraExtraA;
        } else {
          this.pagoHoraExtraA = null;
        }
      },
    }

  },
  methods: {
    calcularVacaciones(fechaIngreso, diasVacacionesGuardados = null) {
      if (diasVacacionesGuardados !== null && diasVacacionesGuardados !== undefined) {
        return diasVacacionesGuardados;
      }

      if (!fechaIngreso) return 0;

      const hoy = new Date();
      const ingreso = new Date(fechaIngreso);
      const añosTrabajados = hoy.getFullYear() - ingreso.getFullYear();

      const yaCumplioEsteAño = (
        hoy.getMonth() > ingreso.getMonth() ||
        (hoy.getMonth() === ingreso.getMonth() && hoy.getDate() >= ingreso.getDate())
      );

      const antiguedad = yaCumplioEsteAño ? añosTrabajados : añosTrabajados - 1;

      if (antiguedad < 1) return 0;
      if (antiguedad === 1) return 12;
      if (antiguedad <= 5) return 12 + (antiguedad - 1) * 2;

      const extra = Math.floor((antiguedad - 6) / 5) * 2;
      return 20 + extra;
    },

    recalcularPosicionTooltip() {
      this.$nextTick(() => {
        const wrapper = this.$refs.tooltipWrapper;
        const tooltip = wrapper.querySelector('.horas-extra-tooltip-content');

        if (!tooltip) return;

        const wrapperRect = wrapper.getBoundingClientRect();
        const tooltipRect = tooltip.getBoundingClientRect();
        const espacioDerecha = window.innerWidth - wrapperRect.right;
        const espacioIzquierda = wrapperRect.left;
        const espacioArriba = wrapperRect.top;
        const espacioAbajo = window.innerHeight - wrapperRect.bottom;

        // Posición horizontal
        if (espacioDerecha < tooltipRect.width && espacioIzquierda > tooltipRect.width) {
          this.tooltipPositionClass = 'left';
        } else {
          this.tooltipPositionClass = '';
        }

        // Posición vertical (opcional)
        if (espacioAbajo < tooltipRect.height && espacioArriba > tooltipRect.height) {
          this.tooltipPositionClass += ' top';
        }
      });
    },
    toNumber(value) {
      const n = parseFloat(value);
      return isNaN(n) ? 0 : n;
    },
    updateHorasExtra() {
      if (this.horasExtraActivadas) {
        // Si el switch está activado, permite al usuario ingresar el valor de pagoHoraExtra
        this.pagoHoraExtra = this.pagoHoraExtra || 0; // Asigna 0 si está vacío
      } else {
        // Si el switch está desactivado, calcula el pagoHoraExtra en base al sueldoDiario y horasDia
        this.pagoHoraExtra = this.sueldoDiario / this.horasDia;
      }
    },
    updateHorasExtraA() {
      if (this.horasExtraActivadasA) {
        // Si el switch está activado, permite al usuario ingresar el valor de pagoHoraExtra
        this.pagoHoraExtraA = this.pagoHoraExtraA || 0; // Asigna 0 si está vacío
      } else {
        // Si el switch está desactivado, calcula el pagoHoraExtra en base al sueldoDiario y horasDia
        this.pagoHoraExtraA = this.sueldoDiarioAsimilado / this.horasDia;
      }
    },
    updateDiaFestivo() {
      if (this.pagoDiaFestivoActivado) {
        // Si ya hay un valor (no null ni undefined), lo dejamos; si no, asignamos 0
        this.pagoDiaFestivo = this.pagoDiaFestivo != null ? this.pagoDiaFestivo : 0;
      } else {
        // Desactivado: cálculo automático
        this.pagoDiaFestivo = this.sueldoDiario * 3;
      }
    },


    updateDiaFestivoA() {
      if (this.pagoDiaFestivoActivadoA) {
        this.pagoDiaFestivoA = this.pagoDiaFestivoA != null ? this.pagoDiaFestivoA : 0;
      } else {
        this.pagoDiaFestivoA = this.sueldoDiarioAsimilado * 3;
      }
    },


    async guardarLaborales() {
      const datos = this.obtenerDatosLaborales();
      console.log(Object.isFrozen(datos));
      datos.id_empleado = this.empleado.id;
      //console.log(this.empleado.id);

      try {

        const response = await fetch(`${this.$apiUrl}/empleados/laborales`, {
          method: "POST",
          headers: {
            "Content-Type": "application/json",
          },
          body: JSON.stringify(datos),
        });

        const resultado = await response.json();
        if (response.ok) {
          Swal.fire({
            icon: 'success',
            title: 'Éxito',
            text: 'Datos laborales guardados exitosamente.',
            confirmButtonText: 'Aceptar'
          });
          this.$emit('datosActualizados');
        } else {
          console.error("Error al guardar datos:", resultado);
          Swal.fire({
            icon: 'error',
            title: 'Error',
            text: 'Error al guardar datos laborales.',
            confirmButtonText: 'Aceptar'
          });
        }
      } catch (error) {
        console.error("Error en la solicitud:", error);
        Swal.fire({
          icon: 'error',
          title: 'Ocurrió un error',
          text: 'Ocurrió un error al intentar actualizar los datos.',
          confirmButtonText: 'Aceptar'
        });
      }
    },

    async actualizarLaborales() {
      const datos = this.obtenerDatosLaborales();
      datos.id_empleado = this.empleado.id;
      console.log("🚀 ~ actualizarLaborales ~ datos:", datos)
      console.log(this.empleado.id);
      try {

        const response = await fetch(`${this.$apiUrl}/empleados/laborales/${this.empleado.id}`, {
          method: "PUT",
          headers: {
            "Content-Type": "application/json",
          },
          body: JSON.stringify(datos),
        });

        const resultado = await response.json();
        if (response.ok) {
          Swal.fire({
            icon: 'success',
            title: 'Éxito',
            text: 'Datos laborales actualizados exitosamente.',
            confirmButtonText: 'Aceptar'
          });
          this.$emit('datosActualizados');


        } else {
          console.error("Error al actualizar datos:", resultado);
          Swal.fire({
            icon: 'error',
            title: 'Error',
            text: 'Error al actualizar datos laborales.',
            confirmButtonText: 'Aceptar'
          });
        }
      } catch (error) {
        console.error("Error en la solicitud:", error);
        Swal.fire({
          icon: 'error',
          title: 'Ocurrió un error',
          text: 'Ocurrió un error al intentar actualizar los datos.',
          confirmButtonText: 'Aceptar'
        });
      }
    },

    obtenerDatosLaborales() {

      return {
        tipoContrato: this.tipoContrato === "otro" ? this.otroTipoContrato : this.tipoContrato,
        sueldoDiario: this.sueldoDiario,
        sueldoDiarioAsimilado: this.sueldoDiarioAsimilado,
        sueldoMes: (Number(this.sueldoDiario) + Number(this.sueldoDiarioAsimilado)) * 30,
        pagoHoraExtra: this.pagoHoraExtraComputed,
        pagoHoraExtraA: this.pagoHoraExtraComputedA,
        pagoDiaFestivo: this.pagoDiaFestivo,
        pagoDiaFestivoA: this.pagoDiaFestivoA,
        vacacionesDisponibles: this.vacacionesDisponibles,
        diasAguinaldo: this.diasAguinaldo,
        descuentoAusencia: this.descuentoAusencia,
        descuentoAusenciaA: this.descuentoAusenciaA,
        primaVacacional: this.primaVacacional,
        infonavit: this.infonavit,
        imss: this.imss,
        tipoRegimen: this.tipoRegimen,
        tipoJornada: this.tipoJornada,
        horasDia: this.horasDia,
        grupoNomina: this.grupoNomina,
        periodicidadPago: this.periodicidadPago,
        sindicato: this.sindicato,
        diasDescanso: this.diasDescanso,
        prestamoPendiente: this.prestamoPendiente,


      };
    },
    esValorValido(valor) {
      return valor !== undefined && valor !== null && valor !== '' && !Number.isNaN(valor);
    },
    cargarDatos() {
      if (!this.empleado?.laborales) return;

      const { laborales } = this.empleado;

      this.sueldoDiario = this.esValorValido(laborales.sueldo_diario) ? laborales.sueldo_diario : 0.00;
      this.sueldoDiarioAsimilado = this.esValorValido(laborales.sueldo_asimilado) ? laborales.sueldo_asimilado : 0.00;

      this.horasExtraActivadas = this.esValorValido(laborales.pago_hora_extra);
      this.pagoHoraExtra = this.horasExtraActivadas ? laborales.pago_hora_extra : null;

      this.horasExtraActivadasA = this.esValorValido(laborales.pago_hora_extra_a);
      this.pagoHoraExtraA = this.horasExtraActivadasA ? laborales.pago_hora_extra_a : null;

      this.pagoDiaFestivoActivado = this.esValorValido(laborales.pago_dia_festivo);
      this.pagoDiaFestivo = this.pagoDiaFestivoActivado ? laborales.pago_dia_festivo : null;

      this.pagoDiaFestivoActivadoA = this.esValorValido(laborales.pago_dia_festivo_a);
      this.pagoDiaFestivoA = this.pagoDiaFestivoActivadoA ? laborales.pago_dia_festivo_a : null;

      const fechaIngreso = this.empleado?.creacion?.split(' ')[0];
      this.vacacionesDisponibles = this.esValorValido(laborales.vacaciones_disponibles)
        ? laborales.vacaciones_disponibles
        : this.calcularVacaciones(fechaIngreso);

      this.tipoContrato = laborales.tipo_contrato || '';
      this.otroTipoContrato = laborales.otro_tipo_contrato || '';
      this.diasAguinaldo = laborales.dias_aguinaldo ?? 0;
      this.descuentoAusencia = laborales.descuento_ausencia ?? 0;
      this.descuentoAusenciaA = laborales.descuento_ausencia_a ?? 0;
      this.primaVacacional = laborales.prima_vacacional ?? 0;
      this.infonavit = laborales.infonavit ?? 0.00;
      this.imss = laborales.imss ?? 0.00;
      this.tipoRegimen = laborales.tipo_regimen || '';
      this.tipoJornada = laborales.tipo_jornada || 'ninguno';
      this.horasDia = laborales.horas_dia ?? 0;
      this.grupoNomina = laborales.grupo_nomina || '';
      this.periodicidadPago = laborales.periodicidad_pago || '';
      this.sindicato = laborales.sindicato || '';
      this.prestamoPendiente = laborales.prestamo_pendiente ?? 0;
      this.diasDescanso = this.esValorValido(laborales.dias_descanso)


        ? JSON.parse(laborales.dias_descanso)
        : [];
    },


    closeModal() {
      this.$emit('cerrar');
    },
    toggleDiaDescanso(dia) {
      this.diasDescanso.includes(dia)
        ? this.diasDescanso = this.diasDescanso.filter(d => d !== dia)
        : this.diasDescanso.push(dia);
    },
    async uploadFile() {
      if (!this.excelFile) {
        Swal.fire({
          icon: 'warning',
          title: 'Archivo no seleccionado',
          text: 'Por favor selecciona un archivo antes de subir.'
        });
        return;
      }

      const formData = new FormData();
      formData.append('file', this.excelFile);
      formData.append('id_rol', this.idRol);
      formData.append('id_cliente', this.idCliente);

      try {
        const response = await axios.post(`${this.$apiUrl}/upload-laborales-info`, formData, {
          headers: {
            'Content-Type': 'multipart/form-data'
          }
        });

        Swal.fire({
          icon: 'success',
          title: 'Éxito',
          text: response.data.message || "Archivo subido y datos actualizados con éxito."
        }).then(() => {
          // Emitir evento solo después de que el usuario acepte el mensaje
          this.$emit('datosActualizados');
          this.isDatosLaboralesVisible = true;
        });


      } catch (error) {
        console.error(error);
        Swal.fire({
          icon: 'error',
          title: 'Error',
          text: error.response?.data?.error || "Hubo un error al subir el archivo."
        });
      }
    },
    cargarExcel(event) {
      this.excelFile = event.target.files[0];
      //console.log('Archivo seleccionado:', this.excelFile);
    },
    descargarPlantillaExcel() {
      const idCliente = this.empleado.id_cliente;

      if (!idCliente) {
        Swal.fire({
          icon: 'error',
          title: 'Error',
          text: 'No se pudo descargar recarga la pagina e intentalo nuevamenmte.'
        });
        return;
      }

      axios.get(`${this.$apiUrl}/download-template-laborales`, {
        params: { id_cliente: idCliente },
        responseType: 'blob',
      }).then((response) => {
        const url = window.URL.createObjectURL(new Blob([response.data]));
        const link = document.createElement('a');
        link.href = url;
        link.setAttribute('download', 'Laborales_empleados.xlsx');
        document.body.appendChild(link);
        link.click();
        document.body.removeChild(link);
      })
        .catch((error) => {
          console.error('Error al descargar el archivo CSV:', error);
          Swal.fire({
            icon: 'error',
            title: 'Descarga fallida',
            text: 'No se pudo descargar la plantilla.'
          });
        });
    },
  },
  watch: {
    empleado: {
      handler() {
        console.log("Empleado actual:", this.empleado);

        this.cargarDatos();

      },
      deep: true,
      immediate: true
    },
    horasExtraActivadas(newValue) {
      if (!newValue) {
        this.pagoHoraExtra = null; // Restablece el valor cuando el switch se desactiva
      }

    },
    horasExtraActivadasA(newValue) {
      if (!newValue) {
        this.pagoHoraExtraA = null; // Restablece el valor cuando el switch se desactiva
      }

    },

  },
}
</script>


