<template>
  <div>
    <GeneralInfoForm 
      :empleadoData="empleadoData" 
      :idRol="idRol"
      :idCliente="idCliente"
      @close="handleClose" 
      @empleadoActualizado="handleEmpleadoActualizado" 
    />
  </div>
</template>

<script>
import GeneralInfoForm from './GeneralInfoForm.vue';

export default {
  props: {
    empleadoData: {
      type: Object,
      required: true
    },
    idRol: {
      type: String,
      default: ''
    },
     idCliente: {
      type: String,
      default: ''
    },
  },
  components: {
    GeneralInfoForm
  },
  methods: {
    handleClose() {
      this.$emit('close'); // Emite el evento close al padre
    },
    handleEmpleadoActualizado(updatedData) {
      // Aquí puedes manejar lo que sucede cuando un empleado es actualizado
      this.$emit('empleadoActualizado', updatedData);
      // Puedes hacer otras acciones aquí, como actualizar el estado en el padre
    }
  }
};
</script>


