<template>
  <div>
    <p>
      Esta sección permite gestionar los documentos esenciales del expediente de empleados, asignar fechas de
      vencimiento y configurar recordatorios automáticos para garantizar un control efectivo y evitar que pierdan
      vigencia.
    </p>
    <div class="header">
      <input type="text" class="search" placeholder="Buscar" v-model="searchQuery" />
      <template v-if="idRol == 6 || idRol == 9 || idRol == 1">
        <div class="tooltip-wrapper">
          <button class="tooltip-exams" @click="openAddOpcionModal">Lista Opciones</button>
          <span class="tooltip-text">
            En este formulario puedes agregar, editar o eliminar tu listado de opciones precargados para nombrar los
            documentos.
          </span>
        </div>
        <button @click="openAddDocumentModal">Agregar Documento</button>

      </template>
    </div>
    <!-- Mensaje cuando no hay documentos -->
    <div v-if="filteredDocuments.length === 0" class="no-documents-message">
      <p>No hay documentos cargados aún.</p>
    </div>
    <div class="document-table-wrapper">
      <table class="document-table">
        <thead>
          <tr>
            <th>Nombre</th>
            <th>Fecha de Subida</th>
            <th>Fecha de Vencimiento</th>
            <th>Días Restantes</th>
            <th>Acciones</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(doc, index) in filteredDocuments" :key="index">
            <td style="position: relative;">
              {{ doc.optionName }} {{ doc.nameAlterno }}
              <span class="tooltipDocuments">{{ doc.description }}</span>
            </td>
            <td>{{ doc.upload_date }}</td>
            <td>
              {{ doc.expiry_date }}
              <div style="position: relative; display: inline-block;">
                <template v-if="idRol == 6 || idRol == 9 || idRol == 1">

                  <i class="fas fa-sync" @click="openUpdateModal(doc)"
                    style="cursor: pointer; margin-left: 30px; color: #59ae38; font-size: 1.5em;"></i>
                  <span class="tooltipDocuments">Actualizar</span>

                  <span v-if="doc.estado === 'amarillo' || doc.estado === 'rojo'" class="estadoDocumento"
                    :class="doc.estado"> </span>

                </template>
              </div>
            </td>
            <td>
              <div :style="getStyle(calculateDaysRemaining(doc.expiry_date, doc.expiry_reminder), doc.expiry_reminder)"
                class="tooltipDocuments-container" style="position: relative;">

                Días <strong>{{ calculateDaysRemaining(doc.expiry_date, doc.expiry_reminder) }}</strong>
                <span class="tooltipDocuments">Recordarme {{ doc.expiry_reminder }} días antes</span>
              </div>
            </td>
            <td>
              <template v-if="doc.nameDocument && doc.nameDocument.includes('_sin_')">
                <!-- contenido cuando incluye '_sin_' -->
              </template>
              <template v-else>
                <a :href="doc.url" target="_blank">
                  <i class="fas fa-eye" @click="openDocument(doc)" title="Ver Documento"></i>
                </a>
              </template>

              <span>&nbsp;&nbsp;</span>
              <template v-if="idRol == 6 || idRol == 9 || idRol == 1">
                <i class="fas fa-trash" @click="deleteDocument(doc)" title="Eliminar  documento"></i>

              </template>
            </td>
          </tr>
        </tbody>
      </table>
    </div>

    <!-- Modal para AGREGAR -->
    <AddDocumentsModal v-if="isAddModalVisible" :isVisible="isAddModalVisible" :employeeId="empleadoData.id"
      :id_portal="empleadoData.id_portal" @close="closeAddDocumentModal" @save="saveDocument"
      @documentoSubido="fetchDocuments" :endpointConfig="{ tabla: '_documentEmpleado', endpoint: '/documents' }" />

    <!-- Modal para ACTUALIZAR -->
    <AddDocumentModal v-if="isUpdateModalVisible" :isVisible="isUpdateModalVisible" :employeeId="empleadoData.id"
      :id_portal="empleadoData.id_portal" @close="closeUpdateModal" @save="saveDocument"
      @documentoSubido="fetchDocuments" :endpointConfig="{ tabla: '_documentEmpleado', endpoint: '/documents' }"
      :documentToEdit="selectedDocument" />

    <!-- Componente ModalOpciones -->
    <AddOpcionModal v-if="isAddOpcionModalVisible" :isVisible="isAddOpcionModalVisible" @close="closeOptionModal"
      :idPortal="empleadoData.id_portal" :idCliente="this.idCliente" tabla="_documentEmpleado"
      @cerrar="cerrarModal('opciones')" />



  </div>
</template>


<script>
import AddDocumentsModal from './AddDocumentsModal.vue';
import AddDocumentModal from './AddDocumentModal.vue';
import AddOpcionModal from '../modales/AddOpcionModal.vue';


import Swal from 'sweetalert2';
import { EventBus } from '@/eventBus';

export default {
  components: {
    AddDocumentsModal,
    AddDocumentModal,
    AddOpcionModal
  },
  props: {
    empleadoData: Object,
    idPortal: {
      type: String,
      default: ''
    },
    idUsuario: {
      type: String,
      default: ''
    },
    idCliente: {
      type: String,
      default: ''
    },
    idRol: {
      type: String,
      default: ''
    },
  },
  data() {
    return {
      isAddModalVisible: false,       // Para agregar documento
      isUpdateModalVisible: false,
      isAddOpcionModalVisible: false,
      newExpiryDate: '',
      status: 1,
      documents: [],
      selectedDocument: null,
      selectedReminder: '0',
      customReminder: '',
      isCustomReminder: false,
      searchQuery: '',
    };
  },
  computed: {
    filteredDocuments() {
      if (!this.documents) return []; // Asegura que documents esté definido

      const query = this.searchQuery.toLowerCase();
      return this.documents.filter(doc => {
        const optionName = doc.optionName ? doc.optionName.toLowerCase() : '';
        const uploadDate = doc.upload_date ? doc.upload_date.toLowerCase() : '';
        const expiryDate = doc.expiry_date ? doc.expiry_date.toString() : '';

        return optionName.includes(query) ||
          uploadDate.includes(query) ||
          expiryDate.includes(query);
      });
    }
  },
  mounted() {
    this.fetchDocuments();
  },
  watch: {
    documents: {
      handler(newDocs) {
        newDocs.forEach(doc => {
          doc.daysRemaining = this.calculateDaysRemaining(doc.expiry_date, doc.expiry_reminder);
        });
      },
      deep: true,
    },
    newExpiryDate() {
      if (this.selectedDocument) {
        this.selectedDocument.daysRemaining = this.calculateDaysRemaining(this.newExpiryDate, this.isCustomReminder ? this.customReminder : this.selectedReminder);
      }
    },
    customReminder() {
      if (this.selectedDocument) {
        this.selectedDocument.daysRemaining = this.calculateDaysRemaining(this.selectedDocument.expiry_date, this.isCustomReminder ? this.customReminder : this.selectedReminder);
      }
    },
    selectedReminder() {
      if (this.selectedDocument) {
        this.selectedDocument.daysRemaining = this.calculateDaysRemaining(this.selectedDocument.expiry_date, this.isCustomReminder ? this.customReminder : this.selectedReminder);
      }
    },
  },
  methods: {

    openDocument(doc) {
      EventBus.$emit('open-document', doc, '_documentEmpleado');  // Abre el documento en una nueva pestaña
    },




    openUpdateModal2(doc) {
      //console.log("🚀 ~ openUpdateModal ~ doc:", doc)
      this.selectedDocument = doc;
      this.status = doc.status;
      this.newExpiryDate = doc.expiry_date;
      this.selectedReminder = doc.expiry_reminder || '';
      const validReminders = [0, 1, 7, 15, 30];

      // Verificamos si selectedReminder es uno de los valores válidos numéricos
      if (!validReminders.includes(Number(this.selectedReminder))) {
        // Si no es un valor válido, asignamos 'custom' y el valor original a customReminder
        this.customReminder = this.selectedReminder;  // Guardamos el valor original como 'custom'
        this.selectedReminder = 'custom'; // Marcamos como 'custom'
      } else {
        // Si es un valor válido, usamos el valor numérico
        this.customReminder = '';  // No hay necesidad de customReminder si el valor es válido
      }
      this.isCustomReminder = this.selectedReminder === 'custom';
      this.customReminder = this.selectedReminder !== 'custom' ? this.selectedReminder : '';
      this.isUpdateModalVisible = true;
    },



    async submitUpdate() {
      if (this.newExpiryDate) {
        try {
          const response = await fetch(`${this.$apiUrl}/documents/${this.selectedDocument.id}`, {
            method: 'PUT',
            headers: {
              'Content-Type': 'application/json',
            },
            body: JSON.stringify({
              table: 'documents_empleado',
              expiry_date: this.newExpiryDate,
              expiry_reminder: this.isCustomReminder ? this.customReminder : this.selectedReminder,
              status: this.status,
            }),
          });

          if (!response.ok) {
            throw new Error('Error al actualizar la fecha de vencimiento');
          }

          // Actualizar la fecha localmente (opcional si solo se quiere actualizar desde el servidor)
          this.selectedDocument.expiry_date = this.newExpiryDate;
          this.selectedDocument.expiry_reminder = this.isCustomReminder ? this.customReminder : this.selectedReminder;

          // Recargar los documentos para que la tabla se actualice
          await this.fetchDocuments();

          // Alerta de éxito
          await Swal.fire({
            icon: 'success',
            title: '¡Éxito!',
            text: 'La fecha de vencimiento se actualizó correctamente.',
            confirmButtonText: 'Aceptar'
          });

          this.closeUpdateModal();
        } catch (error) {
          console.error(error);
          await Swal.fire({
            icon: 'error',
            title: '¡Error!',
            text: 'No se pudo actualizar la fecha de vencimiento.',
            confirmButtonText: 'Aceptar'
          });
        }
      }
    },

    calculateDaysRemaining(expiryDate, daysBefore) {

      const today = new Date();
      // Ajusta la fecha de hoy a medianoche
      today.setHours(0, 0, 0, 0);

      const expiry = new Date(expiryDate);
      // Ajusta la fecha de expiración a medianoche
      expiry.setHours(0, 0, 0, 0);

      // Verifica si daysBefore es null
      if (daysBefore === null || daysBefore == 0) {
        return 'No Notificar'; // Devuelve un valor en blanco
      }

      // Calcula la fecha de advertencia
      const warningDate = new Date(expiry);
      warningDate.setDate(warningDate.getDate() - daysBefore); // Usa daysBefore aquí

      // Calcula los días restantes
      const timeDiff = expiry - today;
      const retornar = Math.ceil(timeDiff / (1000 * 3600 * 24)) + 1;
      // console.log("🚀 ~ calculateDaysRemaining ~ retornar:", retornar)
      return retornar; // Días restantes
    },


    getStyle(timeDiff, daysBefore) {

      if (daysBefore === null || daysBefore == 0) {
        return {
          backgroundColor: '#ccffcc',
          color: 'black',
          padding: '10px',
          borderRadius: '5px',
        };
      }

      // Verificar si la fecha de expiración ha pasado o es hoy
      if (timeDiff <= 0) {
        return {
          backgroundColor: 'rgba(255, 0, 0, 0.906)', // rojo tenue
          color: 'white',
          padding: '10px',
          borderRadius: '5px',
        };
      }

      // Verificar si la diferencia en días es igual a daysBefore
      if (timeDiff <= daysBefore) {
        return {
          backgroundColor: 'rgba(255, 0, 0, 0.906)', // rojo tenue (advertencia)
          color: 'white',
          padding: '10px',
          borderRadius: '5px',
        };
      }

      // Verificar si está dentro de un rango de 5 días antes de daysBefore
      if (timeDiff > daysBefore && timeDiff <= (daysBefore + 5)) {
        return {
          backgroundColor: 'rgba(205, 202, 0, 0.915)', // amarillo tenue
          padding: '10px',
          color: 'white',
          borderRadius: '5px',
        };
      }

      // Si no cumple ninguna de las condiciones anteriores, es verde
      return {
        backgroundColor: 'rgba(12, 232, 34, 0.922)', // verde tenue
        color: 'white',
        padding: '10px',
        borderRadius: '5px',
      };
    },

    async fetchDocuments() {
      try {
        const response = await fetch(`${this.$apiUrl}/documents/${this.empleadoData.id}`);


        if (!response.ok) {
          throw new Error('No se pudo obtener los documentos');
        }

        const data = await response.json();
        console.log('Fetched documents:', data); // Log de los documentos obtenidos

        this.documents = data.documentos.map(doc => ({
          ...doc,
          daysRemaining: this.calculateDaysRemaining(doc.expiry_date, doc.expiry_reminder),
          estado: doc.status == 2 ? 'amarillo' : (doc.status == 3 ? 'rojo' : '')
        }));
        console.log("Cursos recibidos:", data);
        //this.filteredDocuments = this.documents; // Inicializa filteredDocuments con los documentos obtenidos

      } catch (error) {
        console.error('Error fetching documents:', error); // Log del error
        // Inicializa filteredDocuments con documentos de ejemplo
      }
    },

    openAddDocumentModal() {
      this.isAddModalVisible = true;
    },
    closeAddDocumentModal() {
      this.isAddModalVisible = false;
      this.fetchDocuments();
    },
    openAddOpcionModal() {
      this.isAddOpcionModalVisible = true;
    },
    closeOptionModal() {
      this.isAddOpcionModalVisible = false;
      this.fetchDocuments();
    },
    openUpdateModal(doc) {
      this.selectedDocument = doc;
      this.isUpdateModalVisible = true;
    },

    closeUpdateModal() {
      this.isUpdateModalVisible = false;
      this.selectedDocument = null;
      this.fetchDocuments();
    },
    saveDocument(formData) {
      this.$emit('add-document', formData);
      this.closeAddDocumentModal();
    },
    deleteDocument(doc) {
      Swal.fire({
        title: '¿Estás seguro?',
        text: "Este documento será eliminado permanentemente.",
        icon: 'warning',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        confirmButtonText: 'Sí, Borralo!',
        cancelButtonText: 'Cancelar',
      }).then(async (result) => {
        if (result.isConfirmed) {
          try {
            // Log the document name

            const response = await fetch(`${this.$apiUrl}/documents`, {
              method: 'DELETE',
              headers: {
                'Content-Type': 'application/json',
              },
              body: JSON.stringify({
                id: doc.id,
                nameDocument: doc.nameDocument,
                tabla: 'documentos',
              }),
            });

            if (!response.ok) {
              throw new Error('Error al eliminar el documento');
            }

            // Update the document list
            this.documents = this.documents.filter(d => d.id !== doc.id);
            Swal.fire('¡Eliminado!', 'El documento ha sido eliminado.', 'success');
          } catch (error) {
            Swal.fire('¡Error!', error.message, 'error');
          }
        }
      });
    },

    updateReminder() {
      if (this.selectedReminder === 'custom') {
        this.isCustomReminder = true;
      } else {
        this.isCustomReminder = false;
        this.customReminder = this.selectedReminder;
      }
    },
  },
};
</script>




