<template>
  <div>
    <MedicalInfoForm 
      :empleadoData="empleadoData" 
      :idRol="idRol" 
      :idCliente="idCliente" 
      @close="handleClose" 
      @updateEmpleado="handleUpdateEmpleado" 
    />
  </div>
</template>

<script>
import MedicalInfoForm from './MedicalInfoForm.vue';

export default {
  props: {
    empleadoData: Object,
    idRol: {
      type: String,
      default: ''
    },
    idCliente: {
      type: String,
      default: ''
    },
    updateEmpleado: Function
  },
  components: {
    MedicalInfoForm 
  },
  methods: {
    handleUpdateEmpleado(updatedData) {
      // Maneja la actualización del empleado aquí
      this.$emit('updateEmpleado', updatedData); // O cualquier otra lógica que necesites
    },
    handleClose() {
      this.$emit('close'); // Emite el evento close al padre
    }
  }
};
</script>

