<template>
  <div class="modal">
    <div class="modal-content">
      <span class="close" @click="$emit('close')">&times;</span>
      <h2>Solicitar Estudios a RODI</h2>

      <form @submit.prevent="submitRequest">
        <div>
          <label for="socioeconomic-project">Proyecto Socioeconomico:</label>
          <select v-model="selectedProject" id="socioeconomic-project" @change="updateDescription">
            <option value="">N/A</option>
            <option v-for="project in projects" :key="project.id" :value="project.id">
              {{ project.proyecto }}
            </option>
          </select>
          <ul v-if="selectedDescription" class="description-list">

            <li v-for="(line, index) in selectedDescription.split('\n')" :key="index">* {{ line }}</li>
          </ul>
        </div>


        <div>
          <label for="antidoping-package">Antidoping Paquete:</label>
          <select v-model="selectedPackage" id="antidoping-package" @change="updatePackageInfo">
            <option value=null>N/A</option>
            <option v-for="paquete in antidopingPackages" :key="paquete.id" :value="paquete.id">
              {{ paquete.nombre }}
            </option>
          </select>
          <p>{{ selectedPackageInfo }}</p>
        </div>

        <div>
          <label for="psychometric-test">Examen Psicometrico:</label>
          <select v-model="selectedPsychometric" id="psychometric-test">
            <option value="">N/A</option>
            <option value="1">Si</option>
            <option value="0">No</option>
          </select>
        </div>

        <div>
          <label for="medical-exam">Examen Medico:</label>
          <select v-model="selectedMedicalExam" id="medical-exam">
            <option value="">N/A</option>
            <option value="1">Si</option>
            <option value="0">No</option>
          </select>
        </div>
        <div>
          <label for="description">Descripcion:</label>
          <textarea id="description" v-model="description"></textarea>

          <label for="expiryDate">Fecha de Expiracíon:</label>
          <input id="expiryDate" v-model="expiryDate" type="date" required />
        </div>
        <div>
          <label for="expiryReminder">Recordarme:</label>
          <select v-model="expiryReminder">
            <option value="0">No Recordar</option>
            <option value="1">1 día antes</option>
            <option value="7">1 semnana antes</option>
            <option value="15">15 días  antes</option>
            <option value="30">1 més ántes</option>
          </select>
        </div>
        <div class="modal-buttons">
          <button type="submit">Solicitar</button>
          <button type="button" @click="handleCloseModal">Cancelar</button>
        </div>
      </form>
    </div>
  </div>
</template>

<script>
import Swal from 'sweetalert2';

export default {
  props: {
    employeeId: {
      type: Number,
      required: true
    },
    id_portal: {
      type: Number,
      default: null
    },
    idCliente: {
      type: String,
      default: ''
    },
  },
  data() {
    return {
      projects: [],
      antidopingPackages: [],
      selectedProject: '',
      selectedPackage: '',
      selectedPsychometric: '',
      selectedMedicalExam: '',
      selectedDescription: '',
      selectedPackageInfo: '',
      description: '',
      expiryDate:this.getFutureDate(1) ,
      expiryReminder: '',
      descriptions: {
        1: `General Information
            Academic History
            Social Background
            Personal References: 3
            Work References
            Legal Investigation
            Unmentioned Jobs
            Documentation
            Family Group
            Income and Expenses
            Housing and Environment`,
        2: `General Information
            Academic History
            Social Background
            Personal References: 3
            Work References
            Legal Investigation
            Unmentioned Jobs
            Documentation
            Family Group
            Income and Expenses
            Housing and Environment`,
        3: `General Information
            Work References
            Unmentioned Jobs`,
      },
    };
  },
  mounted() {
    this.fetchProjects();
    this.fetchAntidopingPackages();
  },
  methods: {
    async fetchProjects() {
      try {
        const response = await fetch(`${this.$apiUrl}/proyectos-historial`);
        if (!response.ok) {
          throw new Error('Failed to fetch projects');
        }
        this.projects = await response.json();
      } catch (error) {
        console.error(error);
      }
    },
    async fetchAntidopingPackages() {
      try {
        const response = await fetch(`${this.$apiUrl}/antidoping-packages`);
        if (!response.ok) {
         // console.log("🚀 ~ fetchAntidopingPackages ~ response:", response)
          throw new Error('Failed to fetch antidoping packages');
        }
        this.antidopingPackages = await response.json();
      } catch (error) {
        console.error(error);
      }
    },
    async submitRequest() {
      let tipo_antidoping = '';
      if(this.selectedPackage){
        tipo_antidoping = 1;
      }else{
        tipo_antidoping = 0;

      }
      let resultString = ""; // Variable para almacenar los resultados

      // Evaluar cada opción y concatenar el string correspondiente
      if (this.selectedProject) {
        resultString += "Bgv \n "; // Concatenar el valor del proyecto
      }

      if (this.selectedPackage) {
        resultString += "Drug Test\n "; // Concatenar el valor del paquete
      }

      if (this.selectedPsychometric) {
        resultString += "Psicométric \n" ; // Concatenar el valor psicométrico
      }

      if (this.selectedMedicalExam) {
        resultString += "Medical Test "; // Concatenar el valor del examen médico
      }

      const requestData = {
        project: this.selectedProject,
        paquete: this.selectedPackage,
        psychometric: this.selectedPsychometric,
        medicalExam: this.selectedMedicalExam,
        name: resultString,
        id_portal: this.id_portal,
        id_empleado: this.employeeId,
        id_cliente_talent: this.idCliente,
        description: this.description,
        expiry_date: this.expiryDate,
        expiryReminder: this.expiryReminder,
        tipo_antidoping : tipo_antidoping,
      };

      console.log('Submitting request:', requestData);

      try {
        const response = await fetch(`${this.$apiUrl}/registrar-candidato`, {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
          },
          body: JSON.stringify(requestData),
        });

        if (!response.ok) {
          throw new Error('Failed to submit request');
        }

        const responseData = await response.json();
        console.log('Response from server:', responseData);
        this.$emit('documentoSubido', responseData.data);
        // Muestra un mensaje de éxito
        Swal.fire({
          icon: 'success',
          title: 'Success',
          text: responseData.msg, // Mensaje del servidor
          confirmButtonText: 'Accept'
        }).then(() => {
          this.$emit('documentUploaded', response.data);
          this.handleCloseModal(); // Close modal
        });
      } catch (error) {
        console.error('Error submitting request:', error);

        // Muestra un mensaje de error
        Swal.fire({
          icon: 'error',
          title: 'Error',
          text: 'Could not register the candidate, please try again later.',
        });
      }
    },
    updateDescription() {
      this.selectedDescription = this.descriptions[this.selectedProject] || 'fdgdfgdfg';
    },
    updatePackageInfo() {
      const selected = this.antidopingPackages.find(paquete => paquete.id === this.selectedPackage);
      this.selectedPackageInfo = selected ? selected.conjunto : '';
    },
    //obtener  la  fecha  y formato del dia
    getCurrentDate() {
      const today = new Date();
      const year = today.getFullYear();
      const month = String(today.getMonth() + 1).padStart(2, '0');
      const day = String(today.getDate()).padStart(2, '0');
      return `${year}-${month}-${day}`;
    },
    // obtener  la  fecha   y formato  de un  año despues 
    getFutureDate(years) {
      const futureDate = new Date();
      futureDate.setFullYear(futureDate.getFullYear() + years);
      const year = futureDate.getFullYear();
      const month = String(futureDate.getMonth() + 1).padStart(2, '0');
      const day = String(futureDate.getDate()).padStart(2, '0');
      return `${year}-${month}-${day}`;
    },
    handleCloseModal() {

      this.$emit('close');
    },
  },
};
</script>

<style scoped>
.modal {
  display: flex;
  justify-content: center;
  /* Centra horizontalmente */
  align-items: center;
  /* Centra verticalmente */
  position: fixed;
  /* Fija el modal en la pantalla */
  top: 0;
  /* Comienza desde la parte superior */
  left: 0;
  /* Comienza desde la parte izquierda */
  width: 100vw;
  /* Ancho total de la ventana */
  height: 100vh;
  /* Alto total de la ventana */
  background-color: rgba(0, 0, 0, 0.5);
  /* Fondo oscuro semitransparente */
  z-index: 1000;
  /* Asegura que el modal esté encima de otros elementos */
  overflow: auto;
  /* Agrega scroll si el contenido es más grande que la ventana */
}

.modal-content {
  background-color: white;
  /* Fondo blanco para el contenido del modal */
  border-radius: 8px;
  /* Bordes redondeados */
  padding: 20px;
  /* Espaciado interno */
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  /* Sombra para un efecto elevado */
  max-width: 500px;
  /* Ancho máximo del modal */
  width: 90%;
  /* Ancho del modal en pantallas pequeñas */
  max-height: 80vh;
  /* Limita la altura del modal al 80% de la ventana */
  overflow-y: auto;
  /* Agrega scroll si el contenido es mayor que la altura permitida */
  animation: fadeIn 0.3s;
  /* Animación de entrada */
}

/* Para asegurarte de que el modal-content esté centrado */
.modal-content {
  margin: auto;
  /* Aplica un margen automático para centrar el contenido */
  position: relative;
  /* Posicionamiento relativo para el contenido */
  top: 50%;
  /* Mueve el contenido al 50% de la altura */
  transform: translateY(-50%);
  /* Ajusta hacia arriba para centrar verticalmente */
}

.close {
  cursor: pointer;
  /* Cambia el cursor al pasar sobre el botón de cerrar */
  font-size: 24px;
  /* Tamaño del icono de cerrar */
  float: right;
  /* Alinea el botón de cerrar a la derecha */
}

h2 {
  margin: 0 0 20px;
  /* Margen inferior para separar el título del resto */
  font-size: 24px;
  /* Tamaño del título */
  color: #333;
  /* Color del texto del título */
}

form {
  display: flex;
  flex-direction: column;
  /* Apila los elementos del formulario verticalmente */
}

div {
  margin-bottom: 15px;
  /* Espaciado entre los campos del formulario */
}

label {
  width: 100%;
  margin-bottom: 5px;
  /* Espaciado inferior para etiquetas */
  font-weight: bold;
  /* Negrita para las etiquetas */
}



button {
  padding: 10px;
  /* Espaciado interno del botón */
  background-color: #4CAF50;
  /* Color de fondo verde */
  color: white;
  /* Color del texto blanco */
  border: none;
  /* Sin borde */
  border-radius: 4px;
  /* Bordes redondeados para el botón */
  cursor: pointer;
  /* Cambia el cursor al pasar sobre el botón */
  font-size: 16px;
  /* Tamaño de fuente del botón */
}

button:hover {
  background-color: #45a049;
  /* Cambia a un verde más oscuro al pasar el mouse */
}


input {
  width: 95%;
  margin-bottom: 15px;
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 4px;
}

textarea {
  width: 95%;
  margin-bottom: 15px;
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 4px;
}

select {
  width: 100%;
  margin-bottom: 15px;
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 4px;
  text-transform: uppercase;

}

.modal-buttons {
  display: flex;
  justify-content: flex-end;
}

.modal-buttons button {
  background: #007bff;
  color: #fff;
  border: none;
  padding: 10px 15px;
  border-radius: 4px;
  cursor: pointer;
  margin-left: 10px;
  font-size: 16px;
  transition: background-color 0.3s;
}

.modal-buttons button[type="button"] {
  background: #dc3545;
}

.modal-buttons button:hover {
  background: #0056b3;
}

.modal-buttons button[type="button"]:hover {
  background: #c82333;
}

.description-list {
  list-style-type: none;
  /* Opcional: elimina los puntos de la lista */
  padding: 0;
  /* Elimina el padding por defecto */
  margin: 20;
  /* Elimina el margen por defecto */
  text-align: left;
  /* Alinea el texto a la izquierda */
}

.description-list li {
  margin-bottom: 5px;
  /* Espacio entre los elementos de la lista */
}
@media (max-width: 768px) {
  .modal-content {
    width: 95%;
    /* Asegura que el modal tenga un tamaño adecuado en pantallas pequeñas */
    max-height: 80vh;
    /* Mantiene el máximo de altura */
    padding: 10px;
  }
}

@media (max-width: 480px) {
  .modal-content {
    width: 100%;
    /* Modal ocupa todo el ancho en pantallas muy pequeñas */
  }
}
</style>
