<template>
  <div class="add-document-modal-overlay" v-if="isVisible" @click="handleCloseModal">
    <div class="add-document-modal-content" @click.stop>
      <h3>Actualizar Documento</h3>

      <form @submit.prevent="handleSave">
        <label for="docName">Tipo:</label>
        <select v-model="selectedOption" @change="updateDocumentName">
          <option value="" disabled>Selecciona un tipo de documento o escribe uno propio. Si eliges 'Otro', por favor
            ingresa el nombre del documento.</option>
          <option value="other">Otro...</option>
          <option v-for="option in documentOptions" :key="option.id" :value="option.name">{{ option.name }}</option>
        </select>

        <input v-if="selectedOption === 'other'" v-model="document.name" type="text"
          placeholder="Escribe el nombre aquí..." required />

        <label for="description">Descripción:</label>
        <textarea id="description" v-model="document.description"
          placeholder="Agrega una breve descripción..."></textarea>

        <label for="expiryDate">Fecha de Vencimiento:</label>
        <input id="expiryDate" v-model="document.expiryDate" type="date" required />

        <label for="fileUpload">Subir Archivo:</label>
        <input id="fileUpload" type="file" @change="handleFileUpload" />
        <div v-if="document.nameDocument && !document.file" class="current-file-info">
          <p><strong>Archivo actual:</strong> {{ document.nameDocument }}</p>
        </div>
        <label for="expiryReminder">Recordatorio de Vencimiento:</label>
        <select v-model="document.expiryReminder">
          <option value="0">No notificar</option>
          <option value="1">1 día antes</option>
          <option value="7">1 semana antes</option>
          <option value="15">15 días antes</option>
          <option value="30">1 mes antes</option>
        </select>

        <!-- Nuevo campo para el estatus del documento con valores numéricos -->

        <label for="status"
          title="Aqui se  define  si el  estado del documento por  default sera marcado como bueno ">Estatus del
          Documento:</label>
        <br>
        <div class>
          <input type="radio" id="good" value="1" v-model="document.status" />
          <label for="good" class="good-label">Bueno</label>

          <input type="radio" id="average" value="2" v-model="document.status" />
          <label for="average" class="average-label">Regular</label>

          <input type="radio" id="bad" value="3" v-model="document.status" />
          <label for="bad" class="bad-label">Malo</label>
        </div>

        <div class="modal-buttons">
          <button type="submit">Guardar</button>
          <button type="button" @click="handleCloseModal">Cancelar</button>
        </div>
      </form>
    </div>
  </div>
</template>


<script>
import axios from 'axios';
import Swal from 'sweetalert2';
import moment from 'moment-timezone';
export default {
  name: 'AddDocumentModal',
  props: {
    isVisible: {
      type: Boolean,
      required: true
    },
    employeeId: {
      type: Number,
      required: true
    },
    id_portal: {
      type: Number,
      default: null
    },
    origen: {
      type: Number,
      default: null
    },
    documentToEdit: {
      type: Object,
      default: null
    },
    endpointConfig: { // <-- ESTA ES LA QUE FALTABA
      type: Object,
      required: true
    }
  },
  data() {
    return {
      selectedOption: null,
      documentOptions: [],
      document: {
        name: '',
        description: '',
        uploadDate: this.getCurrentDate(),
        expiryDate: this.getFutureDate(1),
        expiryReminder: 0, // default value
        nameDocument: null,
        file: null,
        status: 1,
      }
    };
  },
  mounted() {
    if (!this.endpointConfig.tabla || !this.endpointConfig.endpoint) {
    
      console.warn('endpointConfig incompleto:', this.endpointConfig);
    }
    this.fetchDocumentOptions().then(() => {
      this.loadDocumentToEdit();
    });
  },



  methods: {
    async fetchDocumentOptions() {
      try {
        const params = this.id_portal ? { id_portal: this.id_portal, tabla: this.endpointConfig.tabla } : {};
        const response = await axios.get(`${this.$apiUrl}/document-options`, { params });
        this.documentOptions = response.data;
      } catch (error) {
        console.error('Error fetching document options:', error);
      }
    },

    loadDocumentToEdit() {
      const newDoc = this.documentToEdit;
      if (newDoc) {
        // console.log('Documento a editar:', newDoc);

        const tipo = newDoc.optionName || newDoc.nameAlterno || '';
        const matchedOption = this.documentOptions.find(opt => opt.name === tipo);

        this.selectedOption = matchedOption ? matchedOption.name : 'other';

        this.document = {
          id_document: newDoc.id,
          name: tipo,
          description: newDoc.description || '',
          uploadDate: newDoc.upload_date || this.getCurrentDate(),
          expiryDate: newDoc.expiry_date || this.getFutureDate(1),
          expiryReminder: newDoc.expiry_reminder ?? 0,
          file: null,
          nameDocument: newDoc.nameDocument,
          status: newDoc.status || 1
        };
        //console.log("🚀 ~ loadDocumentToEdit ~ document:", document)

      }
    },

    getCurrentDate() {
      const today = new Date();
      const year = today.getFullYear();
      const month = String(today.getMonth() + 1).padStart(2, '0');
      const day = String(today.getDate()).padStart(2, '0');
      return `${year}-${month}-${day}`;
    },
    getFutureDate(years) {
      const futureDate = new Date();
      futureDate.setFullYear(futureDate.getFullYear() + years);
      const year = futureDate.getFullYear();
      const month = String(futureDate.getMonth() + 1).padStart(2, '0');
      const day = String(futureDate.getDate()).padStart(2, '0');
      return `${year}-${month}-${day}`;
    },
    handleCloseModal() {
      this.$emit('close');
    },
    async handleSave() {
      try {
        const formData = new FormData();

        // Agrega los datos comunes
        formData.append('employee_id', this.employeeId);
        formData.append('name', this.selectedOption === 'other' ? this.document.name : this.selectedOption);
        formData.append('description', this.document.description);
        formData.append('expiry_date', this.document.expiryDate);
        formData.append('expiry_reminder', this.document.expiryReminder || '');
        formData.append('status', this.document.status);
        formData.append('doc_anterior', this.document.nameDocument);
        formData.append('creacion', moment.tz('America/Mexico_City').format('YYYY-MM-DD HH:mm:ss'));
        formData.append('edicion', moment.tz('America/Mexico_City').format('YYYY-MM-DD HH:mm:ss'));
        formData.append('carpeta', this.endpointConfig.tabla);
        formData.append('id_portal', this.id_portal);

        // Agrega el archivo si existe
        if (this.document.file) {
          formData.append('file', this.document.file); // aquí se manda el archivo real
        }

        // Simula método PUT
        formData.append('_method', 'PUT');

        const url = `${this.$apiUrl}${this.endpointConfig.endpoint}/${this.documentToEdit.id}`;

        const response = await fetch(url, {
          method: 'POST', // no usar PUT directamente si estás mandando archivos
          body: formData,
        });

        if (!response.ok) {
          throw new Error('Error al actualizar el documento');
        }

        await Swal.fire({
          icon: 'success',
          title: '¡Éxito!',
          text: 'La información y el archivo se actualizaron correctamente.',
          confirmButtonText: 'Aceptar',
        });

       this.handleCloseModal();

      } catch (error) {
        console.error('Error al enviar datos:', error);
        await Swal.fire({
          icon: 'error',
          title: '¡Error!',
          text: 'No se pudo actualizar la información.',
          confirmButtonText: 'Aceptar',
        });
      }
    }, 





    handleFileUpload(event) {
      this.document.file = event.target.files[0]; // Store the selected file
    },
    updateDocumentName() {
      if (this.selectedOption !== 'other') {
        this.document.name = this.selectedOption;
      }
    }
  }
};
</script>


