<template>
  <div class="profile-header">
    <div class="profile-photo-section">
      <img :src="imageUrl" alt="Profile Picture" class="profile-picture" @error="setDefaultImage"
        @click="showModal = true">
      <template v-if="idRol == 6 || idRol == 9 || idRol == 1 || idRol == 10">

        <input type="file" id="uploadPhoto" @change="handleFileUpload" style="display: none;">
        <label for="uploadPhoto" class="upload-icon"> Actualizar 📷</label>
      </template>
    </div>
    <div class="profile-info">
      <h2>{{ localEmpleadoData.nombre }} {{ localEmpleadoData.paterno }} {{ localEmpleadoData.materno }}</h2>
    </div>
    <div class="profile-extra-info">
      <p><strong>ID:</strong> {{ localEmpleadoData.id }}</p>
      <p><strong>Última Actualizacion:</strong> {{ localEmpleadoData.edicion }}</p>
    </div>

    <!-- Botón para enviar a exempleados -->
    <template v-if="idRol == 6 || idRol == 9 || idRol == 1 || idRol == 10">

      <div class="header-actions">
        <button @click="confirmSendToFormerEmployee" class="btn btn-primary">Enviar a Exempleados</button>
      </div>
    </template>

    <!-- Modal para solicitud de datos -->
    <div v-if="showExitModal" class="exit-modal">
      <div class="exit-modal-content">
        <span class="exit-close" @click="showExitModal = false">&times;</span>
        <h3>Enviar a Ex-empleado</h3>
        <label for="exitDate">Fecha:</label>
        <input type="date" v-model="exitDate" id="exitDate">
        <label for="exitReason">Motivo de la salida del trabajo:</label>
        <textarea v-model="exitReason" id="exitReason"></textarea>
        <button @click="submitExitInfo">Enviar</button>
      </div>
    </div>

    <!-- Modal para la imagen -->
    <div v-if="showModal" class="modal" @click="showModal = false">
      <img :src="imageUrl" alt="Profile Picture" class="modal-image" @click.stop>
      <span class="close" @click="showModal = false">&times;</span>
    </div>
  </div>
</template>

<style scoped>
