  /* Color de fondo rojo */
  color: white;
  /* Texto blanco */
  border: none;
  /* Sin borde */
  padding: 10px 20px;
  /* Espaciado interno */
  border-radius: 5px;
  /* Bordes redondeados */
  font-size: 16px;
  /* Tamaño de fuente */
  cursor: pointer;
  /* Cambia el cursor al pasar el ratón */
  transition: background-color 0.3s;
  /* Transición suave al cambiar color */
}

.btn-primary:hover {
  background-color: darkred;
  /* Color al pasar el ratón */
}

/* Mantener el resto de tus estilos existentes */
