  font-size: 16px;
  cursor: pointer;
  color: #007bff;
  background: #f0f0f0;
  padding: 5px 10px;
  border-radius: 4px;
  text-align: center;
}

.profile-info {
  flex-grow: 1;
  text-align: center;
}

.profile-info h2 {
  margin: 0;
  font-size: 24px;
  color: #333;
}

