  /* Color del texto */
  border: none;
  /* Sin borde */
  padding: 10px 15px;
  /* Espaciado interno */
  border-radius: 5px;
  /* Bordes redondeados */
  cursor: pointer;
  /* Cambiar cursor */
  font-size: 16px;
  /* Tamaño de fuente */
  transition: background-color 0.3s;
  /* Transición suave */
  width: 100%;
  /* Botón ocupa todo el ancho */
}

.exit-modal button:hover {
  background-color: #0056b3;
  /* Color al pasar el ratón */
}

/* Estilos generales para móviles */
@media (max-width: 600px) {
  body {
    margin: 0;
    padding: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    /* Centra todos los elementos */
    justify-content: center;
  }

  .header-actions,
  .profile-info {
    text-align: center;
    /* Centra texto */
    margin: 10px 0;
  }

  .profile-header {
    flex-direction: column;
    align-items: center;
    /* Asegura que todo esté centrado */
    justify-content: center;
    /* Centra verticalmente */
    padding: 15px;
  }




  .profile-photo-section {
    align-items: center;
    /* Centra los elementos de la sección de foto */
    justify-content: center;
    text-align: center;
  }

  .profile-info {
    text-align: center;
    /* Centra el texto dentro de la información del perfil */
  }

  .profile-extra-info {
    text-align: center;
    max-width: 100%;
    /* Se asegura de que ocupe todo el ancho */
  }

  .profile-info h2 {
    font-size: 20px;
  }

  .profile-picture {
    width: 80px;
    height: 80px;
  }
}

/* Estilos para tablets */
@media (min-width: 601px) and (max-width: 1024px) {
  .profile-header {
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
  }


  .header-actions {
    position: relative;
    margin-top: 10px;
    text-align: center;
    width: 100%;
  }

