    text-align: center;
  }
}

/* Estilos para desktops grandes */
@media (min-width: 1025px) {
  .profile-header {
    flex-direction: row;
  }


}



@media (min-width: 600px) {
  .exit-modal-content {
    width: 70%;
  }
}

@media (min-width: 900px) {
  .exit-modal-content {
    width: 600px;
  }
}

/* Botones y formularios */
