<script>

import axios from 'axios';
import Swal from 'sweetalert2';

export default {
  props: {
    empleadoData: {
      type: Object,
      required: true
    },
    idRol: {
      type: String,
      default: ''
    }
  },
  data() {
    return {
      showModal: false,
      showExitModal: false,
      defaultImage: `${this.$imageUrl}perfil.png`,
      localEmpleadoData: this.initializeLocalEmpleadoData(), // Inicializa correctamente
      currentImageName: '',
      exitDate: this.getCurrentDate(), // Añadir propiedades necesarias para el modal
      exitReason: '',
    };
  },
  computed: {
    imageUrl() {
      return this.localEmpleadoData.foto
        ? `${this.$imageUrl}${this.localEmpleadoData.foto}`
        : this.defaultImage;
    }
  },
  methods: {
    initializeLocalEmpleadoData() {
      return {
        id_empleado: this.empleadoData.id,
        nombre: this.empleadoData.nombre || 'Nombre not available',
        paterno: this.empleadoData.paterno || '',
        materno: this.empleadoData.materno || '',
        id: this.empleadoData.id_empleado || 'ID pending to be assigned',
        edicion: this.empleadoData.edicion
          ? new Date(this.empleadoData.edicion).toLocaleDateString('es-MX', { year: 'numeric', month: '2-digit', day: '2-digit' })
          : 'date not available',
        foto: this.empleadoData.foto || ''
      };
    },

    setDefaultImage(event) {
      event.target.src = this.defaultImage;
    },

    async handleFileUpload(event) {
      const file = event.target.files[0];
      if (!file) return;

      const formData = new FormData();
      const id = this.empleadoData.id; // Asegúrate de que este ID sea correcto
      //  console.log("🚀 ~ handleFileUpload ~ id:", id)
      const carpeta = '_perfilEmpleado';

      formData.append('foto', file);
      formData.append('carpeta', carpeta);
      if (this.currentImageName) {
        formData.append('currentImage', this.currentImageName);
      }

      try {
        // Obtener el token CSRF
        const csrfMetaTag = document.querySelector('meta[name="csrf-token"]');
        const csrfToken = csrfMetaTag ? csrfMetaTag.getAttribute('content') : null;

        if (!csrfToken) {
          console.error('CSRF token no encontrado');
          return;
        }

        // Hacer la solicitud
        const response = await fetch(`${this.$apiUrl}/empleados/${id}/foto`, {
          method: 'POST',
          body: formData,
          headers: {
            'X-CSRF-TOKEN': csrfToken,
          },
        });

        // Manejar la respuesta
        if (response.ok) {
          const data = await response.json();
          this.localEmpleadoData.foto = data.ruta; // Actualiza la foto localmente
          Swal.fire({
            icon: 'success',
            title: 'Carga exitosa',
            text: 'La foto se ha actualizado correctamente.',
          });
        } else {
          const errorText = await response.text(); // Obtiene el error como texto
          console.error('Error al cargar la imagen:', errorText);
          Swal.fire({
            icon: 'error',
            title: 'Error',
            text: errorText || 'Error desconocido',
          });
        }
      } catch (error) {
        console.error('Error en la solicitud:', error);
        Swal.fire({
          icon: 'error',
          title: 'Error',
          text: 'Ocurrió un problema al intentar cargar la imagen.',
        });
      }
    },

    async checkImageExists() {
      return new Promise((resolve) => {
        const img = new Image();
        img.src = `${this.$imageUrl}${this.localEmpleadoData.foto}`;

        // console.log("🚀 ~ returnnewPromise ~ img.src:", img.src)
        //  console.log("🚀 ~ returnnewPromise ~ this.localEmpleadoData.foto:", this.localEmpleadoData.foto)
        img.onload = () => {
          resolve(true); // La imagen existe
        };

        img.onerror = () => {
          resolve(false); // La imagen no existe
        };
      });
    },
    getCurrentDate() {
      const today = new Date();
      const year = today.getFullYear();
      const month = String(today.getMonth() + 1).padStart(2, '0');
      const day = String(today.getDate()).padStart(2, '0');
      return `${year}-${month}-${day}`;
    },
    confirmSendToFormerEmployee() {
      Swal.fire({
        title: '¡Advertencia!',
        text: 'El empleado será movido a la lista de exempleados con toda su información y documentos.',
        icon: 'warning',
        showCancelButton: true,
        confirmButtonText: 'Sí, enviar!',
        cancelButtonText: 'Cancelar'
      }).then((result) => {
        if (result.isConfirmed) {
          this.showExitModal = true; // Muestra el modal para ingresar datos de salida
        }
      });
    },
    async submitExitInfo() {
      const filteredData = {
        id_empleado: this.localEmpleadoData.id_empleado,
        titulo: 'Razón para mover a exempleados', 
        creacion: this.exitDate, // Fecha de salida
        comentario: this.exitReason,
        origen: 1,
        status: 2, // Motivo de salida
      };

      // Establecer la fecha de edición con la fecha y hora actual en la zona horaria de México
      //console.log("🚀 ~ filteredData:2", filteredData)

      try {
        const apiUrl2 = `${this.$apiUrl}/comentarios-former-empleado`;


        if (!this.$csrfToken) {
          console.error('CSRF token no encontrado');
          return; // Evita enviar la solicitud si el token no está presente
        }

        const response = await axios.post(apiUrl2, filteredData, {
          headers: {
            'X-CSRF-TOKEN': this.$csrfToken
          }
        });

        console.log('Respuesta de la API:', response); // Ver respuesta completa

        // Mostrar SweetAlert de éxito
        Swal.fire({
          icon: 'success',
          title: 'Envío exitoso',
          text: 'El colaborador se ha enviado exitosamente al módulo de exempleados.',
          confirmButtonText: 'Aceptar'
        }).then(() => {
          // Emitir evento solo después de que el usuario acepte el mensaje
          this.$emit('empleadoActualizado', filteredData);
          this.showExitModal = false; // Cierra modal de motivo
          this.$emit('close');   // Cierra modal principal
        });
      } catch (error) {
        let errorMessage = 'Ocurrió un error al enviar al colaborador intentalo mas tarde.';

        if (error.response) {
          errorMessage = error.response.data.error || 'Error desconocido';
          console.error('Error al actualizar el empleado:', error.response.data);
        } else if (error.request) {
          console.error('No se recibió respuesta del servidor:', error.request);
          errorMessage = 'No se recibió respuesta del servidor.';
        } else {
          console.error('Error en la configuración de la solicitud:', error.message);
          errorMessage = error.message;
        }

        // Mostrar SweetAlert de error
        Swal.fire({
          icon: 'error',
          title: 'Error',
          text: errorMessage,
          confirmButtonText: 'Aceptar'
        });
      }

    },

  },



  async mounted() {
    const exists = await this.checkImageExists();
    if (!exists) {
      // console.log('La imagen no existe. Usando imagen predeterminada.');
      this.localEmpleadoData.foto = ''; // Usa la imagen por defecto
    }
  },

};
</script>


/* ProfileHeader.vue */
