<template>

  <div>
    <h2>Información Médica </h2>
    <!-- Formulario para subir archivo -->
    <template v-if="idRol == 6 || idRol == 9 || idRol == 1">

      <form @submit.prevent="uploadFile" id="uploadForm" method="POST" enctype="multipart/form-data">
        <fieldset class="form-section">
          <legend>Actualización Masiva </legend>
          <p class="upload-instructions">
            La descarga de la plantilla en Excel está diseñada para realizar una <strong>actualización masiva de
              Información Médica</strong> de múltiples empleados.
            Esto facilita subir los datos de todos los empleados en un solo archivo, agilizando el proceso de
            actualización.
          </p>
          <input type="file" @change="cargarExcel" accept=".xlsx, .xls" class="excel-upload" />
          <button type="submit">Subir Archivo</button>
          <!-- Botón para descargar plantilla CSV -->
          <button type="button" @click="descargarPlantillaExcel" class="btn-download">Descargar Plantilla</button>
        </fieldset>
      </form>

    </template>


    <p class="module-description">
      El formulario a continuación es para <strong>actualizar la información de un solo empleado</strong>.
      Utilízalo para modificar o agregar datos específicos del empleado que estás consultando actualmente.
    </p>

    <form @submit.prevent="updateMedicalInfo" class="employee-form">
      <fieldset class="form-section">
        <legend>Actualización colaborador actual</legend>
        <div class="form-grid">
          <div class="form-group">
            <label for="peso">Peso (kg):</label>
            <input type="number" id="peso" v-model="localEmpleadoData.peso" placeholder="Peso"
              title="Ingrese el peso en kilogramos" />
          </div>
          <div class="form-group">
            <label for="edad">Edad:</label>
            <input type="number" id="edad" v-model="localEmpleadoData.edad" placeholder="Edad"
              title="Ingrese la edad del empleado" />
          </div>
          <div class="form-group">
            <label for="alergias_medicamentos">Alergias a Medicamentos:</label>
            <input type="text" id="alergias_medicamentos" v-model="localEmpleadoData.alergias_medicamentos"
              placeholder="Alergias a medicamentos" title="Describa las alergias a medicamentos" />
          </div>
          <div class="form-group">
            <label for="alergias_alimentos">Alergias a Alimentos:</label>
            <input type="text" id="alergias_alimentos" v-model="localEmpleadoData.alergias_alimentos"
              placeholder="Alergias a alimentos" title="Describa las alergias a alimentos" />
          </div>
          <div class="form-group">
            <label for="enfermedades_cronicas">Enfermedades Crónicas:</label>
            <input type="text" id="enfermedades_cronicas" v-model="localEmpleadoData.enfermedades_cronicas"
              placeholder="Enfermedades crónicas" title="Describa las enfermedades crónicas" />
          </div>
          <div class="form-group">
            <label for="cirugias">Cirugías:</label>
            <input type="text" id="cirugias" v-model="localEmpleadoData.cirugias" placeholder="Cirugías previas"
              title="Describa las cirugías previas" />
          </div>
          <div class="form-group">
            <label for="tipo_sangre">Tipo de Sangre:</label>
            <input type="text" id="tipo_sangre" v-model="localEmpleadoData.tipo_sangre" placeholder="Tipo de sangre"
              title="Indique el tipo de sangre" />
          </div>
          <div class="form-group">
            <label for="contacto_emergencia">Contacto de Emergencia:</label>
            <input type="text" id="contacto_emergencia" v-model="localEmpleadoData.contacto_emergencia"
              placeholder="Contacto de emergencia" title="Ingrese los datos del contacto de emergencia" />
          </div>
          <div class="form-group">
            <label for="medicamentos_frecuentes">Medicamentos Frecuentes:</label>
            <input type="text" id="medicamentos_frecuentes" v-model="localEmpleadoData.medicamentos_frecuentes"
              placeholder="Medicamentos frecuentes" title="Describa los medicamentos que toma frecuentemente" />
          </div>
          <div class="form-group">
            <label for="lesiones">Lesiones Crónicas / Fracturas:</label>
            <input type="text" id="lesiones" v-model="localEmpleadoData.lesiones"
              placeholder="Lesiones crónicas o fracturas" title="Describa lesiones crónicas o fracturas previas" />
          </div>
          <div class="form-group two-column">
            <label for="otros_padecimientos">Otros Padecimientos:</label>
            <textarea id="otros_padecimientos" v-model="localEmpleadoData.otros_padecimientos"
              placeholder="Describa otros padecimientos..." rows="4"
              title="Describa cualquier otro padecimiento relevante"></textarea>
          </div>
          <div class="form-group two-column">
            <label for="otros_padecimientos2">Condiciones Adicionales:</label>
            <textarea id="otros_padecimientos2" v-model="localEmpleadoData.otros_padecimientos2"
              placeholder="Describa condiciones adicionales..." rows="4"
              title="Describa cualquier condición adicional"></textarea>
          </div>
        </div>
      </fieldset>

      <div class="button-container">
        <template v-if="idRol == 6 || idRol == 9 || idRol == 1">

          <button type="submit" class="update-button">Actualizar</button>
        </template>
        <button class="close-button" type="button" @click="handleCloseModal">Cerrar</button>
      </div>
    </form>
  </div>
</template>


<script>
import axios from 'axios';
import Swal from 'sweetalert2';
import moment from 'moment-timezone';

export default {
  name: 'MedicalInfoForm',
  props: {
    empleadoData: {
      type: Object,
      required: true
    },
    idRol: {
      type: String,
      default: ''
    },
    idCliente: {
      type: String,
      default: ''
    },
  },
  data() {
    return {
      localEmpleadoData: { ...this.empleadoData },
      excelFile: null,
      // Crea una copia local
    };
  },
  created() {
    this.fetchMedicalInfo();
  },
  methods: {
    cargarExcel(event) {
      this.excelFile = event.target.files[0];
      console.log('Archivo seleccionado:', this.excelFile);
    },

    async uploadFile() {
      if (!this.excelFile) {
        Swal.fire({
          icon: 'warning',
          title: 'Archivo no seleccionado',
          text: 'Por favor selecciona un archivo antes de subir.'
        });
        return;
      }

      const formData = new FormData();
      formData.append('file', this.excelFile);
      formData.append('id_rol', this.idRol);
      formData.append('id_cliente', this.idCliente);

      try {
        const response = await axios.post(`${this.$apiUrl}/upload-medical-info`, formData, {
          headers: {
            'Content-Type': 'multipart/form-data'
          }
        });

        Swal.fire({
          icon: 'success',
          title: 'Éxito',
          text: response.data.message || "Archivo subido y datos actualizados con éxito."
        }).then(() => {
          // Emitir evento solo después de que el usuario acepte el mensaje
          this.handleCloseModal(); // Cerrar modal
        });
      } catch (error) {
        console.error(error);
        Swal.fire({
          icon: 'error',
          title: 'Error',
          text: error.response?.data?.error || "Hubo un error al subir el archivo."
        });
      }
    },
    descargarPlantillaExcel() {
      const empleadoId = this.empleadoData?.id;

      if (!empleadoId) {
        Swal.fire({
          icon: 'error',
          title: 'Error',
          text: 'ID de empleado no disponible.'
        });
        return;
      }

      axios.get(`${this.$apiUrl}/download-template-medical`, {
        params: { empleado_id: empleadoId },
        responseType: 'blob',
      })
        .then((response) => {
          const url = window.URL.createObjectURL(new Blob([response.data]));
          const link = document.createElement('a');
          link.href = url;
          link.setAttribute('download', 'Informacion_Medica.xlsx');
          document.body.appendChild(link);
          link.click();
          document.body.removeChild(link);
        })
        .catch((error) => {
          console.error('Error al descargar el archivo CSV:', error);
          Swal.fire({
            icon: 'error',
            title: 'Descarga fallida',
            text: 'No se pudo descargar la plantilla.'
          });
        });
    },





    fetchMedicalInfo() {
      const employeeId = this.localEmpleadoData.id; // Obtiene el ID del empleado
      // console.log("🚀 ~ fetchMedicalInfo ~ employeeId:", employeeId)
      const csrfMetaTag = document.querySelector('meta[name="csrf-token"]');
      const csrfToken = csrfMetaTag ? csrfMetaTag.getAttribute('content') : null;

      if (!csrfToken) {
        console.error('CSRF token no encontrado');
        return; // Evita enviar la solicitud si el token no está presente
      }

      axios.get(`${this.$apiUrl}/medical-info/${employeeId}`, {
        headers: {
          'X-CSRF-TOKEN': csrfToken
        }
      })
        .then(response => {
          this.localEmpleadoData = response.data; // Actualiza la copia local con la respuesta
        })
        .catch(error => {
          console.error('Error fetching medical info:', error.response ? error.response.data : error.message);
        });
    },
    handleCloseModal() {
      this.$emit('close'); // Emite el evento para cerrar el modal
    },
    async updateMedicalInfo() {
      const filteredData = Object.fromEntries(
        Object.entries(this.localEmpleadoData).filter(([, value]) => value !== '' && value !== null)
      );

      // Establecer la fecha de edición
      filteredData.edicion = moment.tz('America/Mexico_City').format('YYYY-MM-DD HH:mm:ss');

      const employeeId = this.localEmpleadoData.id_empleado; // Obtiene el ID del empleado
      const apiUrl = `${this.$apiUrl}/medical-info/${employeeId}`;

      try {
        console.log('Datos a enviar a la API:', JSON.stringify(filteredData, null, 2));



        if (!this.$csrfToken) {
          console.error('CSRF token no encontrado');
          return; // Evita enviar la solicitud si el token no está presente
        }

        const response = await axios.put(apiUrl, filteredData, {
          headers: {
            'X-CSRF-TOKEN': this.$csrfToken
          }
        });

        console.log('Respuesta de la API:', response);

        // Mostrar SweetAlert de éxito
        Swal.fire({
          icon: 'success',
          title: 'Actualización exitosa',
          text: 'La información médica se ha actualizado correctamente.',
          confirmButtonText: 'Aceptar'
        }).then(() => {
          this.$emit('updateEmpleado', filteredData);
          this.handleCloseModal(); // Cerrar modal
        });
      } catch (error) {
        let errorMessage = 'Ocurrió un error al actualizar la información médica.';

        if (error.response) {
          errorMessage = error.response.data.error || 'Error desconocido';
          console.error('Error al actualizar la información médica:', error.response.data);
        } else if (error.request) {
          console.error('No se recibió respuesta del servidor:', error.request);
          errorMessage = 'No se recibió respuesta del servidor.';
        } else {
          console.error('Error en la configuración de la solicitud:', error.message);
          errorMessage = error.message;
        }

        // Mostrar SweetAlert de error
        Swal.fire({
          icon: 'error',
          title: 'Error',
          text: errorMessage,
          confirmButtonText: 'Aceptar'
        });
      }
    }

  },
  watch: {
    empleadoData: {
      handler(newValue) {
        this.localEmpleadoData = { ...newValue }; // Actualiza la copia local si cambia la prop
      },
      deep: true
    }
  }
};
</script>

