<template>
  <div class="modal-overlay" v-if="isVisible">
    <div class="modal-content">
      <h3>Gestión de Opciones para {{ nombreTabla(tabla) }}</h3>

      <div class="form-group">
        <input v-model="nuevaOpcion" placeholder="Agregar nuevas opciones (separadas por coma)" />
        <button @click="agregarOpciones">Agregar</button>
      </div>

      <ul class="opciones-lista">
        <li v-for="(opcion, index) in opciones" :key="opcion.id || index" class="opcion-item">
          <div v-if="!opcion.editando" class="opcion-contenido">
            <span class="nombre-opcion">{{ opcion.name }}</span>
            <div class="acciones">
              <button @click="editar(index)">Editar</button>
              <button @click="eliminar(index)">Eliminar</button>
            </div>
          </div>
          <div v-else class="opcion-contenido">
            <input v-model="opcion.name" />
            <div class="acciones">
              <button @click="guardarEdicion(index)">Guardar</button>
              <button @click="cancelarEdicion(index)">Cancelar</button>
            </div>
          </div>
        </li>
      </ul>


      <div style="margin-top: 1rem;">
        <button class="cerrar-btn" @click="$emit('close')">Cerrar</button>
        <button class="guardar-btn" @click="guardarTodo">Guardar Todos</button>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios';
import Swal from 'sweetalert2';

export default {
  name: "ModalOpciones",
  props: {
    idPortal: {
      type: [String, Number],
      required: true,
    },
    idCliente: {
      type: [String, Number],
      required: true,
    },
    tabla: {
      type: String,
      required: true,
    },
    isVisible: {
      type: Boolean,
      required: true,
    }
  },
  data() {
    return {
      opciones: [],
      nuevaOpcion: "",
    };
  },
  mounted() {
    this.cargarOpciones();
  },
  computed: {
  
  },
  methods: {
    nombreTabla(valor) {
      switch (valor) {
        case '_documentEmpleado':
          return 'Documentos de Empleado';
        case '_cursos':
          return 'Cursos';
        case '_examEmpleado':
          return 'Exámenes de Empleado';
        default:
          return valor;
      }
    },
    async cargarOpciones() {
              console.log("🚀 ~ cargarOpciones ~ id_portal:"+this.tabla)

      try {
        const params = this.idPortal ? { id_portal: this.idPortal, tabla: this.tabla  } : {};
        const response = await axios.get(`${this.$apiUrl}/document-options`, { params });
        // Filtrar opciones con id_portal no null
        this.opciones = response.data
          .filter(opt => opt.id_portal !== null)
          .map(opt => ({ ...opt, editando: false }));
      } catch (error) {
        console.error('Error fetching document options:', error);
      }
    },

    agregarOpciones() {
      const nuevas = this.nuevaOpcion
        .split(',')
        .map(opt => opt.trim())
        .filter(opt => opt);

      if (!nuevas.length) {
        alert("Debe escribir al menos una opción válida.");
        return;
      }

      nuevas.forEach(name => {
        this.opciones.push({ id: null, name, editando: false });
      });

      this.nuevaOpcion = ""; // limpiar input
    },

    editar(index) {
      this.opciones[index].editando = true;
    },

    guardarEdicion(index) {
      if (!this.opciones[index].name.trim()) {
        alert("El nombre no puede estar vacío.");
        return;
      }
      this.opciones[index].editando = false;
    },

    cancelarEdicion(index) {
      this.opciones[index].editando = false;
    },

    async eliminar(index) {
      const opcion = this.opciones[index];

      const result = await Swal.fire({
        title: '¿Estás seguro?',
        text: 'Esta opción será eliminada permanentemente.',
        icon: 'warning',
        showCancelButton: true,
        confirmButtonColor: '#d33',
        cancelButtonColor: '#3085d6',
        confirmButtonText: 'Sí, eliminar',
        cancelButtonText: 'Cancelar',
      });

      if (result.isConfirmed) {
        try {
          if (opcion.id) {
            // Si tiene ID, eliminar en backend
            await axios.delete(`${this.$apiUrl}/document-options/delete`, {
              data: {
                id: opcion.id,
                tabla: this.tabla,
              },
            });
          }

          // Eliminar del array local
          this.opciones.splice(index, 1);

          Swal.fire({
            title: '¡Eliminado!',
            text: 'La opción fue eliminada correctamente.',
            icon: 'success',
            timer: 2000,
            showConfirmButton: false,
          });
        } catch (error) {
          console.error("Error al eliminar:", error);
          Swal.fire({
            title: 'Error',
            text: 'No se pudo eliminar la opción.',
            icon: 'error',
          });
        }
      }
    },


    async guardarTodo() {
      try {
        const payload = this.opciones.map(({ id, name }) => ({ id, name }));

        await axios.post(`${this.$apiUrl}/document-options/save`, {
          id_portal: this.idPortal,
          tabla: this.tabla,
          opciones: payload,
        });

        await Swal.fire({
          icon: 'success',
          title: 'Opciones guardadas',
          text: 'Las opciones se han guardado correctamente.',
          confirmButtonText: 'Aceptar',
        });

        this.$emit('close'); // o 'closeOption' si ese es el correcto
      } catch (error) {
        console.error("Error guardando opciones:", error);

        await Swal.fire({
          icon: 'error',
          title: 'Error',
          text: 'No se pudieron guardar las opciones. Intenta nuevamente.',
          confirmButtonText: 'Aceptar',
        });
      }
    }
  },
};
</script>



